import React, { Component } from "react"
import { Checkbox, Button, Spin, Icon, Tooltip } from "antd"
import {actions, connect} from 'mirrorx'
const CheckboxGroup = Checkbox.Group
import "./index.less"

import { services } from 'config/services'

class LeapComponents extends Component {
  constructor(props) {
    super(props)
    this.state = {}
    this.errMsgId = {}
  }
  componentWillMount = () => {
    let selectComponent = this.props.value
    let serviceName = this.props.serviceName

    this.setState({
      btnLoading: true,
      selectComponent
    })
    actions.create.getService().then(res => {
      let {serviceList} = this.props
      selectComponent = selectComponent || {}
      serviceList.map((rec, index) => {
        const {installedServices} = this.props

        if(installedServices){
          if(installedServices.includes(rec.name) || serviceName == rec.name){
            selectComponent[rec.name] = +rec.id
          }
        }else{
          if(rec.necessary){
            selectComponent[rec.name] = +rec.id
          }
        }
      })
      serviceList = this.hasRequired(selectComponent, serviceList)
      this.setState({selectComponent, serviceList, btnLoading: false})
    }).catch(err => {
      this.setState({
        btnLoading: false
      })
    })
  }
  // 选择服务 from: 判定是直接点击checkbox还是点击其他元素
  checkboxHandle = (reocrd, from, e) => {
    if(e.target.className == 'errorDom') return
    const selectComponent = _.cloneDeep(this.state.selectComponent) || {}
    const serviceList = _.cloneDeep(this.state.serviceList)
    const checked = from ? !selectComponent[reocrd.name] : e.target.checked
    
    checked
      ? (selectComponent[reocrd.name] = +reocrd.id)
      : delete selectComponent[reocrd.name]

    let service = this.hasRequired(selectComponent, serviceList)
    this.setState({ selectComponent, serviceList: service})
  }
  // 判断是否有依赖项
  hasRequired = (selectComponent, serviceList) => {
    if(!serviceList) return
    const chooseValus = Object.values(selectComponent)  // 已选中项的ID集合
    const newServiceList = {}   // 保存新组装的数据
    this.errMsgId = {}  // 清空存储错误信息的对象
    
    // 重新组装数据 {id: record}, 并清空依赖信息 以待后用
    serviceList.map(rec => {
      rec.requiredServiceName = null
      newServiceList[rec.id] = rec
    })
    // 根据已选择数据 重新设置依赖数据
    chooseValus && chooseValus.map(chooseId => {
      let requiredComponents = {}      // 当前的依赖项对象 {id: name}
      const item = newServiceList[chooseId]
      if(item.requiredServices){
        let curRequired = item.requiredServices.split(',')
        curRequired.map(req => {
          if(!chooseValus.includes(+req)){
            requiredComponents[req] = newServiceList[req].displayName
            this.errMsgId[req] = newServiceList[req].displayName
          }
        })
        newServiceList[chooseId].requiredServiceName = requiredComponents
      }
    })
    return Object.values(newServiceList)
  }
  nextStepHandle = () => {
    // actions.create.g

    if(Object.keys(this.errMsgId).length > 0){
      return
    }

    const {components, installedServices} = this.props

    const {selectComponent} = this.state
    if(installedServices){
      let {devTools, installedServices} = this.props
      let slave_selectComponent = {}
      devTools.map((rec, index) => {
        if(installedServices){
          if(installedServices.includes(rec.name)){
            slave_selectComponent[rec.name] = +rec.id
          }
        }
      })
      const allService = {...selectComponent, ...slave_selectComponent}
      actions.create.getComponents(allService)
      this.props.onsubmit(slave_selectComponent, 1, "installComponent")
      return 
    }
    
    this.props.onsubmit(this.state.selectComponent, 1, "installComponent")
  }
  render() {
    const selectComponent = this.state.selectComponent || {}
    let {serviceList, installedServices} = this.props
    const errorMsg = Object.values(this.errMsgId)
    serviceList = this.state.serviceList || serviceList
    return (
      <div className="installComponent stepContent">
      <Spin spinning={!serviceList}>
        <h2>请选择要安装的LEAP组件</h2>
        <div className="componentBox">
          <div className="selectGroup">
            {!serviceList && <p>正在获取数据</p>}
            <div>
              {serviceList && serviceList.map(item => {
                const errvalues = item.requiredServiceName && Object.values(item.requiredServiceName) || ''
                return (
                  <div
                    onClick={item.necessary ? () => {} : this.checkboxHandle.bind(this, item, "div")}
                    className="selectItem"
                    key={item.id}
                  >
                    <Checkbox
                      disabled={installedServices ? installedServices.includes(item.name) : item.necessary}
                      onChange={this.checkboxHandle.bind(this, item, "")}
                      checked={selectComponent[item.name] || false}
                    />
                    <span className="selectItemName">
                      {item.displayName}
                      <Tooltip title={services[item.name].comment}>
                        <Icon type="question-circle-o" className="serviceComment"/>
                      </Tooltip>
                    </span>
                    <span>版本号: {services[item.name].versions}</span>
                    {/* <span className="comment">{services[item.name].comment}</span> */}
                    <span className="errorDom" style={{color: 'red'}}>{errvalues && errvalues.length > 0 && `此组件必须依赖于 ${errvalues.join('、')}`}</span>
                  </div>
                )
              })}
            </div>
            {serviceList && serviceList.length == 0 && <p>暂无数据</p>}
          </div>
        </div>
        {errorMsg && errorMsg.length > 0 && <p style={{color: 'red', margin: 0, marginTop: 10}}>当前配置中, {errorMsg.join('、')} 等依赖项尚未勾选.</p>}
        <div className="btnGroup">
          {
            !this.props.installedServices &&
            <Button onClick={() => this.props.onsubmit(null, -1, "installComponent")}>
              上一步
            </Button>
          }
          <Button loading={this.state.btnLoading} className="stepNext" onClick={this.nextStepHandle} type="primary">
            下一步
          </Button>
        </div>
      </Spin>
      </div>
    )
  }
}
const ConnLeapComponents = connect(state=>{
  return{
    serviceList: state.create.serviceList,
    devTools: state.create.devTools
  }
})(LeapComponents)
export default ConnLeapComponents