import React from 'react'
import { Route, Switch, Redirect } from 'mirrorx'
import Dashboard from 'pages/monitor/dashboard'
import ServiceInfo from 'components/serviceInfo'


const MonitorRoute = () => (
    <Switch>
        <Route path="/monitor/dashboard" component={Dashboard}/>
        <Route path="/monitor/services/:servicesName" component={ServiceInfo}/>
        <Redirect to="/monitor/dashboard" />
    </Switch>
)

export default MonitorRoute