import React, {Component} from 'react'
import {Tabs, Select, Button, Popover, Icon, Modal, Menu} from 'antd'

import ComponentList from './toolsList'
import ComponentVersion from './toolsVersion'

import {FormattedMessage} from 'react-intl'

import './index.less'

import {startAllService} from 'api/components'

import { connect, actions } from 'mirrorx'
import NullCluster from 'pages/nullCluster'

import ConsoleHeader from 'components/consoleHeader'

const TabPane = Tabs.TabPane
const Option = Select.Option


class DevTools extends Component {
    constructor(props) {
        super(props)
        this.state = {
            keyword: '',
            searchInputWidth: 0
        }
        this.en2cn = {start: '启动', 'stop': '停止', restart: '重启'}
    }

    componentWillMount(){
      //actions.layout.changeMenuSelected('hosts')

      if(this.props.currentCluster){
        actions.components.getComponentsList('tools')
      }else{
        actions.app.getClusterList().then(rec => {
          if(rec && rec.length > 0){
            actions.components.getComponentsList('tools')
          }
        })
      }
    }
    

    toggleSearchInput = e => {
      const width = this.state.searchInputWidth || 0;
      this.setState({ searchInputWidth: 200 - width });
    }

    tabChange(val) {
    }

    clusterChange = (val) => {
      actions.components.getComponentsList('tools')
    }

    operateMenu = () => {
      return <Popover placement="bottomRight" overlayClassName="operateService" content={this.menuContent()} trigger="hover">
        <Button>操作</Button>
      </Popover>
    }

    menuContent = () => {
      const {servicesList} = this.props
      const whoCanUse = servicesList && servicesList.map(rec => {
        return rec.serviceStatus
      })
      return <ul onClick={this.operate} className="devToolsOperatMenu">
        {/* {whoCanUse.includes('INSTALLED') && <li data-key="start"><Icon type="caret-right" />启动所有</li>}
        {whoCanUse.includes('STARTED') && <li data-key="stop"><Icon type="caret-right" />停止所有</li>} */}
        <li data-key="start"><Icon type="caret-right" />启动所有</li>
        <li data-key="stop"><Icon type="caret-right stopIcon" />停止所有</li>
        {/* <li data-key="restart"><Icon type="retweet" />重启所有所需服务</li> */}
      </ul>
    }

    operate = e => {
      let type = e.target.dataset.key || e.target.getAttribute('data-key')
      if(e.target.nodeName !== 'LI'){
        type = e.target.parentNode.dataset.key || e.target.parentNode.getAttribute('data-key')
      }
      this.setState({
        type
      })
    }

    closeOperateModal = () => {
      this.setState({type: null})
    }

    modalSureHandle = () => {
      const {devTools, currentCluster} = this.props
      const {type} = this.state
      this.setState({loading: true})
      startAllService({
        cluster_name: currentCluster.clusterName,
        type
      })
      .then(res => {
        if(res.code == 1){
          actions.host.updateState({taskModalVisible: true}) 
          this.setState({
            type: null,
            loading: false
          })
        }
      })
    }

    render() {
      const {keyword, clusterList, devTools} = this.props
      const {type, loading} = this.state
      if(!clusterList || clusterList.length == 0){
          return <NullCluster />
      }
      return <div className="componentsWrap">
        <ConsoleHeader selectChange={this.clusterChange}/>
        <Tabs className="tabContent" onChange={this.tabChange.bind(this)} type="card" tabBarExtraContent={this.operateMenu()}>
          <TabPane tab="工具" key="1">
            <ComponentList />
          </TabPane>
          <TabPane tab="版本" key="2">
            <ComponentVersion />
          </TabPane><Modal
          title="确认"
          className="hostOperateModal"
          visible={!!type}
          onCancel={this.closeOperateModal}
          footer={
            <Button loading={loading} type="primary" onClick={this.modalSureHandle}>确定</Button>
          }
        >
          确认 {this.en2cn[type]} 所有服务?
        </Modal>
        </Tabs>
        
      </div>;
    }
}

const ConnDevTools = connect(state => {
    return {
        clusterList: state.app.clusterList,
        currentCluster: state.app.currentCluster,
        servicesList: state.components.servicesList
    }
})(DevTools)

export default ConnDevTools 