import request from 'utils/asyncService'

// 获取服务类型
const getClusterTypes = (data) => {
  return request({
      url:'/platform/getServerType',
      data,
  })
}
// 创建集群
const createCluster = (data) => {
  return request({
    url: '/platform/createCluster',
    data,
    method: 'post'
  })
}
// 获取服务列表
const getService = (data) => {
  return request({
      url:'/platform/getServiceAndComponent',
      data
  })
}
// 添加服务
const postServiceList = (data) => {
  return request({
    url: '/platform/addServiceToCluster',
    data,
    method: 'post',
    // isDataString: true
    // headers: {
    //   dataType: 1
    // }
  })
}

// 内部添加服务
const postReServiceList = (data) => {
  return request({
    url: '/platform/addServiceToExistCluster',
    data,
    method: 'post',
    // isDataString: true
    // headers: {
    //   dataType: 1
    // }
  })
}

const checkCreateStatus = (data) => {
  return request({
    url: '/platform/checkCreatingStatus',
    data,
  })
}
const checkAddingStatus = (data) => {
  return request({
    url: '/platform/checkAddingStatus',
    data,
  })
}
export {getClusterTypes, createCluster, getService, postServiceList, checkCreateStatus, postReServiceList, checkAddingStatus}