import React, { Component } from 'react'
import {Icon, Progress} from 'antd'
import {connect, } from 'mirrorx'
import {history} from 'utils/index'
import './index.less'

class CreateSuccess extends Component {
  constructor(props){
    super(props)
    this.state = {
      times: 5,
      isJump: false
    }
    this.percent = null
  }
  componentWillReceiveProps(np){
    if(np.createStatus && (np.createStatus == 'SUCCESS' || np.createStatus == 'START FAILED')){
      this.setState({
        percent: 100
      })
      setTimeout(() => {
        this.setState({
          isJump: true,
        })
        sessionStorage.removeItem('currentCluster')
        sessionStorage.removeItem('clusterList')
        this.times()
      }, 1000)
    }
  }
  times = () => {
    if(timer) return
    let timer = setInterval(() => {
      const times = this.state.times - 1
      if(times === 0){
        clearInterval(timer)
        timer = null
        this.goback()
        return
      }
      this.setState({
        times
      })
    }, 1000)
  }
  goback = () => {
    const {lastRouter} = this.props
    // if(lastRouter == '/noCluster'){
      history.push('/')
    // }else{
    //   history.goBack()
    // }
  }
  render() {
    let {createStatus} = this.props
    createStatus = createStatus || ''
    if(createStatus && createStatus.indexOf('step') >= 0){
      const curStep = createStatus && createStatus.split('step')[1].split(' ')[0]
        this.percent = curStep > 0 ? (curStep * (100 / 14)).toFixed(2) : 2
    }

    if(createStatus && (createStatus == 'SUCCESS' || createStatus == 'START FAILED')){
      this.percent = 100
    }
    return (
      <div className="stepSuccess">
      {
        !this.state.isJump && createStatus ? 
        <div className="statusProgress">
          { 
            createStatus != 'FAILED' && createStatus.indexOf('SUCCESS') < 0 &&
            <h2>服务正在安装中<span className="dotdotdot"></span><p className="smalltips fr">预计需要时间5-15分钟</p></h2>
          }
          {
            createStatus == 'FAILED' &&
            <h2 style={{color: '#f5222d'}}>安装失败</h2>
          }

          <Progress percent={+this.percent} status={createStatus && createStatus == 'FAILED' ? 'exception' : "active"} />
          <p style={{margint: '15px', textAlign: 'left'}}>{createStatus}</p>
        </div>
        :
        <div className="jump">
          <Icon type="check-circle" />
          <h1>{this.props.title}</h1>
          {this.state.times}秒后跳转, 若没有跳转, 请点击<a onClick={this.goback}>跳转</a>
        </div>
      }
      </div>
    )
  }
}

const ConnCreateSuccess = connect(state=>{
  return{
    createStatus: state.create.createStatus,
    lastRouter: state.app.lastRouter
  }
})(CreateSuccess)
export default ConnCreateSuccess