import React, { Component } from "react";
import { connect, actions, Link } from "mirrorx";
import { Table, Icon, Button, Tag, Card } from "antd";

const columns = [
  {
    title: "组件",
    dataIndex: "display_name",
    key: "display_name",
    render: (value, row, index) => (
      <Link to={`/componentInfo/${value}`}>
        {value}
      </Link>
    )
  },
  {
    title: "版本",
    dataIndex: "versions",
    key: "versions"
  },
  {
    title: "rpm版本",
    dataIndex: "releaseVersion",
    key: "releaseVersion"
  },
  {
    title: "状态",
    dataIndex: "status",
    key: "status",
    render: (value, row, index) => (
      <Tag color={value ? "#1ACB40" : "#FF5858"}>
        {value ? "已安装" : "未安装"}
      </Tag>
    )
  },

  {
    title: "描述",
    dataIndex: "comment",
    key: "comment",
    width: 350
  },
  {
    title: "操作",
    key: "action",
    width: 80,
    render: (text, row) => {
      if (row.status) {
        return (
          <a href="javascript:;">
            <Icon type="delete" /> 卸载
          </a>
        )
      } else {
        return (
          <a href="javascript:;" style={{ color: "#1ACB40" }}>
            <Icon type="plus-circle" /> 安装
          </a>
        )
      }
    }
  }
];

class License extends Component {
  constructor(props) {
    super(props);
    this.state = {
      bbb: 123
    }
    this.currentClusterName = null
  }

  componentWillReceiveProps(props){
    if(props.currentCluster && props.currentCluster.clusterName != this.currentClusterName){
      this.currentClusterName = props.currentCluster.clusterName
      this.componentWillMount()
    }
  }

  componentWillMount() {
    actions.security.getLicenseData()
  }

  render() {
    const { status, licenseData } = this.props

    return (
      <div>
        <div className="topTitle">许可证</div>
        <Card
          loading={status}
          type="inner"
          title="概览"
        >
          <ul className="licenseDetail">
            <li>
              <label>授权给:</label>{licenseData.company_name}
            </li>
            <li>
              <label>Server密钥:</label>{licenseData.server_key}
            </li>
            <li>
              <label>允许安装主机的最大数量:</label>{licenseData.max_host_count}
            </li>
            <li>
              <label>已使用主机的数量:</label>{licenseData.working_hostcount}
            </li>
            <li>
              <label>允许安装CPU的最大数量:</label>{licenseData.max_cpu_count}
            </li>
            <li>
              <label>已使用CPU的数量:</label>{licenseData.work_cpucount}
            </li>
            <li>
              <label>截止日期:</label>{licenseData.deadline}
            </li>
          </ul>
        </Card>
        
      </div>
    );
  }
}

const ConnLicense = connect(state => {
  return {
    status: state.security.status,
    licenseData: state.security.licenseData
  }
})(License)

export default ConnLicense;
