import React, { Component } from 'react'
import {Icon, Button} from 'antd'
import './index.less'
import CreatePic from 'assets/images/common/create_pic.png'
import {history} from 'utils/index'

function CreateCluster(){
  return (
    <div className="jumpToCreate">
      <img src={CreatePic} style={{height: '238px'}} className="switcher" alt="创建集群"/>
      <h1>您还没有创建集群</h1>
      <Button onClick={() => history.push('/createCluster')} type="primary"><Icon type="plus"/>创建集群</Button>
    </div>
  )
}

export default CreateCluster