import React from 'react'
import ReactDOM from 'react-dom'
import {
    Input,
    Button,
    Steps,
    Radio, 
    Select,
    DatePicker,
    Cascader,
    Modal
} from 'antd'

const Step = Steps.Step
const RadioGroup = Radio.Group
const Option = Select.Option
const RadioButton = Radio.Button

import InstallLeapComponent from 'pages/create/installLeapComponents'
import InstallDevToos from 'pages/create/installDevTools'
import ChooseHost from 'pages/create/chooseHost'
import ChooseSlave from 'pages/create/chooseSlave'
import CreateSuccess from 'pages/create/success'

import './index.less'
import 'pages/create/index.less'

import {connect, actions} from 'mirrorx'



export default class InstallModal extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      currentStep: 0,
      hostList: [],
      installedMaster: {},

      installedSlave: {}
    }
  }

  componentDidMount() {
    
  }


  stepHandle = (value, derection, keys, type) => {
    let {currentStep} = this.state
    currentStep = currentStep + derection
    document.querySelector('.createstepWrapper').scrollTop = 0
    value = value || this.state[keys]
    if(keys === 'slave'){
      this.handleCancel()
      actions.host.updateState({taskModalVisible: true})
      return 
    }
    if(type === 0){
      this.setState({ [keys]: value })
    }else{
      this.setState({currentStep, [keys]: value })
    }
  }

  // handleOk = () => {
  //   this.setState({ loading: true });
  //   setTimeout(() => {
  //     this.setState({ loading: false, visible: false });
  //   }, 3000);
  // }


  handleCancel = () => {
    this.setState({
      installComponent: null, 
      installDevTools: null,
      slave: null, 
      master: null,
      currentStep: 0
    })
    this.props.callbackParent()
  }

  render() {
    const {currentStep, config, installComponent, installDevTools, master, slave} = this.state


    let { modalVisible, installType, serviceName, installedServices, hostList, installedMaster, installedSlave } = this.props
    const steps = [
      installType == 'component' ? '安装组件' : '安装开发工具',
      '指派Master',
      '指派slave和client',
    ]
    return (
    <div>
        {modalVisible &&
          <Modal
            width={1000}
            visible={modalVisible}
            title="添加服务向导"
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            footer={false}
          >

            <div className="createLeapPlatForm installModal">
              <div className="createClusterInner">
                <div className="createstepWrapper">
                  <StepsComponent className="createSteps" currentStep={currentStep || 0} steps={steps}>
                    {(step, index) => <Step key={index} title={step} icon={<i>{index + 1}</i>} />}
                  </StepsComponent>

                  {
                    currentStep == 0 ? installType == 'component' ?
                      <InstallLeapComponent serviceName={serviceName} installedServices={installedServices} value={installComponent} onsubmit={this.stepHandle} />
                      :
                      <InstallDevToos value={installDevTools} installedServices={installedServices} serviceName={serviceName} components={installComponent} onsubmit={this.stepHandle} />
                      : ''
                  }

                  {currentStep == 1 && <ChooseHost installedMaster={installedMaster} hostListInstalled={hostList} value={master} onsubmit={this.stepHandle} />}
                  {currentStep == 2 && <ChooseSlave value={slave} master={master} installedServices={installedServices} installedSlave={installedSlave} hostListInstalled={hostList} clustername={config && config.clustername} onsubmit={this.stepHandle} />}
                </div>
              </div>
            </div>


          </Modal>
        } 
    </div>
      
    )
  }
}

function StepsComponent(props){
  return (<Steps current={props.currentStep} className={props.className}>
    {props.steps.map((rec, index) => props.children(rec, index))}
  </Steps >)
}

InstallModal.defaultProps = {
  modalVisible: false,
  installType: 'component',
  installedServices: [],
  installedSlave: [],
  installedSlave: [],
  serviceName: '',
}
