import React, { Component } from 'react'
import { connect, actions, Link } from "mirrorx"

import { Radio, message, DatePicker, Upload, Modal, Form, Input, Tooltip, Icon, Cascader, Select, Row, Col, Checkbox, Button, AutoComplete } from 'antd'
const RadioGroup = Radio.Group

const FormItem = Form.Item
const Option = Select.Option;
const AutoCompleteOption = AutoComplete.Option;

import Header from 'components/loginHeader'
import axios from 'axios'
import './index.less'
import Cookies from 'js-cookie'


import Editor from 'react-umeditor'


const residences = [{
  value: 'zhejiang',
  label: 'Zhejiang',
  children: [{
    value: 'hangzhou',
    label: 'Hangzhou',
    children: [{
      value: 'xihu',
      label: 'West Lake',
    }],
  }],
}, {
  value: 'jiangsu',
  label: 'Jiangsu',
  children: [{
    value: 'nanjing',
    label: 'Nanjing',
    children: [{
      value: 'zhonghuamen',
      label: 'Zhong Hua Men',
    }],
  }],
}];

class ProductCreate extends Component {
  constructor(props){
    super(props)
    this.state = {
      loading: true,
      userKey: Cookies.get('TRIPNOTE-KEY'),
      autoCompleteResult: [],
      previewVisible: false,
      previewImage: '',

      priceType: 1,
      limitTime: '',
      rawrawPrice: '',
      discountPrice: '',
      rawrawPrice: '',

      peopleNum1: '',
      collagePrice1: '',
      collageLimitTime1: '',
      peopleNum2: '',
      collagePrice2: '',
      collageLimitTime2: '',
      peopleNum3: '',
      collagePrice3: '',
      collageLimitTime3: '',

      introduction: ' ',
      costDescription: ' ',
      reservationNote: ' ',
      guide: ' ',

      fileList: [],
    }
  }

  componentWillMount(){
    // this.getProductList()
  }

  handleChangeintroduction(content){
    this.setState({
      introduction: content
    })
  }
  handleChangecostDescription(content){
    this.setState({
      costDescription: content
    })
  }
  handleChangereservationNote(content){
    this.setState({
      reservationNote: content
    })
  }
  handleChangeguide(content){
    this.setState({
      guide: content
    })
  }



  getIcons(){
    var icons = [
        "source | undo redo | bold italic underline strikethrough fontborder emphasis | ",
        "paragraph fontfamily fontsize | superscript subscript | ",
        "forecolor backcolor | removeformat | insertorderedlist insertunorderedlist | selectall | ",
        "cleardoc  | indent outdent | justifyleft justifycenter justifyright | touppercase tolowercase | ",
        "horizontal date time  | image emotion spechars | inserttable"
    ]
    return icons;
  }
  getImgUploader(){
    return {
      url: '/upload/common/image',
      type: 'local',
      name: "file",
      request: "image_src"
    }
  }

  getProductList = () => {
    let self = this
    console.log(self)
    axios.post(
      '/token/product/doSearch', 
      {isMy: true},
      {
        headers: {
          'authorization': this.state.userKey,
        }
      }
    ).then(res => {
      if (res.data.code == 1) {
        self.setState({
          loading: false,
          productList: res.data.data.data
        })
      }
    })
  }
  formatStatus = (status) => {
    if(status == 0){
      return '未发布'
    }else if(status == 1){
      return '已发布'
    }else{
      return '已结束'
    }
  }
  timestampToTime = (timestamp) => {
    var date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
    var Y = date.getFullYear() + '-';
    var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
    var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
    var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
    var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
    var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
    return Y+M+D+h+m+s;
  }


  setStatusDown = (item) => {
    return () => {
      console.log(item)
    }
  }

  setStatusUp = (item) => {
    return () => {
      console.log(item)
    }
    // let res = await wepy.request({
    //   method: 'post',
    //   url: config.service.host + '/token/product/status/doUpdate',
    //   data: {"statuses": [{id: item.id, status: 1}]},
    //   header: {
    //     'authorization': this.$parent.globalData.userKey,
    //     'content-type': 'application/json'
    //   }
    // })
    // this.getProductList()
  }

  handlePreview = (file) => {
    console.log(file)
    this.setState({
      previewImage: file.url || file.thumbUrl,
      previewVisible: true,
    });
  }

  handleChange = (info) => {
    //把fileList拿出来
    let {fileList} = info;

    const status = info.file.status;
    if (status !== 'uploading') {
        console.log(info.file, info.fileList);
    }
    if (status === 'done') {
        //message.success(`${info.file.name} file uploaded successfully.`);
    } else if (status === 'error') {
        message.error(`${info.file.name} file upload failed.`);
    }

    //重新设置state
    this.setState( {fileList});
  }

  handleChangeInput = (e) => {
    console.log(e)
  }

  changePriceType = (e) => {
    console.log('radio checked', e.target.value);
    this.setState({
      priceType: e.target.value,
    })
  }




  // 创建
  handleSubmit = (e) => {
    let self = this
    e.preventDefault()

    console.log(this.state.guide)


    this.props.form.validateFields((err, values) => {
      if (!err) {
        const {productName, userDate, productCount, rawPrice, discountPrice, limitTime,
          peopleNum1,
          collagePrice1,
          collageLimitTime1,
          peopleNum2,
          collagePrice2,
          collageLimitTime2,
          peopleNum3,
          collagePrice3,
          collageLimitTime3
        } = values
        this.setState({
          submitLoading: true
        })

        let imgs = []

        this.state.fileList.forEach((item) => {
          imgs.push(item.response.data.image_src)
        })


        let data = {
          "name": productName,
          "userDate": userDate,
          "priceType": this.state.priceType,
          "productCount": productCount,
          "rawPrice": rawPrice,
          "discountPrice": discountPrice,
          "status": 0,
          "limitTime": limitTime,
          "commission": discountPrice*0.1,
          "introduction": this.state.introduction,
          "costDescription": this.state.costDescription,
          "reservationNote": this.state.reservationNote,
          "guide": this.state.guide,
          "imageUrls": imgs,
          "imageUrl": imgs[0]
        }

        console.log(this.state.guide)

        // return;
  
        if(this.state.priceType == 2){
          let productCollages = []
          if(peopleNum1 != '' && collagePrice1 != '' && collageLimitTime1 != ''){
            productCollages.push({
              peopleNum1: peopleNum1,
              collagePrice1: collagePrice1,
              collageLimitTime1: collageLimitTime1
            })
          }
          if(peopleNum2 != '' && collagePrice2 != '' && collageLimitTime2 != ''){
            productCollages.push({
              peopleNum1: peopleNum2,
              collagePrice1: collagePrice2,
              collageLimitTime1: collageLimitTime2
            })
          }
          if(peopleNum3 != '' && collagePrice3 != '' && collageLimitTime3 != ''){
            productCollages.push({
              peopleNum1: peopleNum3,
              collagePrice1: collagePrice3,
              collageLimitTime1: collageLimitTime3
            })
          }
          data['productCollages'] = productCollages
        }

        axios.post('/token/product/doAdd', 
          data,
          {
						headers: {
              'Content-Type': 'application/json',
              'authorization': self.state.userKey
						}
					}
      
        )
        .then(res => {
          this.setState({
            submitLoading: false
          })
          console.log(res)
          const { history } = this.props
          message.success('添加成功！');
          history.push('/')
        })
        .catch(err => {
          this.setState({
            submitLoading: false
          })
        })
      }
    })
  }


  render() {
    const { getFieldDecorator } = this.props.form
    const {priceType, loading, productList, submitLoading, 
      autoCompleteResult, previewVisible, previewImage, fileList
    } = this.state


    const uploadButton = (
      <div>
        <Icon type="plus" />
        <div className="ant-upload-text">Upload</div>
      </div>
    )

    const formItemLayout = {
      labelCol: {
        xs: { span: 28 },
        sm: { span: 4 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 28,
          offset: 4,
        },
        sm: {
          span: 20,
          offset: 4,
        },
      },
    };
    const prefixSelector = getFieldDecorator('prefix', {
      initialValue: '86',
    })(
      <Select style={{ width: 70 }}>
        <Option value="86">+86</Option>
        <Option value="87">+87</Option>
      </Select>
    );

    const websiteOptions = autoCompleteResult.map(website => (
      <AutoCompleteOption key={website}>{website}</AutoCompleteOption>
    ));


    var icons = this.getIcons();
    var uploader = this.getImgUploader();
    var plugins = {
        image:{
            uploader:uploader
        }
    }
    var count = 100;
    var editors = []
    for(var i=0;i<count;i++){
      editors.push({
        icons:icons,
        plugins:plugins
      })
    }


    return (
      <div className="productCreateWrap">
        <Header />
        <div className="productCreateForm">
          <Form onSubmit={this.handleSubmit}>
            <FormItem
              {...formItemLayout}
              label="产品名称"
            >
              {getFieldDecorator('productName', {
                rules: [{
                  required: true, message: '请输入产品名称',
                }],
              })(
                <Input />
              )}
            </FormItem>
            <FormItem
              {...formItemLayout}
              label="使用日期"
              className="itemLeavl0"
            >
              {getFieldDecorator('userDate', {
                rules: [{ type: 'object', required: true, message: '请选择日期!' }],
              })(
                <DatePicker className="itemLeavl0"/>
              )}
            </FormItem>
            <FormItem
              {...formItemLayout}
              label="产品图片"
            >
              <Upload
                action="/upload/common/image"
                listType="picture-card"
                fileList={fileList}
                onPreview={this.handlePreview}
                onChange={this.handleChange}
              >
                {fileList.length >= 10 ? null : uploadButton}
              </Upload>
              <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                <img alt="example" style={{ width: '100%' }} src={previewImage} />
              </Modal>
            </FormItem>

            <FormItem
              {...formItemLayout}
              label="产品介绍"
              className="itemLeavl1"
            >
              <Editor 
                icons={icons}
                value={this.state.introduction}
                defaultValue=''
                onChange={this.handleChangeintroduction.bind(this)}
                plugins={plugins} />
            </FormItem>

            <FormItem
              {...formItemLayout}
              label="费用说明"
              className="itemLeavl2"
            >
              <Editor 
                icons={icons}
                value={this.state.costDescription}
                defaultValue=''
                onChange={this.handleChangecostDescription.bind(this)}
                plugins={plugins} />
            </FormItem>

            <FormItem
              {...formItemLayout}
              label="预定须知"
              className="itemLeavl3"
            >
              <Editor 
                icons={icons}
                value={this.state.reservationNote}
                defaultValue=''
                onChange={this.handleChangereservationNote.bind(this)}
                plugins={plugins} />
            </FormItem>

            <FormItem
              {...formItemLayout}
              label="实用指南"
            >
              <Editor 
                icons={icons}
                value={this.state.guide}
                defaultValue=''
                onChange={this.handleChangeguide.bind(this)}
                plugins={plugins} />
            </FormItem>

            <FormItem
              {...formItemLayout}
              label="价格类型"
            >
              <RadioGroup name="radiogroup" defaultValue={priceType} onChange={this.changePriceType}>
                <Radio value={1}>限时特价</Radio>
                <Radio value={2}>拼团</Radio>
              </RadioGroup>
            </FormItem>
            
            {priceType == 1 ? <div><FormItem
                {...formItemLayout}
                label="时间限制"
              >
                {getFieldDecorator('productLimitTime', {
                  rules: [],
                })(
                  <Input className="inputS"/>
                )}
              </FormItem>
              <FormItem
                {...formItemLayout}
                label="商品份数"
              >
                {getFieldDecorator('productCount', {
                  rules: [],
                })(
                  <Input className="inputS"/>
                )}
              </FormItem>
              <FormItem
                {...formItemLayout}
                label="原价"
              >
                {getFieldDecorator('rawPrice', {
                  rules: [],
                })(
                  <Input className="inputS"/>
                )}
              </FormItem>
              <FormItem
                {...formItemLayout}
                label="折扣价"
              >
                {getFieldDecorator('discountPrice', {
                  rules: [],
                })(
                  <Input className="inputS"/>
                )}
              </FormItem></div> : ''}

              {priceType == 2 ? <div><FormItem
                {...formItemLayout}
                label="商品份数"
              >
                {getFieldDecorator('productCount', {
                  rules: [],
                })(
                  <Input className="inputS"/>
                )}
              </FormItem>
              <FormItem
                {...formItemLayout}
                label="原价"
              >
                {getFieldDecorator('rawPrice', {
                  rules: [],
                })(
                  <Input className="inputS"/>
                )}
              </FormItem>
              <FormItem
                {...formItemLayout}
                label="拼团价格"
              >

                <div className="groupList">
                  <div className="cell">
                    <label>人数：</label>
                    {getFieldDecorator('peopleNum1', {
                        rules: [],
                    })(
                        <Input />
                    )}
                  </div>
                  <div className="cell">
                    <label>价格：</label>
                    {getFieldDecorator('collagePrice1', {
                        rules: [],
                    })(
                        <Input />
                    )}
                  </div>
                  <div className="cell">
                    <label>时限：</label>
                    {getFieldDecorator('collageLimitTime1', {
                        rules: [],
                    })(
                        <Input />
                    )}
                  </div>
                </div>

                <div className="groupList">
                  <div className="cell">
                    <label>人数：</label>
                    {getFieldDecorator('peopleNum2', {
                        rules: [],
                    })(
                        <Input />
                    )}
                  </div>
                  <div className="cell">
                    <label>价格：</label>
                    {getFieldDecorator('collagePrice2', {
                        rules: [],
                    })(
                        <Input />
                    )}
                  </div>
                  <div className="cell">
                    <label>时限：</label>
                    {getFieldDecorator('collageLimitTime2', {
                        rules: [],
                    })(
                        <Input />
                    )}
                  </div>
                </div>

                <div className="groupList">
                  <div className="cell">
                    <label>人数：</label>
                    {getFieldDecorator('peopleNum3', {
                        rules: [],
                    })(
                        <Input />
                    )}
                  </div>
                  <div className="cell">
                    <label>价格：</label>
                    {getFieldDecorator('collagePrice3', {
                        rules: [],
                    })(
                        <Input />
                    )}
                  </div>
                  <div className="cell">
                    <label>时限：</label>
                    {getFieldDecorator('collageLimitTime3', {
                        rules: [],
                    })(
                        <Input />
                    )}
                  </div>
                </div>
              </FormItem></div> : ''}

            <FormItem {...tailFormItemLayout}>
              <Button type="primary" htmlType="submit">提交</Button>
            </FormItem>
          </Form>
        </div>
      </div>
    )
  }
}
const ProCreate = Form.create()(ProductCreate)
export default ProCreate