import mirror, { actions } from 'mirrorx'
import { history } from 'utils/index'
import './user'
import './monitor'
import './services/heatMap'
import './services/summary'
import './createService'
import './components'
import './host'
import './security'
import './alerts'

import * as API from 'api/index'
import * as cluster from 'api/host'
import * as createServiceAPI from 'api/createCluster'


const _currentCluster = sessionStorage.getItem('currentCluster')
const _clusterList = sessionStorage.getItem('clusterList')
mirror.model({
  name: 'app',
  initialState: {
    clusterList: _clusterList ? JSON.parse(_clusterList) : [],
    currentCluster: _currentCluster ? JSON.parse(_currentCluster) : null,
    loadService: false,
    serviceList: [],
    getClusterLoading: false,
    hostList: [],
    hostComponentsList: []
  },
  reducers: {
    updateState(state, data) {
      const allState = { ...state, ...data }
      // if (allState.currentCluster) {
      //   sessionStorage.setItem('currentCluster', JSON.stringify(allState.currentCluster))
      // }
      // if (allState.clusterList) {
      //   sessionStorage.setItem('clusterList', JSON.stringify(allState.clusterList))
      // }
      return allState
    }
  },
  effects: {
    routeChange(d, getState) {
      // { pathname, search }
      const { updateState } = actions.app
      const {currentrouter} = getState().app
      updateState({
        currentrouter: d.pathname,
        lastRouter: currentrouter
      })
    },
    async getAllService(data, getState) {
      const { updateState } = actions.app;
      const { currentCluster } = getState().app
      let clusterName = currentCluster.clusterName

      let statckRes = await API.getAllStackService({ currentCluster: clusterName });
      let serviceRes = await API.getAllService({ currentCluster: clusterName });
      let serviceList = [];
      let obj = {};

      statckRes = statckRes.data
      serviceRes = serviceRes.data

      let hasHeatmapSection = ['HDFS', 'YARN', 'HBASE']

      if (statckRes['items']) {
        for (let i = 0; i < statckRes.items.length; i++) {
          let statckItem = statckRes.items[i]
          for (let j = 0; j < serviceRes.items.length; j++) {
            let serviceItem = serviceRes.items[j];
            if (statckItem.StackServices.service_name === serviceItem.ServiceInfo.service_name) {
              let serviceName = statckItem.StackServices.service_name;
              if (!obj[serviceName]) {
                obj[serviceName] = true;
                serviceList.push({
                  service_name: serviceName,
                  display_name: statckItem.StackServices.display_name,
                  ServiceInfo: serviceItem.ServiceInfo,
                  StackServices: statckItem.StackServices,
                  artifacts: statckItem.artifacts,
                  hasHeatMap: hasHeatmapSection.indexOf(serviceName) > -1,
                  components: statckItem.components
                })
              }
            }
          }
        }
      }
      updateState({
        loadService: true,
        serviceList
      })

    },
    // 获取集群列表
    async getClusterList(data, getstate) {
      const { updateState } = actions.app;
      let { currentCluster } = getstate().app;
      updateState({ getClusterLoading: true });
      const res = await cluster.getClusterList();
      if (res && res.code === 1) {
        if (data && data.host_name) {
          currentCluster = res && res.data.filter(item => {
            return data.host_name.toLowerCase().indexOf(item.clusterName.toLowerCase()) >= 0
          })[0]
        }
        if (res.data && res.data.length == 0) {
          sessionStorage.removeItem('currentCluster')
          sessionStorage.removeItem('clusterList')
          updateState({
            getClusterLoading: false
          })
          return res
        }
        updateState({
          clusterList: res.data,
          currentCluster: currentCluster || res.data[0],
          getClusterLoading: false
        });
        return res.data;
      } else {
        updateState({
          getClusterLoading: false
        });
      }
    },
    async changeCluster(data, getstate) {
      const { updateState } = actions.app;
      const { clusterList, currentrouter } = getstate().app;
      updateState({
        getClusterLoading: true
      })

      if (data) {
        updateState({
          currentCluster: data,
          getClusterLoading: false
        })
      } else {
        updateState({
          currentCluster: clusterList[0] || {},
          getClusterLoading: false
        })
      }

      return true

      // let pathname = history.location.pathname

      // if (pathname == '/hosts') {
      //   await actions.host.getHostList({ clusterName: data.clusterName });
      // } else if (pathname == '/alerts') {
      //   actions.alerts.getAlertsList()
      // } else if (pathname == '/componentManage') {
      //   actions.components.getComponentsList('components')
      // } else if (pathname == '/devTools') {
      //   actions.components.getComponentsList('tools')
      // } else if (pathname == '/security') {
      //   actions.security.getServiceAccount()
      // } else if (pathname == '/monitor/dashboard') {
      //   await actions.app.getAllService();
      //   await actions.monitor.getClusterInfoComponent();
      //   await actions.monitor.getServiceMetric();
      //   await actions.monitor.getClusterInfo();
      // }
    },

    async getHostList(data, getstate) {
      const { updateState } = actions.app;
      const { currentCluster } = getstate().app
      const res = await API.getHostList({ clusterID: currentCluster.clusterId })
      updateState({
        hostList: res.data
      })
    },

    // 获取主机上安装的组件
    async getHostComponents(data1, getstate) {
      const { updateState } = actions.app;
      const { currentCluster } = getstate().app
      let data = await API.getHostComponents({ clusterID: currentCluster.clusterId })
      data = data.data

      let _data = { master: [], slave: [] }
      for (let key in data) {
        for (let index = 0; index < data[key].length; index++) {
          if (data[key][index].category == 'SLAVE' || data[key][index].category == 'CLIENT') {
            if (_data['slave'][data[key][index].name]) {
              _data['slave'][data[key][index].name] = _data['slave'][data[key][index].name].concat(key)
            } else {
              _data['slave'][data[key][index].name] = [key]
            }
          } else if (data[key][index].category == 'MASTER') {
            if (_data['master'][data[key][index].name]) {
              _data['master'][data[key][index].name] = _data['master'][data[key][index].name].concat(key)
            } else {
              _data['master'][data[key][index].name] = [key]
            }
          }
        }
      }

      updateState({
        hostComponentsList: _data
      })
    },


  }
})

mirror.model({
  name: 'layout',
  initialState: {
    width: 200,
    xWidth: 80,
    collapsed: false,
    selected: ['monitor'],
    openKey: ['manage'],
    selectedKeys: ['monitor'],
    menuItems: [
      {
        key: 'manage',
        name: '集群管理',
        icon: 'clusterManage',
        children: [{
          key: "monitor", name: "状态&监控", icon: 'file-text'
        },
        {
          key: "hosts", name: "主机管理", icon: 'file-text'
        },
        {
          key: "componentManage", name: "组件管理", icon: 'file-text'
        },
        {
          key: "devTools", name: "开发工具", icon: 'file-text'
        },
        {
          key: "security", name: "安全管理", icon: 'file-text'
        },
        {
          key: "alerts", name: "告警管理", icon: 'file-text'
        },
        ]
      },
      {
        key: 'tools',
        name: '开发工具',
        icon: 'tools'
      },
      {
        key: 'user',
        name: '用户管理',
        icon: 'userManage',
        children: []
      }, {
        key: 'db',
        name: '数据库管理',
        icon: 'dbManage',
        children: []
      },
    ]
  },

  reducers: {
    toggle(state) {
      return {
        ...state,
        collapsed: !state.collapsed
      }
    },
    updateMenuSelectedKey(state, data) {
      return {
        ...state,
        ...data
      }
    },
  },
  effects: {
    changeMenuSelectedKey(data, getstate) {
      const { updateMenuSelectedKey } = actions.layout
      updateMenuSelectedKey({
        selectedKeys: data
      })
    }
  }
})
