import React, { Component } from 'react'
import { actions, Route, Link, Redirect, connect } from 'mirrorx'
import {Breadcrumb, Tabs, Badge, Button, Input, Spin, Icon, Popover, Modal} from 'antd'
import { history } from 'utils'
const TabPane = Tabs.TabPane
import './index.less'
import Summary from '../summary'
import Configs from '../configs'
import Alerts from '../alerts'
import Versions from '../versions'
import Logs from '../logs'
import NullCluster from 'pages/nullCluster'
import formatPostData from '../formatdata'
import ConsoleHeader from 'components/consoleHeader'

class Host extends Component {
  constructor(props){
    super(props)
    this.state = {
      loading:false
    }
  }
  componentWillMount = async() => {
    const { hostLists } = this.props
    const Host = this.props.match.params.host = this.props.match.params.host
    // if(currentCluster){
    //   !hostLists && await actions.host.getHostList({clusterName: currentCluster.clusterName, host_name: Host})
    // }else{
    await actions.app.getClusterList({host_name: Host}).then(rec => {
      if(rec.length == 0){
        history.push('/noCluster')
      }
      if(rec && rec.length > 0){
        // const current = rec.filter(item => {
        //   return Host.toLowerCase().indexOf(item.clusterName.toLowerCase()) >= 0
        // })
        !hostLists && actions.host.getHostList({clusterName: rec[0].clusterName, host_name: Host})
      }
    })
    // }
    const pathname = this.props.location.pathname
    if(pathname.indexOf('/hosts/') >= 0){
      let activeKey = pathname.split('/hosts/')[1]
      if(activeKey.indexOf('/')){
        activeKey = activeKey.split('/')[1]
      }
      this.setState({
        activeKey
      })
    }

  }

  componentWillReceiveProps(np){
    if(np && np.currentCluster){
      if(this.old && this.old.clusterName !== np.currentCluster.clusterName){
        this.props.history.push('/hosts')
      }
      this.old = np.currentCluster
    }
  }

  clusterChange = (val) => {
    this.props.history.push('/hosts')
    // this.setState({
    //   loading: true

    // })
    // actions.host.getHostList({clusterName: val.clusterName})
    // .then(rec => {
    //   this.setState({
    //     loading: false
    //   })
    // })
  }

  popover = () => {
    return <Popover placement="bottomRight" overlayClassName="hostTabOperate summaryOperate" content={this.hostComponentsOperate()} trigger="click">
      告警
    </Popover>
  }

  tabChange = val => {
    this.setState({
      activeKey: val
    })
  }

  tabBarExtraContent = () => {
    return <Popover placement="bottomRight" overlayClassName="hostTabOperate" content={this.tabBarOperate()} trigger="click">
    <Button>操作</Button>
  </Popover>
  }
  
  tabBarOperate = () => {
    const {hostinfo} = this.props
    return <ul onClick={this.operateHost}>
      <li data-type="startAll" data-text="启动全部主机组件">
        <Icon type="caret-right" /> 启动所有组件
      </li>
      <li data-type="stopAll" data-text="停止全部主机组件" className="stopAllComponent">
        <Icon type="caret-right" /> 停止所有组件
      </li>
      <li data-type="restartAll">
        <Icon type="retweet" /> 重启所有组件
      </li>
      <li data-type="setRack" data-text="设置Rack">
        <Icon type="setting" /> 设置Rack
      </li>
      {
        hostinfo && hostinfo.Hosts && hostinfo.Hosts.maintenance_state == 'OFF' ?
        <li data-type="open" data-text="打开维护模式为主机">
          <Icon type="camera" /> 打开维护模式
        </li> :
        <li data-type="close" data-text="关闭维护模式为主机">
          <Icon type="camera" /> 关闭维护模式
        </li>
      }
      

      {/* <li>
        <Icon type="close" /> 删除主机
      </li> */}
      {/* <li>
        <Icon type="download" /> 下载客户端配置
      </li> */}
    </ul>
  }

  operateHost = e => {
    if(e.target.nodeName == 'LI' || e.target.nodeName == 'I'){
      // e.target
      const {hostinfo} = this.props
      const type = e.target.dataset.type || e.target.getAttribute('data-type')
      let text = e.target.dataset.text || e.target.getAttribute('data-text')
      if(type == 'restartAll'){
        text = `重启${hostinfo.Hosts.host_name}上的全部组件`
      }
      this.setState({operateType: type, operateText: text})
      // console.log(type, text)
    }
  }
  modalSureHandle = () => {
    const {operateType, operateText, newRack} = this.state
    const {hostinfo} = this.props
    // return
    if(operateType == 'startAll' || operateType == 'stopAll'){
      const servicesArr = hostinfo && hostinfo.host_components.map(rec => {
        return rec.HostRoles.component_name
      })
      actions.host.postOperate({
        state: operateType.indexOf('start') >= 0 ? 'STARTED' : 'INSTALLED',
        clusterName: hostinfo.Hosts.cluster_name,
        host_name: hostinfo.Hosts.host_name,
        context: operateText,
        level: 'HOST',
        query: `HostRoles/component_name.in(${servicesArr.join(',')})`,
        fromHost: true
      }).then(rec => {
        if(rec.code == 1){
          this.setState({
            operateText: null
          })
        }
      })
    }else if(operateType == 'restartAll'){
      const formatdata = formatPostData([hostinfo], 1)
      actions.host.restartAllComponents({
        clusterName: hostinfo.Hosts.cluster_name,
        filters: formatdata.data,
        context: operateText
      }).then(rec => {
        if(rec.code == 1){
          this.setState({
            operateText: null
          })
        }
      })
    }else if(operateType == 'close' || operateType == 'open'){
      const formatdata = formatPostData([hostinfo], 0)
      actions.host.openOrCloseMaintenance({
        clusterName: hostinfo.Hosts.cluster_name,
        context: operateText,
        hostsStr: formatdata.data,
        isOpen: operateType == 'open' ? 'ON' : 'OFF'
      }).then(rec => {
        if(rec.code == 1){
          this.setState({
            operateText: null
          })
        }
      })
    }else if(operateType == 'setRack'){
      if(!newRack){
        return
      }
      const formatdata = formatPostData([hostinfo], 0)
      actions.host.setHostRack({
        cluster_name: hostinfo.Hosts.cluster_name,
        context: operateText,
        hosts: formatdata.data,
        rack: newRack
      }).then(rec => {
        if(rec.code == 1){
          this.setState({
            operateText: null,
            modalHasErr: false
          })
        }
      })
      .catch(err => {
        this.setState({modalLoading: false, modalHasErr: true})
      })
    }
  }
  closeOperateModal = () => {
    this.setState({operateText: null})
  }
  rackChange = (e) => {
    let newRack = e.target.value
    newRack = newRack.indexOf('/') == 0 ? newRack : '/' + newRack
    this.setState({ newRack })
  }

  render() {
    const HostUrl = this.props.match.url
    const currUrl = location.pathname
    const Host = this.props.match.params.host
    const {activeKey, operateText, newRack, modalHasErr} = this.state
    const { currentCluster} = this.props
    const num = this.props.warningNumber && this.props.warningNumber.CRITICAL || 0
    // if(!currentCluster || currentCluster.length == 0){
    //   return <NullCluster />//<Spin spinning={true} style={{width: '100%', height: '100%'}}/>
    // }
    if(currentCluster && currentCluster.length == 0){
      return <NullCluster />
    }
    if(!currentCluster){
      return <Spin spinning={true} style={{width: '100%', height: '100%'}}/>
    }
 
    return (
      <div className="hostPage">
          <ConsoleHeader selectChange={this.clusterChange}/>
          <div className="hostPageContent">
            <Breadcrumb separator=" > " className="hostPageBreadcrumb">
              <Breadcrumb.Item><Link to="/hosts">主机</Link></Breadcrumb.Item>
              <Breadcrumb.Item>{Host}</Breadcrumb.Item>
            </Breadcrumb>
            {
              HostUrl === currUrl &&
              <Redirect exact from={`/hosts/${Host}`} to={`/hosts/${Host}/summary`}/>
            }
            <Tabs activeKey={activeKey || 'summary'} className="leapHostsTabs" onChange={this.tabChange} type="card" tabBarExtraContent={this.tabBarExtraContent()}>
              <TabPane tab={<Link to={`/hosts/${Host}/summary`}>概览</Link> } key="summary">
                <Route path={`/hosts/${Host}/summary`} component={Summary}/>
              </TabPane>
              {/* <TabPane tab={<Link to={`/hosts/${Host}/configs`}>配置</Link> } key="configs">
                <Route path={`/hosts/${Host}/configs`} component={Configs}/>
              </TabPane> */}
              <TabPane tab={<Link to={`/hosts/${Host}/alerts`}><Badge className="tabWarnBadge" count={num}>告警</Badge></Link> } key="alerts">
                <Route path={`/hosts/${Host}/alerts`} component={Alerts}/>
              </TabPane>
              <TabPane tab={<Link to={`/hosts/${Host}/versions`}>版本</Link> } key="versions">
                <Route path={`/hosts/${Host}/versions`} component={Versions}/>
              </TabPane>
              {/* <TabPane tab={<Link to={`/hosts/${Host}/logs`}>日志</Link> } key="logs">
                <Route path={`/hosts/${Host}/logs`} component={Logs}/>
              </TabPane> */}
            </Tabs>
          </div>
          <Modal
            title={operateText == '设置Rack' ? operateText : '确认批量操作'}
            className={operateText == "设置Rack" ? "setRackModal" : "hostOperateModal"}
            visible={!!operateText}
            onCancel={this.closeOperateModal}
            maskClosable={false}
            footer={
              <Button type="primary" onClick={this.modalSureHandle}>确定</Button>
            }
          >
          {
            operateText && operateText != '设置Rack' ?
            <div>
              确认?
              {/* 要在下面{theOperateHost && theOperateHost.length}个主机上<b>{operateText}</b>吗?
              <ul>
                {
                  theOperateHost && theOperateHost.map(rec => {
                    return <li key={rec.href}>{rec.Hosts.host_name}</li>
                  })
                }
              </ul> */}
            </div> :
            <div>
              <label>Rack:</label> 
              <Input ref={(el) => this.rackIpt = el} value={newRack} onChange={this.rackChange}/>
              <p style={{color: modalHasErr ? 'red' : '#999'}}>{modalHasErr ? 'Rack更改失败' : '应以斜杠开始，可以包含字母、数字、点、连字符和斜杠'}</p>
            </div>
          }
          </Modal>
      </div>
    )
  }
}

// 
const HomeConn = connect(state=>{
  return{
    warningNumber: state.host.summaryInfo && state.host.summaryInfo.alerts_summary,
    hostinfo: state.host.hostinfo,
    clusterList: state.app.clusterList,
    currentCluster: state.app.currentCluster
  }
})(Host)

export default HomeConn
