import React, {Component} from 'react'
import { connect, actions, Link } from "mirrorx";

import {Tabs, Select, Button, Input, Icon, DatePicker, Table, Tag} from 'antd'

import NullCluster from 'pages/nullCluster'

import ConsoleHeader from 'components/consoleHeader'

import './index.less'

const TabPane = Tabs.TabPane
const Option = Select.Option

class Tools extends Component {
    constructor(props) {
        super(props)
        this.state = {
            keyword: '',
            filterValues: {},
            headerFilterKeywords: ''
        }
    }

    componentWillMount(){
      if(this.props.currentCluster){
        actions.summary.getServicesStatusInfo()
      }else{
        actions.app.getClusterList().then(rec => {
          if(rec && rec.length > 0){
            actions.summary.getServicesStatusInfo()
          }
        })
      }
    }

    clusterChange(){
      console.log(123)
    }

    render() {
        const { status, clusterList, servicesList, servicesStatusLoading, servicesStatusInfo } = this.props

        if(!clusterList || clusterList.length == 0){
          return <NullCluster />
        }

        return <div>
            <ConsoleHeader selectChange={this.clusterChange}/>
            <div className="toolsWrap">
              <div className="title">开发工具</div>
              <div className="content">
                {
                  servicesStatusInfo.LEAPSQL && servicesStatusInfo.LEAPSQL.status && 
                  <a className="item" href={`http://sql.${servicesStatusInfo.LEAPSQL.host}:8080`} target="_blank">
                    <div className="con">
                      <i className="icon iconSql"></i>
                      <div className="info">
                        <div className="tit">LEAP SQL</div>
                        <div className="des">LEAP SQL是一个数据查询工具</div>
                      </div>
                    </div>
                  </a>
                }
                {
                  servicesStatusInfo.LEAPSQL && !servicesStatusInfo.LEAPSQL.status && 
                  <div className="item disable">
                    <div className="con">
                      <i className="icon iconSql"></i>
                      <div className="info">
                        <div className="tit">LEAP SQL</div>
                        <div className="des">LEAP SQL是一个数据查询工具</div>
                      </div>
                    </div>
                  </div>
                }


                {
                  servicesStatusInfo.LEAPDATAHUB && servicesStatusInfo.LEAPDATAHUB.status && 
                  <a className="item" href={`http://datahub.${servicesStatusInfo.LEAPDATAHUB.host}:8080`} target="_blank">
                    <div className="con">
                      <i className="icon iconDatahub"></i>
                      <div className="info">
                        <div className="tit">LEAP Datahub</div>
                        <div className="des">Load data from RS DB to hadoop cluster</div>
                      </div>
                    </div>
                  </a>
                }
                {
                  servicesStatusInfo.LEAPDATAHUB && !servicesStatusInfo.LEAPDATAHUB.status && 
                  <div className="item disable">
                    <div className="con">
                      <i className="icon iconDatahub"></i>
                      <div className="info">
                        <div className="tit">LEAP Datahub</div>
                        <div className="des">Load data from RS DB to hadoop cluster</div>
                      </div>
                    </div>
                  </div>
                }


                {
                  servicesStatusInfo.LEAPID && servicesStatusInfo.LEAPID.status && 
                  <a className="item" href={`http://id.${servicesStatusInfo.LEAPID.host}:8080/leapid-admin/view/login.html`} target="_blank">
                    <div className="con">
                      <i className="icon iconId"></i>
                      <div className="info">
                        <div className="tit">LEAP ID</div>
                        <div className="des">Leap系统的帐号中心，管理leap生态的帐号信息以及统一的登录入口</div>
                      </div>
                    </div>
                  </a>
                }
                {
                  servicesStatusInfo.LEAPID && !servicesStatusInfo.LEAPID.status && 
                  <div className="item disable">
                    <div className="con">
                      <i className="icon iconId"></i>
                      <div className="info">
                        <div className="tit">LEAP ID</div>
                        <div className="des">Leap系统的帐号中心，管理leap生态的帐号信息以及统一的登录入口</div>
                      </div>
                    </div>
                  </div>
                }



                {
                  servicesStatusInfo.LEAPPROCESS && servicesStatusInfo.LEAPPROCESS.status && 
                  <a className="item" href={`http://ts.${servicesStatusInfo.LEAPPROCESS.host}:8080/TaskSchedule`} target="_blank">
                    <div className="con">
                      <i className="icon iconTask"></i>
                      <div className="info">
                        <div className="tit">Task Scheduler</div>
                        <div className="des">Task Scheduler是一个大数据处理流程建立的工具</div>
                      </div>
                    </div>
                  </a>
                }
                {
                  servicesStatusInfo.LEAPPROCESS && !servicesStatusInfo.LEAPPROCESS.status && 
                  <div className="item disable">
                    <div className="con">
                      <i className="icon iconTask"></i>
                      <div className="info">
                        <div className="tit">Task Scheduler</div>
                        <div className="des">Task Scheduler是一个大数据处理流程建立的工具</div>
                      </div>
                    </div>
                  </div>
                }

                
              </div>
            </div>
          </div>
    }
}
const ConnTools = connect(state => {
  return {
    status: state.components.status,
    componentsListData: state.components.componentsListData,
    servicesList: state.components.servicesList,
    currentCluster: state.app.currentCluster,
    hostList: state.app.hostList,
    hostComponentsList: state.app.hostComponentsList,
    clusterList: state.app.clusterList,

    servicesStatusLoading: state.summary.servicesStatusLoading,
    servicesStatusInfo: state.summary.servicesStatusInfo,
  }
})(Tools)

export default ConnTools