import mirror, {actions} from "mirrorx"
import * as API from 'api/host'

const initialState = {
  hostLists: null,        // 记录 host    的数据列表
  hostStatus: null,       // 记录 host    的统计数据
  summaryInfo: null,      // 记录 host    的常规数据
  alertFilter: null,      // 记录 alerts  的操作条件  
  alertsList: null,       // 记录 alerts  的展示数据
  alert_backupData: null, // 记录 alerts  的原始数据
  versions: null,         // 记录 version 的展示数据
  vs_backupData: null,    // 记录 version 的原始数据
  clusterList: null,      // 记录 cluster 的数据
  taskModalVisible: false,
}
const hostState = sessionStorage.getItem('host')
mirror.model({
  name: 'host',
  initialState: initialState, //hostState && JSON.parse(hostState) || 
  reducers: {
    updateState(state, data) {
      const allState = {...state, ...data}
      return allState
    }
  },
  effects: {

    // 获取主机列表
    async getHostList(data, getState){
      const {updateState, getHostList} = actions.host
      let {hostinfo} = getState().host
      const host_name = data.host_name
      const oldData = {...data}
      delete data.host_name
      const res = await API.getHostLists(data)
      if(res.code === 1){
        const hostLists = res.data.items
        const newHostinfo = hostLists.filter(rec => {
          return rec.Hosts.host_name == host_name
        })
        updateState({
          hostLists,
          clusterName: data.clusterName,
          hostinfo: hostinfo || newHostinfo[0]
        })
        return res.data
      }
      return res
    },
    // 获取主机组件以及主机信息
    async getHostSummary(data,getstate){
      const {updateState, getHostSummary} = actions.host
      const {currentCluster} = getstate().app
      const postData = {
        cluster_name: currentCluster.clusterName, 
        host_name: data.host_name
      }
      const res = await API.getSummaryInfo(postData)
      if(res.code == 1){
        const summaryInfo = res.data.items
        let summaryData
        summaryInfo && summaryInfo.map(rec => {
          if(rec.Hosts.host_name == data.host_name){
            summaryData = rec
          }
        })
        updateState({
          summaryInfo: summaryData
        })
        // setTimeout(() => {
        //   getHostSummary(data, getstate)
        // }, 3000)
        return summaryData
      }else{
        return false
      }
      return res
    },
    // 获取主机指标
    async getHostStatus(data, getstate){
      const {updateState, getHostSummary} = actions.host
      const {currentCluster} = getstate().app
      const postData = {
        cluster_name: currentCluster.clusterName, 
        host_name: data.host_name,
        start: data.start || undefined,
        end: data.end || undefined
      }
      const res = await API.getHostStatus(postData)
      // .then(res => {
        if(res.code == 1){
          updateState({
            hostStatus: res.data.metrics
          })
          return res.data.metrics
        }
        return null
      // })
      // getHostStatus
    },
    // 获取版本列表
    getVersions (versions, getstate){
      const {updateState} = actions.host
      let versionFilter = {server:[], display_name: [], state: []}
      versions && versions.map(rec => {
        if(!versionFilter.server.includes(rec.stack_name)){
          versionFilter.server.push(rec.stack_name)
        }
        if(!versionFilter.display_name.includes(rec.display_name)){
          versionFilter.display_name.push(rec.display_name)
        }
        if(!versionFilter.state.includes(rec.state)){
          versionFilter.state.push(rec.state)
        }
      })
      updateState({
        versions,
        versionFilter,
        version_backupData: versions
      })
    },
    // 获取告警列表
    async getAlertsLists(data, getstate){
      const {updateState} = actions.host
      const {currentCluster} = getstate().app
      await API.getAlertsLists({cluster_name: currentCluster.clusterName, hosts: data.hostName})
      .then(res => {
        if(res.success){
          const lists = res.data.items;
          let filter = {server:[]}
          const alerts = lists && lists.map(rec => {
            if(!filter.server.includes(rec.Alert.service_name)){
              filter.server.push(rec.Alert.service_name)
            }
            return rec.Alert
          })
          updateState({
            alertsList: alerts,
            alertFilter: filter,
            alerts_backupData: alerts
          })
        }
      })
    },
    // 获取操作任务列表
    async getOperateProgress(data, getstate){
      const {updateState} = actions.host
      return await API.getTaskProgress(data)
      .then(res => {
        if(res.code == 1){
          const responseData = res.data.items
          updateState({
            taskLists: responseData.reverse()
          })
        }
        return res
      })
    },
    // 筛选告警数据
    filterAlertsList(data, getstate){
      const type = data.type || 'alerts'
      delete data.type
      const backupData = getstate().host[type + '_backupData']
      const {updateState, filterData} = actions.host
      const filterDatas = filterData({allData: backupData, filter: data.filterValues ? data.filterValues : data})
      updateState({
        [type + 'List']: filterDatas
      })
    },

    // 设置当前选中的主机信息
    setCurrentHost(hostinfo) {
      const {updateState} = actions.host
      updateState({hostinfo})
      return Promise.resolve(hostinfo)
    },
    // 启动,停止(所有)服务
    postOperate(data, getstate){
      const {updateState} = actions.host
      return API.startOrStopComponents(data)
      .then(res => {
        if(res.code == 1){
          updateState({
            taskModalVisible: true
          })
        }
        return res
      })
      .catch(err => {
        return err
      })
    },
    // // 重新安装失败组件
    // installFailedComponents(data, getstate){
    //   console.log(data)
    //   return API.installFailedComponents(data)
    //   .then(res => {
    //     return res
    //   })
    // },
    // 重启全部组件
    restartAllComponents(data, getstate){
      const {updateState} = actions.host
      return API.restartAllComponents(data)
      .then(res => {
        if(res.code == 1){
          updateState({
            taskModalVisible: true
          })
        }
        return res
      })
      .catch(err => {
        return err
      })
    },
    // 维护模式开关
    openOrCloseMaintenance(data, getstate){
      return API.openOrCloseMaintenance(data)
      .then(res => {
        return res
      })
      .catch(err => {
        return err
      })
    },
    // 设置Rack
    setHostRack(data, getstate){
      return API.setHostRack(data)
      .then(res => {
        return res
      })
      .catch(err => {
        return err
      })
    },
    // 关闭/启动服务
    async startComponents(data, getstate){
      const {updateState} = actions.host
      const res = await API.startComponents(data).then(req => {
        if(req.code == 1){
          updateState({
            taskModalVisible: true
          })
        }
        return req
      })
      // if(res.code === 1){
      //   const d = res.data.items
      //   let summaryData
      //   d && d.map(rec => {
      //     if(rec.Hosts.host_name == data.host_name){
      //       summaryData = rec
      //     }
      //   })
      //   const {updateState} = actions.host
      //   updateState({
      //     summaryInfo: summaryData,
      //   })
      // }
      return res
      
    },
    // 重启服务
    async restartComponents(data, getstate){
      const res = await API.restartComponents(data)
      if(res.code === 1){
        const d = res.data.items
        let summaryData
        d && d.map(rec => {
          if(rec.Hosts.host_name == data.host_name){
            summaryData = rec
          }
        })
        const {updateState} = actions.host
        updateState({
          summaryInfo: summaryData,
          taskModalVisible: true
        })
      }
      return res
    },
    // 打开/关闭服务的维护模式
    async openOrCloseMaintenanceState(data, getstate){
      const res = await API.openOrCloseMaintenanceState(data)
      if(res.code === 1){
        const d = res.data.items
        let summaryData
        d && d.map(rec => {
          if(rec.Hosts.host_name == data.host_name){
            summaryData = rec
          }
        })
        const {updateState} = actions.host
        updateState({
          summaryInfo: summaryData,
        })
      }
      return res
    },

    // opt {allData: Array, filter: Object}
    // allData 需要筛选的原始数据
    // filter  筛选条件 key: 筛选字段 value: 筛选值 模糊筛选可以在字段前加 fuzzy_ 标记
    filterData(opt){
      const {allData, filter} = opt
      let filterData = allData
      const keys = Object.keys(filter)
      if(keys.length !== 0){
        keys.map(key => {
          const val = filter[key]
          if(!val) {
            return
          }
          filterData = filterData.filter(rec => {
            if(key.indexOf('fuzzy_') != -1){
              const fuzzy_key = key.split('fuzzy_')[1]
              return rec[fuzzy_key].toLowerCase().indexOf(val.toLowerCase()) >= 0
            }
            return rec[key].toLowerCase() === val.toLowerCase()
          })
        })
        filterData = filterData.length ? filterData : []
      }
      return filterData
    }
    
  }
})