import React, {Component} from 'react'
import styled from 'styled-components'
import {Icon, Menu, Dropdown, Modal, Button, Alert, Slider, InputNumber, Tooltip} from 'antd'
import {connect, actions} from 'mirrorx'


import './index.less'

class VersionModal extends Component {
    constructor(props) {
        super(props)
    }

    cancelBtn = () => {
        this.props.hideVersionModal()
    }

    render() {
        const {showVersionModal, versionModalInfo} = this.props
        return <Modal
            title="版本的详细信息"
            visible={showVersionModal}
            onCancel={this.cancelBtn}
            width="350px"
            footer= {(
                <div>
                  <Button onClick={this.cancelBtn}>关闭</Button>
                </div>
              )}
        >
            <div className="versionName">222</div>
            <div className="hostInfo">
                <p>host</p>
                <ul>
                    <li>
                        <Tooltip placement="top" title={`没有主机列表`}>
                            0
                        </Tooltip>
                        <br/>没有安装
                    </li>
                    <li><Tooltip placement="top" title={`没有主机列表`}>
                            0
                        </Tooltip><br/>已安装</li>
                    <li><Tooltip placement="top" title="12">
                            0
                        </Tooltip><br/>当前</li>
                </ul>
            </div>
        </Modal>
    }
}


export default VersionModal