import React, { Component } from 'react'
import {Progress, Table, Modal, Button, Icon, Tooltip, Spin} from 'antd'
import {actions, connect} from 'mirrorx'
import moment from 'moment'
import * as API from 'api/host'

class TaskProgress extends Component {
  constructor(props){
    super(props)
    this.state = {
      page_size: 10,
      modalVisible: true,
      curStep: 1
    }
    this.currentCluster = {}
  }
  componentDidMount(){
    this.cluster_name = this.props.currentCluster.clusterName
    this.getTaskLists(this.props.currentCluster.clusterName)
  }
  // 获取任务列表
  getTaskLists = async(cluster_name) => {
    await actions.host.getOperateProgress({ cluster_name })
    this.timer = setTimeout(() => {
      this.getTaskLists(cluster_name)
    }, 3000)
  }
  // 获取任务日志
  getTaskLogs = async (data) => {
    this.setState({
      loading: true
    })
    return await API.getTaskLogs(data).then(res => {
      console.log(res)
      if(res.code == 1){
        this.setState({
          curTaskLogs: res.data.Tasks,
          loading: false,
          curStep: 4
        })
      }
    })
  } 
  // 获取任务详情
  getTaskSummary = async(data) => {
    this.setState({
      loading: true
    })
    return await API.getTaskSummary(data).then(res => {
      if(res.code == 1){
        const tasks = res.data.tasks
        let obj = {}
        tasks && tasks.map(rec => {
          let item = rec.Tasks
          if(obj[item.host_name]){
            let components = obj[item.host_name].components
            let status = obj[item.host_name].status
            let count = obj[item.host_name].count
            components.push(item)
            status.push(item.status)
            // FAILED ABORTED  PENDING  QUEUED
            obj[item.host_name] = {
              components,
              status,
              host: item.host_name,
              request_id: item.id,
              create_time: item.create_time,
            }
          }else{
            obj[item.host_name] = {
              components: [item],
              status: [item.status],
              host: item.host_name,
              request_id: item.id,
              create_time: item.create_time,
            }
          }
        })
        this.setState({
          taskInfo: obj,
          curStep: 2,
          loading: false
        })
      }
    })
  }
  tableRowClick = (record, e) => {
    const {curStep} = this.state
    // conso
    if(curStep == 1){
      const request_id = record.Requests.id
      this.request_id = request_id
      this.title = record.Requests.request_context 
      this.getTaskSummary({request_id, cluster_name: this.cluster_name})
    }else if(curStep == 2){
      this.setState({
        curStep: 3,
        currentHost: record.host
      })
    }else if(curStep == 3){
      this.title = record.host_name
      this.modalTitle = record.command_detail
      this.getTaskLogs({request_id: record.request_id, cluster_name: this.cluster_name, task_id: record.id})
    }
  }
  modalStepBack = () => {
    const {curStep} = this.state;
    this.setState({
      curStep: curStep > 1 ? curStep - 1 : curStep
    })
  }
  closeModal = () => {
    clearTimeout(this.timer)
    this.timer = null
    actions.host.updateState({
      taskModalVisible: false
    })
  }
  column1 = () => {
    return [{
      title: '操作',
      dataIndex: 'Requests.request_context',
      render: (text, record, index) => {
        let icon = 'loading'
        let status = 'LOADING'
        let title = 'in_progress'
        if(record.Requests.request_status == 'COMPLETED'){
          icon = 'check'
          status = 'COMPLETED'
          title = 'success'
        }
        if(record.Requests.request_status == 'FAILED'){
          icon = 'close'
          status = 'FAILED'
          title = 'failed'
        }
        if(record.Requests.request_status == 'ABORTED'){
          icon = 'minus'
          status = 'ABORTED'
          title = 'aborted'
        }
        
        if(text.includes('_PARSE_')){
          let pre = text.includes('START') ? '启动 ' : '停止 '
          let service = text.split('.')
          text = pre + (service[2] == 'ALL_SERVICES' ? '全部服务' : service[2])
        }
        return <label className="clearfix">
          <Tooltip placement="top" title={title}>
            <Icon type={icon} className={`statusIcon fl ${status}`} style={{width: 30}}/>
          </Tooltip>
          <span style={{width: 'calc(100% - 30px)', float: 'left'}}>{ text == '_PARSE_.START.ALL_SERVICES' ? '启动全部服务' : text || '请求的名称没有指定' }</span>
        </label>
      }
    }, {
      title: '启动时间',
      dataIndex: 'Requests.start_time',
      width: 130,
      render: record => {
        return <label>{moment(record).format('YYYY/MM/DD hh:mm')}</label>
      }
    }, {
      title: '时长',
      key: 'Requests.id',
      width: 100,
      render: record => {
        let end_time = record.Requests.end_time 
        if(end_time == -1){
          end_time = new Date().getTime()
        }
        let times = ((end_time - record.Requests.start_time) / 1000).toFixed(2)
        return <label>{times}<i className="smallFont">secs</i></label>
      }
    },{
      title: '进度',
      key: 'href',
      width: 180,
      render: record => {
        const request_status = record.Requests.request_status
        let status = 'exception'
        if(request_status != 'FAILED' && request_status != 'ABORTED'){
          status = request_status == 'COMPLETED' ? 'success' : 'active'
        }
        return  <Progress percent={Math.floor(record.Requests.progress_percent)} size="small" status={status}/>
      }
    }]
  }
  column2 = () => {
    return [{
      title: '',
      key: 'request_id',
      width: 30,
      render: (text, record) => {
        const _status = record.status
        let icon = 'loading'
        let title = 'in_progress'
        let status = 'LAODING'
        if(_status.includes('FAILED')){
          icon = 'close'
          title = 'failed'
          status = 'FAILED'
        }else if(_status.includes('ABORTED')){
          icon = 'minus'
          title = 'aborted'
          status = 'ABORTED'
        }else if(_status.join(',').match(/COMPLETED/g) && _status.join(',').match(/COMPLETED/g).length == _status.length){
          icon = 'check'
          title = 'success'
          status = 'COMPLETED'
        }
        return <label>
          <Tooltip placement="top" title={title}>
            <Icon type={icon} className={`statusIcon ${status}`}/>
          </Tooltip>
        </label>
      }
    }, {
      title: '主机',
      dataIndex: 'host'
    }, {
      title: '进度',
      key: 'create_time',
      width: 180,
      render: record => {
        const status = record.status
        let percent = status.join(',').match(/COMPLETED/g) && (status.join(',').match(/COMPLETED/g).length / status.length).toFixed(2)
        let process_status = 'exception'
        if(status.includes('FAILED') || status.includes('ABORTED')){
          process_status = 'exception'
          percent = 100
        }else{
          process_status = status.join(',').match(/COMPLETED/g) && status.join(',').match(/COMPLETED/g).length == status.length ? 'success' : 'active'
          percent = status.join(',').match(/COMPLETED/g) && status.join(',').match(/COMPLETED/g).length == status.length ? 100 : percent
        }
        return  <Progress percent={+percent} size="small" status={process_status}/>
      }
    }]
  }
  column3 = () => {
    return [{
      title: '',
      key: 'status',
      width: 30,
      render: (text, record, index) => {
        const status = record.status
        let icon = 'loading'
        if(status == 'FAILED'){
          icon = 'close'
        }else if(status == 'COMPLETED'){
          icon = 'check'
        }else if(status == 'ABORTED'){
          icon = 'minus'
        }else if(status == 'PENDING' || status == 'QUEUED'){
          icon = 'setting'
        }
        return <label>
          <Tooltip placement="top" title={status.toLowerCase()}>
            <Icon type={icon} className={`statusIcon ${status}`}/>
          </Tooltip>
        </label>
      }
    }, {
      title: '任务',
      dataIndex: 'command_detail'
    }]
  }
  render() {
    const { curStep, taskInfo, currentHost, curTaskLogs, loading } = this.state
    const { taskLists, taskModalVisible } = this.props

    let tableData
    let columns
    let title = this.title
    if(curStep == 1){
      tableData = taskLists
      columns = this.column1()
      title = '操作进度'
    }else if(curStep == 2){
      tableData = Object.values(taskInfo)
      columns = this.column2()
    }else if(curStep == 3){
      tableData = taskInfo && taskInfo[currentHost].components
      columns = this.column3()
      title = taskInfo && taskInfo[currentHost].host
    }
    return (
      <Modal
        title={<div>
          {curStep != 1 && <Button icon="left" onClick={this.modalStepBack}>返回</Button>}
          {title}
        </div>}
        visible={taskModalVisible}
        onOk={this.closeModal}
        onCancel={this.closeModal}
        wrapClassName={`taskProgressModal ${curStep == 4 ? 'paddingTopNone' : ''}`}
        okText="确认"
        cancelText="取消"
        maskClosable={false}
        width={800}
        bodyStyle={{maxHeight: '500px', overflow: 'auto'}}
        footer={
          <Button type="primary" onClick={this.closeModal}>确定</Button>
        }
      >
        { curStep == 4 && <p className="modalHeader">{this.modalTitle}</p> }
        {
          curStep != 4 ?
          <Table
            columns={columns}
            dataSource={tableData}
            pagination={false}
            loading={!tableData || loading}
            rowKey={rec => rec.href || rec.host || rec.id}
            onRow={(record) => {
              return {
                onClick: this.tableRowClick.bind(this, record)
              }
            }}
          /> : 
          <Spin spinning={loading} wrapperClassName="taskProgressModalLogs">
            <section>
              <p><label>stderr: </label>{curTaskLogs && curTaskLogs.error_log}</p>
              <code>{curTaskLogs && curTaskLogs.stderr}</code>
            </section>
            <section>
              <p><label>stdout: </label>{curTaskLogs && curTaskLogs.output_log}</p>
              <code>{curTaskLogs && curTaskLogs.stdout}</code>
            </section>
          </Spin>
        }
      </Modal>
    )
  }
}
const ConnTaskProgress = connect(state => {
  return {
    taskLists: state.host.taskLists,
    currentCluster: state.app.currentCluster,
    taskModalVisible: state.host.taskModalVisible
  }
})(TaskProgress)

export default ConnTaskProgress
