import React, { Component } from 'react'
import {Card, Popover, Button, Icon, Modal, Spin, Tooltip} from 'antd'
import {actions, Link} from 'mirrorx'
import {services} from 'config/services'
import * as API from 'api/host'

export default class Components extends Component {
  constructor(props){
    super(props)
    this.state = {
      componentOperateData: {}
    }
  }
  componentDidMount(){
    this.getComponentData()
    this.timer = setInterval(() => {
      this.getComponentData()
    }, 5000)
    // slave "Metrics Monitor"
    // {"RequestInfo":{"context":"安装 Metrics Monitor","operation_level":{"level":"HOST_COMPONENT","cluster_name":"LPC-4-1531290635","host_name":"lpc-4-1531290635-agent2.cloud.itcoder.club","service_name":"AMBARI_METRICS"}},"Body":{"HostRoles":{"state":"INSTALLED"}}}
    // PUT  /api/v1/clusters/LPC-4-1531290635/hosts/lpc-4-1531290635-agent2.cloud.itcoder.club/host_components/METRICS_MONITOR?HostRoles/state=INIT
  }
  componentWillUnmount(){
    clearInterval(this.timer)
    this.timer = null
  }
  getComponentData = () => {
    const {currentCluster, host_components} = this.props
    const host_name = this.props.match.path.split('/')[2]
    this.host_name = host_name
    const postData = { clusterID: currentCluster.clusterId }
    const p1 = API.getClusterVmnodesWithComponents(postData)
    const p2 = API.getAvailableComponentOnVmnode(postData)
    let p3 = actions.host.getHostSummary({host_name})

    Promise.all([p1, p2, p3]).then(([result1, result2, summary]) => {
      let canAddComponents
      let allComponents
      let currComponents
      let needRestart = []
      if(result1 && result1.code == 1){
        allComponents = result1.data
        currComponents = allComponents[host_name]
        // 获取必须重启的组件
        let components = host_components.length > 0 || !summary ? host_components : summary.host_components
        if(components && components.length > 0){
          needRestart = this.getNeedRestartService(currComponents, components)
        }
      }
      if(result1 && result1.code == 1){
        canAddComponents = result2.data[host_name]
      }
      this.setState({canAddComponents, allComponents, needRestart, currComponents, summary})
    })
  }
  // 主机操作菜单
  hostComponentsOperateMenu = (item, type) => {
    if(type === 'client'){
      return <ul style={{width: '100px'}} onClick={this.componentOperate.bind(this, item)}>
      <li data-type="updateClient">重启所有客户端</li>
      {/* <li data-type="installClient">安装客户端</li> */}
    </ul>
    }
    return <ul style={{width: '100px'}} onClick={this.componentOperate.bind(this, item)}>
      {item.state === 'STARTED' && <li data-type="restart">重启</li>}
      {item.state !== 'STARTED' && <li data-type="start">启动</li>}
      { item.state === 'STARTED' && <li data-type="stop">停止</li> }
      <li data-type="maintenance">{item.maintenance_state === 'ON' ? '关闭' : '打开'}维护模式</li>
    </ul>
  }
  // 主机组件的添加按钮
  hostComponentsAddBtn = () => {
    return <Popover placement="bottomRight" overlayClassName="hostTabOperate summaryOperate" content={this.hostComponentsAdd()} trigger="click">
      <Button>添加</Button>
    </Popover>
  }
  // 添加主机服务
  hostComponentsAdd = () => {
    const {canAddComponents} = this.state
    return <ul>
      {
        canAddComponents && canAddComponents.map(rec => {
          return <li key={rec.id} 
                     onClick={this.addClient.bind(this, rec)}>
                  {rec.displayName}
          </li>
        })
      }
    </ul>
  }
  // 添加主机client,slave
  addClient = record => {
    this.setState({componentOperateData: [], curAddService: record})
  }
  // 操作服务
  componentOperate = (data, e) => {
    const type = e.target.dataset.type || e.target.getAttribute('data-type')
    if(type.indexOf('Client') >= 0){
      data = Object.values(data)
      // data.text = `重启${Object.values(data)[0].host_name}上的所有客户端`
      // data.action = 'restartAllComponents'
    }else if(type == 'start' || type == 'stop'){
      const text = type == 'start' ? `启动 ${data.displayName}` : `停止 ${data.displayName}`
      data.text = text
      data.action = 'startComponents'
    }else if(type == 'restart'){
      const text = `重启${data.displayName}`
      data.text = text
      data.action = 'restartComponents'
    }else if(type == 'maintenance'){
      const text = data.maintenance_state == 'ON' ? `为${data.displayName}关闭维护模式` : `为${data.displayName}打开维护模式`
      data.text = text
      data.action = 'openOrCloseMaintenanceState'
    }else if(type == 'restartNeedComponents'){
      data.text = `重启${data[0].host_name}主机上具有Stale Config属性的组件`
      data.action = 'restartAllComponents'
    }
    this.setState({componentOperateData:　data, operateType: type})
  }
  // 确认执行操作
  operateOk = async(e) => {
    const {componentOperateData, curAddService, operateType} = this.state
    this.setState({
      confirmLoading: true
    })
    let type
    let res
    const {currentCluster} = this.props
    if(Array.isArray(componentOperateData)){
      if(componentOperateData.length == 0){
        await actions.create.postReServiceList({
          services: JSON.stringify({[curAddService.belongServiceName]: [{[curAddService.name]: [this.host_name]}]}),
          clustername: currentCluster.clusterName,
          noCheck: true
        })
        .then(res => {
          setTimeout(() => {
            this.setState({
              confirmLoading: false,
              componentOperateData: null
            }, () => {
              actions.host.updateState({
                taskModalVisible: true
              })
            })
          }, 3000)
        })
        return
      }
      const needData = operateType == 'updateClient' ? Object.values(componentOperateData) : componentOperateData
      const host = needData[0].host_name
      const context = operateType == 'updateClient' ? 
                      `重启${host}上的所有客户端` : 
                      `重启${host}主机上具有Stale Config属性的组件`
      const filters = needData.map(rec => {
        return {
          "service_name": rec.belongServiceName,
          "component_name": rec.name,
          "hosts": rec.host_name,
        }
      })
      res = await actions.host.restartAllComponents({
        clusterName: currentCluster.clusterName,
        context,
        filters
      })
      await this.getComponentData()
    }else{
      res = await actions.host[componentOperateData.action](componentOperateData)
      await this.getComponentData()
    }
    if(res && res.code == 1){
      this.setState({
        componentOperateData: {},
        confirmLoading: false,
        errMsg: null
      })
    }else{
      this.setState({
        confirmLoading: false,
        errMsg: `操作失败(${res.statusCode == 200 ? res.data : res.statusCode})`
      })
    }
  }
  // 取消执行操作
  cancelOperate = () => {
    this.setState({
      componentOperateData: {},
      confirmLoading: false
    })
  }
  // 获取stale_configs=true的组件(需要重启的)
  getNeedRestartService = (host_components, leap_components) => {
    let newComponent = {}
    leap_components.map(rec => {
      newComponent[rec.HostRoles.component_name] = rec.HostRoles
    })
    let _finally = []
    host_components && host_components.filter(rec => {
      const otherAttr = newComponent[rec.name]
      const component = Object.assign({}, rec, otherAttr)
      if(component.stale_configs){
        _finally.push(component)
      }
    })
    return _finally
  }
  render() {
    const {componentOperateData, confirmLoading, errMsg, needRestart, currComponents, summary} = this.state
    let host_components = this.props.host_components || summary 
    const clients = {}
    let modalTitle
    if(Array.isArray(componentOperateData)){
      modalTitle = '确认'
    }else if(componentOperateData && componentOperateData.text){
      modalTitle = '确认' + componentOperateData.text + '?'
    }
    let newComponent = {}
    host_components.map(rec => {
      newComponent[rec.HostRoles.component_name] = rec.HostRoles
    })
    if(host_components && host_components.length == 0){
      return <Spin spinning={true}/>
    }
    return (
      <Card className="hostDetailCard components" title="组件" extra={this.hostComponentsAddBtn()}>
          { 
            needRestart && needRestart.length > 0 && 
            <h2 className="componentNeedRestart">主机需要 {needRestart.length} 组件 重新启动 
            <Button onClick={this.componentOperate.bind(this, needRestart)} data-type='restartNeedComponents'>重新启动</Button></h2> 
          }
          <ul>
            {
              currComponents &&
              currComponents.map((rec,idx) => {
                const otherAttr = newComponent[rec.name]
                const component = Object.assign({}, rec, otherAttr)
                let clientComponts
                let masterComponents

                if(component.displayName.indexOf('Client') >= 0){
                  clients[component.displayName] = component
                }else{
                  let iconType = 'warning'
                  const state = component.state// ? newComponent[component.dispalyName].state : 'INSTALLED'
    
                  if(state == 'STARTED'){
                    iconType = 'check-circle'
                  }
                  masterComponents = (<li className="clearfix" key={rec.id}>
                    <span className="fl">
                      <Tooltip placement="top" title={iconType == 'warning' ? '已停止' : '已启动'}>
                        <Icon type={iconType} className={'stateIcon ' + iconType} /> 
                      </Tooltip>
                      {component.displayName} / 
                      <Link to={`/monitor/services/${component.belongServiceName}`}> {services[component.belongServiceName].display_name}</Link>
                      { 
                        component.maintenance_state == 'ON' && 
                        <Tooltip placement="right" title={`${component.displayName}组件已打开维护模式`}>
                          <Icon type="medicine-box" className={'stateIcon medicine-box noMargin'}/>
                        </Tooltip>
                      }
                    </span>
                    <div className="operate fr">
                    <Popover overlayClassName="hostTabOperate componentOperateMenu" placement="bottom" content={this.hostComponentsOperateMenu(component)} trigger="focus">
                      <Button style={{border: 'none'}}>{state == 'STARTED' ? '已启动' : '已停止'}<Icon type="caret-down" style={{marginLeft: '10px'}}/></Button>
                    </Popover>
                    </div>
                  </li>)
                }
                if (idx == currComponents.length - 1) {
                  const clientKeys = Object.keys(clients)
                  if (clientKeys.length > 0) {
                    clientComponts = <li className="clearfix" key='last'>
                      <span className="fl" style={{ marginLeft: 22 }}>
                        客户端 /
                      </span>
                      <span className="fl" style={{ width: '47%', marginLeft: 10 }}>
                        {clientKeys.join('，')}
                      </span>
                      <div className="operate fr">
                        <Popover overlayClassName="hostTabOperate componentOperateMenu" placement="bottom" content={this.hostComponentsOperateMenu(clients, 'client')} trigger="focus">
                          <Button style={{ border: 'none' }}>已安装<Icon type="caret-down" style={{ marginLeft: '10px' }} /></Button>
                        </Popover>
                      </div>
                    </li>
                  }
                }

                return [masterComponents, clientComponts]
              })
            }
          </ul>
        <Modal
          title={modalTitle}
          visible={ modalTitle ? true : false}
          onOk={this.operateOk}
          onCancel={this.cancelOperate}
          confirmLoading={confirmLoading}
          okText="确认"
          cancelText="取消"
        >
          <label>{modalTitle}</label>
          {errMsg && <p style={{color: 'red'}}>{errMsg}</p>}
        </Modal>
      </Card>
    )
  }
}

Components.defaultProps = {
  host_components: []
}