import React, { Component } from 'react'
import {Button, message, Steps,Input, Radio, Select,Table, InputNumber} from 'antd'
// import Select
import axios from 'axios'
import {connect,actions} from 'mirrorx'
import './index.less'
import {createCluster} from 'api/createCluster'
import _ from 'lodash'
const Step = Steps.Step
const RadioGroup = Radio.Group
const Option = Select.Option
const RadioButton = Radio.Button

class Create extends Component {
  constructor(props){
    super(props)
    this.state = {
      tableRadioValue: 0,
      servercount: 3,
      instanceConf: {},
      selectlist: null,
      serviceType: 'g',
      servertype: 'bigData'
    }
    this.tipsObj = {}
    this.ENtoCN = {vCPU: 'vCPU', 'memory': '内存', 'storage': '存储', 'system': '系统'}
  }
  componentWillMount = () => {
    const value = this.props.value
    if(value){
      this.setState({...value})
      return
    }
    actions.create.getClusterType().then(res => {
      if(res){
        const datalist = this.formatData(res, this.state.serviceType)
        const filters = this.getFilterData(datalist)
        this.setState({
          datalist,
          filters,
          tableRadioValue: datalist[0].id,
          tableRadioData : datalist[0]
        })
      }
    })
  }

  setFormValue = (keys, e) => {
    let value = typeof e == 'object' ? e.target.value : e
    if(keys == 'serviceType'){
      // const datalist = this.formatData(this.props.clusterTypes, value)
      const datalist = this.filterData(this.state.instanceConf, value)
      let filters = this.getFilterData(datalist)
      // const filters = this.getFilterData(datalist)
      // , datalist, filters, instanceConf: {}
      this.setState({[keys]: value, datalist, filters})
      return
    }
    this.setState({[keys]: value, clusterNameIsEmpty: false})
  }

  selectListChange = (keys, val) => {
    const {instanceConf} = this.state
    if(val){
      instanceConf[keys] = val
    }else{
      delete instanceConf[keys]
    }
    const data = this.filterData(instanceConf)
    let filters = this.getFilterData(data)
    if(Object.keys(instanceConf).length == 1){
      let {clusterTypes} = this.props
      const backupData = _.cloneDeep(clusterTypes)
      let allData = this.formatData(backupData, this.state.serviceType)
      let newFilterData = []
      let newKey = Object.keys(instanceConf)[0]
      allData.map(rec => {
        if(!newFilterData.includes(rec[newKey])){
          newFilterData.push(rec[newKey])
        }
      })
      filters[newKey] = newFilterData
      // filters = this.getFilterData(filtersList)
    }
    this.setState({
      instanceConf,
      datalist: data,
      filters
    })
  }
  // 组装数据
  formatData(types, category){
    // 获取筛选条件列表
    let filters
    let finallyData = []
    Array.isArray(types) && types.length && types.map((rec, index) => {
      // cup核数-内存大小（G）-磁盘容量（G）
      const item = rec.split('-')
      let vCPU = `${item[0]} vCPU`
      let memory = `${item[1]} GB`
      let storage = `${item[2]} GB`
      let system = 'centos'
      finallyData.push({id: rec, vCPU, memory, storage, system})
    })
    
    let i = 0
    finallyData = finallyData.map(rec => {
      const item = rec.id.split('-')
      if(category == 'c' && item[1] >= 32){
        i++
        rec.name = `计算型${i}`
        return rec
      }
      else if(category == 'm' && item[2] >= 100){
        i++
        rec.name = `内存型${i}`
        return rec
      }
      else if (category == 'g' && item[2] < 100 && item[1] < 32){
        i++
        rec.name = `通用型${i}`
        return rec
      }
    })
    return finallyData.filter(rec => rec != undefined)
  }
  // 筛选数据
  filterData(instanceConf, serviceType){
    let {clusterTypes} = this.props
    const backupData = _.cloneDeep(clusterTypes)
    let filterData = this.formatData(backupData, serviceType || this.state.serviceType)
    
    const keys = Object.keys(instanceConf)
    if(keys.length == 0){
      // filterData = backupData
    }else{
      keys.map(key => {
        const val = instanceConf[key]
        if(!val) {
          return
        }
        filterData = filterData.filter(rec => {
          return rec[key] === val
        })
      })
    }
    filterData = filterData.length ? filterData : null
    if(!filterData){
      return []
    }

    return filterData
  }
  // 筛选条件数据
  getFilterData(types){
    let filters = {vCPU: [], 'memory': [], 'storage': [], 'system': []}
    Array.isArray(types) && types.length && types.map(rec => {
      // cup核数-内存大小（G）-磁盘容量（G）
      const item = rec.id ? rec.id.split('-') : rec.split('-')
      let vCPU = `${item[0]} vCPU`
      let memory = `${item[1]} GB`
      let storage = `${item[2]} GB`
      let system = 'centos'
      if(! filters.vCPU.includes(vCPU)){
        filters.vCPU.push(vCPU)
      }
      if(! filters['memory'].includes(memory)){
        filters['memory'].push(memory)
      }
      if(! filters['storage'].includes(storage)){
        filters['storage'].push(storage)
      }
      if(! filters['system'].includes(system)){
        filters['system'].push(system)
      }
    })
    return filters
  }
  // 确认提交 下一步
  submitHandle = async (e) => {
    const {clustername, servertype, servercount, serviceType, tableRadioValue, tableRadioData} = this.state
    if(! clustername){
      this.setState({
        clusterNameIsEmpty: true
      })
      return
    }
    const allData = {clustername, servertype, servercount, serviceType, tableRadioValue, tableRadioData}
    // 如果已创建集群  则直接下一步
    if(this.props.value){
      this.props.onsubmit(this.props.value, 1, 'config')
      return
    }
    this.setState({
      loading: true
    })
    await createCluster({clusterAlias: clustername, servercount, servertype: tableRadioValue})
    .then(res => {
      this.setState({
        loading: false
      })
      if(res.code === 1){
        actions.create.setHostList(res.data.vmNodes)
        allData._clusterName = res.data.clusterName
        this.props.onsubmit && this.props.onsubmit(allData, 1, 'config')
      }else{
        message.error('创建失败')
      }
    })
  }
  setOptions = (data) => {
    return data.map(rec => {
      if(Object.prototype.toString.call(rec) === "[object Object]"){
        return <Option key={rec.value} value={rec.value}>{rec.name}</Option>
      }else{
        return <Option key={rec} value={rec}>{rec}</Option>
      }
    })
  }
  tableRadioChange = (e) => {
    this.setState({tableRadioValue: e.target.value})
  }

  sortHandle = (x, y, attr) => {
    const num1 = +x[attr].match(/[0-9]\d*/ig)[0]
    const num2 = +y[attr].match(/[0-9]\d*/ig)[0]
    return num1 - num2
  }

  columns = () => {
    return [
        {
          title: '',
          dataIndex: 'id',
          render: (text, record, index) => {
            return <Radio value={record.id} checked={this.state.tableRadioValue == record.id} onChange={this.tableRadioChange}></Radio>
          }
        },
        {
            title: '规格',
            dataIndex: 'name',
            key: 'name',
            sorter: (a, b) => {
              return this.sortHandle(a, b, 'name')
            }
        }, {
            title: 'vCPU',
            dataIndex: 'vCPU',
            key: 'vCPU',
            sorter: (a, b) => {
              return this.sortHandle(a, b, 'vCPU')
            }
        }, {
            title: '内存',
            dataIndex: 'memory',
            key: 'memory',
            sorter: (a, b) => {
              return this.sortHandle(a, b, 'memory')
            }
        }, {
            title: '存储',
            dataIndex: 'storage',
            sorter: (a, b) => {
              return this.sortHandle(a, b, 'storage')
            }
        }, {
            title: '操作系统',
            dataIndex: 'system',
            key: 'system',
            sorter: (a, b) => a.system.charCodeAt() - b.system.charCodeAt()
        }
    ]
  }

  onRowSelectHandle = (record) => {
    return {
      onClick: () => {
        this.setState({
          tableRadioValue: record.id,
          tableRadioData : record
        })
      }
    }   
  }
  render() {
    const {
      clustername, 
      servertype, 
      servercount, 
      instanceConf, 
      loading,
      datalist,
      filters,
      clusterNameIsEmpty,
      serviceType, 
      tableRadioData
    } = this.state

    return (
      <div className="configuration stepContent">
        <div className={`formItem clusterName ${clusterNameIsEmpty ? 'error' : ''}`}>
          <label>集群名称: </label>
          <Input 
           className="inputInline clusterNameIpt" 
           placeholder="" value={clustername} 
           onChange={this.setFormValue.bind(this, 'clustername')} 
           disabled={this.props.value ? true : false}/>
          <p className="errorTips" >请输入集群名称</p>
        </div>
        {/* <div className="formItem clusterType">
          <label>集群类型: </label>
          <RadioGroup value={servertype || 'bigData'} onChange={this.setFormValue.bind(this, 'servertype')}>
            <Radio value='bigData'>大数据</Radio>
          </RadioGroup>
        </div> */}
        <div className="formItem serviceType instance">
          <label>创建服务实例: </label>
            {
              filters && Object.keys(filters).map((rec, idx) => {
                const item = filters[rec]
                return <span key={idx} className="selectItem">
                  <label>{this.ENtoCN[rec]} </label>
                  <Select 
                      value={instanceConf[rec]} 
                      allowClear 
                      onChange={this.selectListChange.bind(this, rec)} 
                      placeholder={`请选择${this.ENtoCN[rec]}`}>
                    {this.setOptions(item)}</Select>
                </span>
              })
            }
          {/* <p ref={(el) => this.tipsObj['clusterType'] = el}></p> */}
        </div>
        <div className="formItem serviceType chooseProduct">
          <span className="classify">
            <label>分类: </label>
            <RadioGroup defaultValue="g" size="large" value={serviceType || 'g'} onChange={this.setFormValue.bind(this, 'serviceType')} className="buttonSelect">
              <RadioButton value="g">通用型</RadioButton>
              <RadioButton value="c" >计算型</RadioButton>
              <RadioButton value="m" >内存型</RadioButton>
              {/* <RadioButton value="d" disabled>大数据型</RadioButton>
              <RadioButton value="e" disabled>本地SSD</RadioButton>
              <RadioButton value="f" disabled>高主频率</RadioButton>
              <RadioButton value="g" disabled>入门级(共享)</RadioButton> */}
            </RadioGroup>
          </span>
          {
            !this.props.value &&
              <Table bordered 
                onRow={this.onRowSelectHandle} 
                pagination={false} 
                loading={datalist ? false : true}
                rowKey={(record) => record.id} 
                columns={this.columns()} 
                dataSource={datalist}/>
          }
          
          <div className="currentSelect">
            <label>当前实例选择:</label> 
            <span>{tableRadioData ? Object.keys(tableRadioData).map((rec, idx) => {
              if(rec ==='id') return ''
              if(idx != 1){
                return `, ${tableRadioData[rec]}`
              }else{
                return tableRadioData[rec]
              }
            }): tableRadioData}</span>
          </div>
          <div className="createNumber">
            <label>创建实例数量:</label>
            <span><InputNumber min={3} max={10} disabled={this.props.value ? true : false} className="inputNumber" value={servercount} onChange={this.setFormValue.bind(this, 'servercount')}/> 台</span>
          </div>
          {/* <p className="errorTips" ref={(el) => this.createError = el}></p> */}
          <Button loading={loading} type="primary" onClick={this.submitHandle} className="stepNext">下一步</Button>
          { !clustername && <p style={{color: '#f5222d', margin: 0}} >尚未输入集群名称</p> }
        </div>
      </div> 
    )
  }
}

const ConnCreate = connect(state=>{
  return{
    clusterTypes: state.create.clusterTypes,
    selectlist: state.create.clusterFilterList
  }
})(Create)
export default ConnCreate
