import React, { Component } from 'react'
import {Table, Select, Input, Spin, Tooltip, Button} from 'antd'
import { connect, actions} from 'mirrorx'
import moment from 'moment'
import './index.less'
const Option = Select.Option
class Alerts extends Component {
  constructor(props){
    super(props)
    this.state = {
      filterValues: {}
    }
    this.stateToCN = {
      CURRENT: '当前的',
    }
  }
  async componentDidMount(){
    let {summaryInfo} = this.state
    if(summaryInfo && summaryInfo.stack_versions){
      const data = this.formatVersionData(summaryInfo.stack_versions)
      actions.host.getVersions(data)
    }else{
      const host_name = this.props.match.path.split('/')[2]
      summaryInfo = await actions.host.getHostSummary({host_name})
      if(summaryInfo && summaryInfo.stack_versions){
        const data = this.formatVersionData(summaryInfo.stack_versions)
        actions.host.getVersions(data)
      }else{
        actions.host.getVersions([])
      }
    }
  }
  formatVersionData = (data) => {
    return data.map(rec => {
      return {
        ...rec.HostStackVersions,
        ...rec.repository_versions[0].RepositoryVersions
      }
    })
  }
  filterValueChange = (key, value) => {
    const val = typeof value == 'object' ? value.target.value : value
    const {filterValues} = this.state
    if(val !== 'all'){
      filterValues[key] = val
    }else{
      delete filterValues[key]
    }
    // 全部   已安装   安装中   安装失败   状态不同步，原因可能是您在集群中执行了以下操作：添加主机、添加服务、添加组件、移动组件。  当前的  升级中   升级失败
    actions.host.filterAlertsList({filterValues, type: 'version'})
    this.setState({
      filterValues
    })
  }

  columns = () => {
    const {versionFilter} = this.props
    const {filterValues} = this.state
    return [{
      title: <span className="tableHeaderOperate">服务: 
        <Select allowClear value={filterValues['stack_name']|| 'all'} onChange={this.filterValueChange.bind(this, 'stack_name')}>
          <Option value='all'>全部</Option>
          {
            versionFilter && versionFilter.server.map(rec => {
              return <Option value={rec} key={rec}>{rec}</Option>
            })
          }
        </Select></span>,
      dataIndex: 'stack_name',
    },{
      title: <span className="tableHeaderOperate">名称: 
        <Select allowClear value={filterValues['display_name']|| 'all'} onChange={this.filterValueChange.bind(this, 'display_name')}>
          <Option value='all'>全部</Option>
          {
            versionFilter && versionFilter.display_name.map(rec => {
              return <Option value={rec} key={rec}>{rec}</Option>
            })
          }
        </Select></span>,
      dataIndex: 'display_name'
    }, {
      title: <span className="tableHeaderOperate">状态: 
        <Select allowClear value={filterValues['state']|| 'all'} onChange={this.filterValueChange.bind(this, 'state')}>
          <Option value='all'>全部</Option>
          {
            versionFilter && versionFilter.state.map(rec => {
              return <Option value={rec} key={rec}>{rec}</Option>
            })
          }
        </Select></span>,
      dataIndex: 'state',
      render: (r) => <span className={`statusBtn ${r}`}>{r}</span>
    }, {
      title: '',
      dataIndex: 'host_name',
      render: (record) => <Button disabled={record.state == 'CURRENT' ? true : false}>安装</Button>
    }]
  }

  render() {
    const {versions, summaryInfo} = this.props
    // const versionsList = versions.data
    if(!versions){
      return <Spin style={{width: '100%', height: '100%'}}/>
    }
    return (
      <div className="hostAlerts hostVersions">
        <Table dataSource={versions} columns={this.columns()} rowKey={record => record.href}/>
      </div>
    )
  }
}

export default connect(state => ({
  versions: state.host.versions,
  summaryInfo: state.host.summaryInfo,
  versionFilter: state.host.versionFilter
}))(Alerts)
