import request from './../utils/asyncService'

const cluster = 'LEAP';
/**
 * 获取集群
 */
const getCluster=()=>{
    return request({
        url:'/api/v1/clusters?fields=Clusters/provisioning_state'
    })
}
/**
 * 获取所有的服务
 * @returns {*}
 */
const getAllService = (data) => request({
    url: '/platform/ForwardTo',
    data: {
        url: `/api/v1/clusters/${data.currentCluster}/services?fields=ServiceInfo/state,ServiceInfo/maintenance_state,components/ServiceComponentInfo/component_name&minimal_response=true`,
        clusterName: data.currentCluster,
        method: 'get',
    }
})
/**
 * 获取StackServices
 * @returns {*}
 */
const getAllStackService = (data) => request({
    url: '/platform/ForwardTo',
    data: {
        url: `/api/v1/stacks/LEAP/versions/3.4.4/services?fields=StackServices/*,components/*,components/dependencies/Dependencies/scope,artifacts/Artifacts/artifact_name`,
        clusterName: data.currentCluster,
        method: 'get',
    }
})

/**
 * 获取主机列表
 * @returns {*}
 */
const getHostList = (data) => request({
    url: '/platform/getClusterVmnodes',
    data: {
        clusterID: data.clusterID
    }
})

/**
 * 获取主机安装组件列表
 * @returns {*}
 */
const getHostComponents = (data) => request({
    url: '/platform/getClusterVmnodesWithComponents',
    data: {
        clusterID: data.clusterID
    }
})

export {
    getCluster,
    getAllService,
    getAllStackService,
    getHostList,
    getHostComponents
}