import React, { Component } from 'react'
import {Button, Checkbox, Tooltip, Icon} from 'antd'
import {actions, connect} from 'mirrorx'

import { services } from 'config/services'


class LeapDevTools extends Component {
  constructor(props){
    super(props)
    this.state = {}
  }
  async componentWillMount(){
    let selectComponent = this.props.value
    let serviceName = this.props.serviceName
    
    
    let {devTools} = this.props
    if(!devTools){
      await actions.create.getService()
    }
    devTools = this.props.devTools
    const {installedServices} = this.props
    selectComponent = selectComponent || {}
    devTools.map((rec, index) => {

      if(installedServices){
        if(installedServices.includes(rec.name) || serviceName == rec.name){
          selectComponent[rec.name] = +rec.id
        }
      }else{
        if(rec.necessary){
          selectComponent[rec.name] = +rec.id
        }
      }

    })
    // devTools = this.hasRequired(selectComponent, devTools)
    
    this.setState({selectComponent, devTools})
  }
  // 选择服务 from: 判定是直接点击checkbox还是点击其他元素
  checkboxHandle = (reocrd, from, e) => {
    if(e.target.className == 'errorDom') return
    const selectComponent = _.cloneDeep(this.state.selectComponent) || {}
    const devTools = _.cloneDeep(this.state.devTools)
    const checked = from ? !selectComponent[reocrd.name] : e.target.checked
    
    checked
      ? (selectComponent[reocrd.name] = +reocrd.id)
      : delete selectComponent[reocrd.name]

    let service = this.hasRequired(selectComponent, devTools)
    this.setState({ selectComponent, devTools: service})
  }
  // 判断是否有依赖项
  hasRequired = (selectComponent, devTools) => {
    if(!devTools) return
    const chooseValus = Object.values(selectComponent)  // 已选中项的ID集合
    const newdevTools = {}   // 保存新组装的数据
    this.errMsgId = {}  // 清空存储错误信息的对象
    
    // 重新组装数据 {id: record}, 并清空依赖信息 以待后用
    devTools.map(rec => {
      rec.requiredServiceName = null
      newdevTools[rec.id] = rec
    })
    // 根据已选择数据 重新设置依赖数据
    chooseValus && chooseValus.map(chooseId => {
      let requiredComponents = {}      // 当前的依赖项对象 {id: name}
      const item = newdevTools[chooseId]
      if(item && item.requiredServices){
        let curRequired = item.requiredServices.split(',')
        curRequired.map(req => {
          if(!chooseValus.includes(+req)){
            requiredComponents[req] = newdevTools[req].displayName
            this.errMsgId[req] = newdevTools[req].displayName
          }
        })
        newdevTools[chooseId].requiredServiceName = requiredComponents
      }
    })
    return Object.values(newdevTools)
  }
  // components
  nextStepHandle = () => {
    const {selectComponent} = this.state

    if(this.props.serviceName){
      let {serviceList, installedServices} = this.props
      let master_selectComponent = {}
      serviceList.map((rec, index) => {
        if(installedServices){
          if(installedServices.includes(rec.name)){
            master_selectComponent[rec.name] = +rec.id
          }
        }
      })
      const allService = {...selectComponent, ...master_selectComponent}
      actions.create.getComponents(allService)
      this.props.onsubmit(master_selectComponent, 1, "installDevTools")
      return 
    }
    const {components} = this.props
    const allService = {...components, ...selectComponent}
    actions.create.getComponents(allService)
    this.props.onsubmit(this.state.selectComponent, 1, 'installDevTools')
  }
  render() {
    const selectComponent = this.state.selectComponent || {}
    const {devTools, installedServices} = this.props
    return (
      <div className="installComponent stepContent">
        <h2>请选择需要安装的LEAP开发工具</h2>
        <div className="componentBox">
          <div className="selectGroup">
          {
            devTools && devTools.map((item, idx) => {
              return (
                <div className="selectItem" 
                 key={item.id} 
                 onClick={item.necessary ? () => {} : this.checkboxHandle.bind(this, item, 'click')}>
                  <Checkbox
                  disabled={installedServices ? installedServices.includes(item.name) : item.necessary}
                  onChange={this.checkboxHandle.bind(this, item, '')} checked={selectComponent[item.name] || false}></Checkbox>
                  <span className="selectItemName">
                    {item.displayName}
                    <Tooltip title={services[item.name].comment}>
                      <Icon type="question-circle-o" className="serviceComment"/>
                    </Tooltip>
                  </span>
                  <span>版本号: {services[item.name].versions}</span>
                  {/* <span className="comment">{services[item.name].comment}</span> */}
                </div>
              )
            })
          }
          </div>
        </div>
        <div className="btnGroup">
        {
          !this.props.installedServices &&
          <Button onClick={() => this.props.onsubmit(null, -1, 'installDevTools')}>上一步</Button>
        }
          <Button type="primary" className="stepNext" onClick={this.nextStepHandle}>下一步</Button>
        </div>
      </div>
    )
  }
}
const ConnLeapDevTools = connect(state=>{
  return{
    devTools: state.create.devTools,
    serviceList: state.create.serviceList
  }
})(LeapDevTools)
export default ConnLeapDevTools