import React, {Component} from 'react'
import {connect, actions} from 'mirrorx'
import {Table, Icon, Button, Tag} from 'antd'


class ComponentVersion extends Component {
    constructor(props) {
        super(props)
        this.state = {
            // version: 'LEAP-3.4.4.0'
        }
    }
    getColumns = () => {
        return [{
            title: '',
            dataIndex: 'name',
            key: 'name',
            width: 150
        }, {
            title: 
                <div>
                    {this.props.version}&nbsp;&nbsp;
                    {/* <span style={{color: '#999'}}>(<a href="javascript:;" style={{color: '#999'}}>显示详细信息</a>)</span><br/> */}
                    <span style={{color: '#999'}}>当前</span>
                </div>
            ,
            dataIndex: 'versions',
            key: 'versions',
            render: (value, row, index) => (
                <Tag color="#1ACB40">
                  {value}
                </Tag>
              )
        }]
    }

    render() {
        const { status, componentsListData } = this.props
        return <div>
            <Table 
                loading={status} 
                bordered={true} 
                dataSource={componentsListData} 
                columns={this.getColumns()}
                pagination={false}
            />
        </div>
    }
}

const ConnComponentVersion = connect(state => {
    return {
        version: state.components.version,
        status: state.components.status,
        componentsListData: state.components.componentsListData,
    }
})(ComponentVersion)
export default ConnComponentVersion