import React, { Component } from 'react'
import { Steps, Input, Button, Form, message, Icon } from 'antd'
import StepsComponent from '../create/stepComponent'
import Header from 'components/loginHeader'
import Pic from 'assets/images/common/register_bg.png'
import axios from 'axios'
import StepSuccess from '../create/success'
import {connect,actions} from 'mirrorx'
import './index.less'
const Step = Steps.Step
const FormItem = Form.Item
class ForgetPwd extends Component {
  constructor(props) {
    super(props)
    this.state = {
      currentStep: 0,
      times: 5
    }

  }
  // componentDidMount = () => {
  //   let step = location.hash && location.hash.indexOf('step') >= 0 && location.hash.split('=')[1]
  //   step = +step || 0
  //   this.setState({currentStep: step})
  // }
  
  inputChange = (keys, e) => {
    if(keys === 'username'){
      this.emailErr.className = 'rowInput'
    }
    this.setState({
      [keys]: e.target.value
    })
  }
  getVCode = () => {
    let {username} = this.state
    if(!username){
      this.emailErr.className = 'rowInput error'
      return
    }
    this.setState({
      nextLoading: true
    })
    axios.post('/user/getMailVcode', {username, forCreate: false})
    .then(res => {
      if(res.data.code == 1){
        // actions.user.logout('offline')
        this.nextStep({nextLoading: false})
      }else{
        if(res.data.message == 'Code has been send'){
          setTimeout( () => {
            this.emailErr.className = 'rowInput info'
            this.emailErr.querySelector('p').innerHTML = '邮箱已发送, 请查看最近 30 分钟内的邮件'
            this.nextStep({nextLoading: false})
          }, 1500)
        }else{
          this.setState({
            nextLoading: false
          })
        }
      }
    })
    .catch(err => {
      this.setState({
        nextLoading: false
      })
      this.emailErr.className = 'rowInput info'
      this.emailErr.querySelector('p').innerHTML = '验证码发送失败, 请稍候再试'
    })
  }
  checkVCode = () => {
    let {vcode, username} = this.state
    // /user/checkVcode
    this.setState({
      nextLoading: true
    })
    axios.post('/user/checkVcode', {username, vcode})
    .then(res => {
      this.setState({
        nextLoading: false
      })
      if(res.data.code == 1){
        this.nextStep()
      }else{
        message.error('验证码有误')
      }
    })
  }
  postNewPwd = () => {
    let password = this.props.form.getFieldsValue().password
    const {username} = this.state
    this.setState({
      nextLoading: true
    })
    axios.post('/user/resetPassword', {username, password})
    .then(res => {
      this.setState({
        nextLoading: false
      })
      if(res.data.code == 1){
        this.nextStep()
      }
    })
    // this.times()
    // this.setState({
    //   currentStep: +this.state.currentStep + 1
    // })
  }
  checkThePwd = (rule, value, callback) => {
    const reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/
    if(! reg.test(value)){
      callback('请输入8~12位数字字母组成的密码')
    }
    callback()
    return
  }
  checkTheRePwd = (rule, value, callback) => {
    let password = this.props.form.getFieldsValue().password
    if(!this.props.form.isFieldTouched('password')){
      this.props.form.setFields({
        password: {
          errors: [new Error('请先输入密码')],
        }
      })
    }
    if(password !== value){
      callback('两次密码输入不一致')
    }
    callback()
    return
  }
  nextStep = (states) => {
    const {currentStep} = this.state
    location.hash = `?step=${+currentStep + 1}`/
    this.setState({
      currentStep: +currentStep + 1,
      ...states
    })
  }
  render() {
    const steps = ['填写邮箱', '身份验证', '设置新密码', '修改完成']
    const { currentStep, times, nextLoading } = this.state
    const { getFieldDecorator } = this.props.form
    return (
      <div className="changePwd">
        <Header />
        <div className="changePwdContent">
          <div className="changePwdWrapper">
            <div className="leftContent fl">
              <h2>找回密码</h2>
              <StepsComponent className="createSteps" currentStep={currentStep || 0} steps={steps}>
                {(step, index) => <Step key={index} title={step} icon={<i>{index + 1}</i>} />}
              </StepsComponent>
              {
                currentStep == 0 &&
                <div className="forgetPwd forgetPwdStep1">
                  <div className="rowInput" ref={(el) => this.emailErr = el}>
                    <Input placeholder="请输入邮箱" onChange={this.inputChange.bind(this, 'username')} prefix={<label>邮箱:</label>} />
                    <p >请输入正确的邮箱地址</p>
                  </div>
                  <Button loading={nextLoading} type="primary" onClick={this.getVCode}>获取验证码</Button>
                  {/* <span>已经获取验证码, 直接<a href="#" onClick={this.nextStep}>下一步</a></span>                   */}
                </div>
              }
              {
                currentStep == 1 &&
                <div className="forgetPwd forgetPwdStep2">
                  <div className="rowInput">
                    <Input onChange={this.inputChange.bind(this, 'vcode')} placeholder="请输入验证码" prefix={<label>验证码:</label>} />
                    <Button onClick={this.getVCode} className="reget">重新获取</Button>
                  </div>
                  <Button loading={nextLoading} type="primary" onClick={this.checkVCode}>下一步</Button>
                </div>
              }
              {
                currentStep == 2 &&
                <Form className="forgetPwd changePwdForm" onSubmit={this.handleSubmit}>
                  <FormItem>
                    {getFieldDecorator('password', {
                      rules: [{
                        required: true,
                        message: '请输入密码'
                      },{ validator: this.checkThePwd }],
                    })(
                      <Input type="password" placeholder="请输入密码" prefix={"修改密码:"} />
                    )}
                  </FormItem>
                  <FormItem>
                    {getFieldDecorator('rePassword', {
                      rules: [{
                        required: true,
                        message: '请确认密码'
                      },{ validator: this.checkTheRePwd }],
                    })(
                      <Input type="password" placeholder="请确认密码" prefix={"确认密码:"} />
                    )}
                  </FormItem>
                  <Button loading={nextLoading} type="primary" onClick={this.postNewPwd}>确认</Button>
                </Form>
              }
              {
                currentStep == 3 &&
                <StepSuccess title="密码修改完成" pathTo="signIn"/>
              }
            </div>
            <div className="rightPic fl">
              <img src={Pic} alt="修改密码" />
            </div>
          </div>

        </div>
      </div>
    )
  }
}
ForgetPwd = Form.create()(ForgetPwd)
export default ForgetPwd