import mirror, {actions} from 'mirrorx'
import * as API from "api/components"

mirror.model({
    name: 'components',
    initialState: {
        status: true, // 加载状态
        componentsEntity: [
            'HDFS',
            'YARN',
            'MAPREDUCE2',
            'HIVE',
            'HBASE',
            'SQOOP',
            'ZOOKEEPER',
            'FLUME',
            'AMBARI_METRICS',
            'KAFKA',
            'STORM',
            'LOGSEARCH',
            'SPARK2',
            'AMBARI_INFRA',
            'ELK',
            'HADOOP_KMS',
            'IMPALA',
            'SENTRY'
        ],
        toolsEntity: [
            'DATA HUB',
            'User Admin',
            'Task Scheduler',
            'SQL Editor',
        ],
        version: '', // leap版本
        hostSum: '',
        componentsListData: [],
        servicesList: [],
        componentsVersionData: [],
    },
    reducers: {
        updateState(state, data) {
            return {
                ...state, ...data
            }
        },

        updateComponentsList(state, data) {
            return {
                ...state,
                ...data
            }
        },

        updateHostInfo(state, data) {
            return {
                ...state,
                ...data
            }
        },

    },
    effects: {
        // 获取列表
        async getComponentsList(data, getState) {
            const {updateComponentsList} = actions.components
            updateComponentsList({
                status: true,
                componentsListData: []
            })

            const {componentsListData} = getState().components

            const {currentCluster} = getState().app
            let clusterName = currentCluster.clusterName

            let listRes = await API.getComponentsList(clusterName)
            listRes = listRes.data
            let installRes = await API.getComponentsInstalledList(clusterName)
            installRes = installRes.data

            let listData = listRes.items[0].repository_versions[0].RepositoryVersions.stack_services
            let installData = installRes.items

            let _installDataObj = {}

            installData.map((item, idx) => {
                _installDataObj[item.ServiceInfo.service_name] = item.ServiceInfo.state
            })

            // 添加安装状态
            listData.map((item, idx) => {
                if(_installDataObj[item.name] 
                    && (_installDataObj[item.name] == 'STARTED' || _installDataObj[item.name] == 'INSTALLED')
                ){
                    item['status'] = 1
                }else{
                    item['status'] = 0
                }
                item.serviceStatus = _installDataObj[item.name] ? _installDataObj[item.name] : null
            })

            const allListData = listData

            // 过滤组件
            listData = listData.filter((item, idx) => {
                if(data == 'components' && getState().components.componentsEntity.indexOf(item.name) != -1){
                    return item
                }else if(data == 'tools' && getState().components.toolsEntity.indexOf(item.display_name) != -1){
                    return item
                }
            })

            updateComponentsList({
                status: false,
                componentsListData: listData,
                servicesList: allListData,
                version: listRes.items[0].ClusterStackVersions.cluster_name + ' ' + listRes.items[0].ClusterStackVersions.version + '.0'
            })

            return listData
        },

        async uninstallService(data, getState){
            const {updateHostInfo, getComponentsList} = actions.components
            const {currentCluster} = getState().app
            let clusterName = currentCluster.clusterName

            let res = await API.uninstallService(data.serviceName, clusterName)

            getComponentsList(data.type)

            return res
        },

        // 获取host信息
        async getHostInfo(data, getState) {
            const {updateHostInfo} = actions.components
            const {currentCluster} = getState().app
            let clusterName = currentCluster.clusterName

            let res = await API.getHostInfo(clusterName)

            res = res.data

            updateHostInfo({
                status: false,
                hostSum: res.itemTotal
            })
        },
    }
})
