import React, {Component} from 'react'
import {Tabs, Select, Button, Input, Icon, DatePicker} from 'antd'
import ClusterState from 'pages/monitor/clusterState'
import DevelopToolStatus from 'pages/monitor/developToolStatus'
import ComponentStatus from 'pages/monitor/componentStatus'
import NullCluster from 'pages/nullCluster'

import { connect, actions, Link } from "mirrorx";

import ConsoleHeader from 'components/consoleHeader'

const TabPane = Tabs.TabPane

class IndexDashboard extends Component {
    constructor(props) {
        super(props)
        this.state = {
            curTab: 1
        }
    }

    componentWillMount(){
        const {currentCluster} = this.props
        
        if(!currentCluster){
            actions.app.getClusterList()
        }
    }

    tabChange(val) {
        this.setState({
            curTab: val
        })
    }

    render() {
        return <div>
            <ConsoleHeader/>
            {!this.props.loading ? this.props.currentCluster?
                <Tabs onChange={this.tabChange.bind(this)} value={this.state.curTab} type="card" className="pageContent" >
                    <TabPane tab="集群状态" key="1">
                        <ClusterState/>
                    </TabPane>
                    <TabPane tab="组件状态" key="2">
                        {this.state.curTab == 2 && <ComponentStatus/>}
                    </TabPane>
                    <TabPane tab="开发工具状态" key="3">
                        {this.state.curTab == 3 && <DevelopToolStatus currentType="tools"/>}
                    </TabPane>
                </Tabs> : <NullCluster/> : ''}
            </div>
    }

}

const ConnIndexDashboard = connect(state => {
    return {
        loading: state.monitor.loading,
        currentCluster: state.app.currentCluster
    }
})(IndexDashboard)
  
export default ConnIndexDashboard;