import React, { Component } from "react"
import {
  Tabs,
  Menu,
  Dropdown,
  Modal,
  Icon,
  Alert,
  Slider,
  InputNumber
} from "antd"
import { connect, actions } from "mirrorx"
import SummaryPage from "./summary"
import HeatMapsPage from "./heatmaps"
import ConfigurationPage from "./configuration"

import NullCluster from 'pages/nullCluster'

import ConsoleHeader from 'components/consoleHeader'

import './index.less'

const TabPane = Tabs.TabPane


const serviceActionMenu = (
  <Menu>
    <Menu.Item key="0">
      <i className="dealIcon start" />
      <span>启动</span>
    </Menu.Item>
    <Menu.Item key="1">
      <i className="dealIcon stop" />
      <span>停止</span>
    </Menu.Item>
    <Menu.Item key="3">
      <i className="dealIcon restartAll" />
      <span>重启所有</span>
    </Menu.Item>
  </Menu>
)

class ServiceInfo extends Component {
  constructor(props) {
    super(props)
    this.state = {
      hasHeatmapSection: ["HDFS", "YARN", "HBASE"]
    }
  }

  componentWillMount(){
    if(this.props.currentCluster){
      actions.alerts.getAlertsList()
      actions.summary.getServicesStatusInfo()
    }else{
      actions.app.getClusterList().then(rec => {
        if(rec && rec.length > 0){
          actions.alerts.getAlertsList()
          actions.summary.getServicesStatusInfo()
        }
      })
    }
  }

  tabChange() {}

  quickLink = () => {
    alert(this.props.servicesName)
  }

  render() {
    const { servicesName, clusterList, servicesStatusInfo } = this.props

    if(!clusterList || clusterList.length == 0){
      return <NullCluster />
    }

    const quickLinksMenu = (
      <Menu>
        <Menu.Item key="0">
          <a href="http://www.alipay.com/">1st menu item</a>
        </Menu.Item>
        <Menu.Item key="1">
          <a href="http://www.taobao.com/">2nd menu item</a>
        </Menu.Item>
        <Menu.Divider />
        <Menu.Item key="3">3rd menu item</Menu.Item>
      </Menu>
    )


    return (
      <div>
        <ConsoleHeader />
        <div className="serviceInfoWrap pageContent">
          <div className="breadcrumbsWrap">{servicesName}</div>
          
          <Tabs onChange={this.tabChange.bind(this)} type="card">
            <TabPane tab="概览" key="1">
              <SummaryPage servicesName={servicesName} />
            </TabPane>
            {this.state.hasHeatmapSection.indexOf(servicesName) > -1 && (
              <TabPane tab="热图" key="2">
                <HeatMapsPage servicesName={servicesName} />
              </TabPane>
            )}

            {/* <TabPane tab="配置" key="3">
              <ConfigurationPage servicesName={servicesName} />
            </TabPane> */}
          </Tabs>
          <div className="serviceButton">
            {
              servicesName == 'LEAPSQL' && servicesStatusInfo.LEAPSQL && servicesStatusInfo.LEAPSQL.status && 
              <a href={`http://sql.${servicesStatusInfo.LEAPSQL.host}:8080`} target="_blank">
                <span>快速链接</span>
              </a>
            }
            {
              servicesName == 'LEAPDATAHUB' && servicesStatusInfo.LEAPDATAHUB && servicesStatusInfo.LEAPDATAHUB.status && 
              <a href={`http://datahub.${servicesStatusInfo.LEAPDATAHUB.host}:8080`} target="_blank">
                <span>快速链接</span>
              </a>
            }
            {
              servicesName == 'LEAPID' && servicesStatusInfo.LEAPID && servicesStatusInfo.LEAPID.status && 
              <a href={`http://id.${servicesStatusInfo.LEAPID.host}:8080/leapid-admin/view/login.html`} target="_blank">
                <span>快速链接</span>
              </a>
            }
            {
              servicesName == 'LEAPPROCESS' && servicesStatusInfo.LEAPPROCESS && servicesStatusInfo.LEAPPROCESS.status && 
              <a href={`http://ts.${servicesStatusInfo.LEAPPROCESS.host}:8080/TaskSchedule`} target="_blank">
                <span>快速链接</span>
              </a>
            }

            <div className="dropDownBtnItem">
              <Dropdown overlay={serviceActionMenu} trigger={["click"]}>
                <a className="ant-dropdown-link" href="#">
                  <span>操作</span>
                  <Icon type="caret-down" />
                </a>
              </Dropdown>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const ConnServiceInfo = connect((state, { match, location }) => {
  return {
    servicesName: match.params.servicesName,
    currentCluster: state.app.currentCluster,
    clusterList: state.app.clusterList,
    servicesStatusInfo: state.summary.servicesStatusInfo
  }
})(ServiceInfo)

export default ConnServiceInfo
