import React, { Component } from "react";
import { connect, actions, Link } from "mirrorx";
import { Table, Icon, Button, Tag, Modal } from "antd";

import InstallModal from 'components/installModal'


class ComponentsList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalVisible: false,
      serviceName: '',
      installedServices: [],
      activeService: '',
      confirmSubmitLoading: false
    }
  }

  componentWillMount() {
    const {hostList} = this.props

    if(hostList.length == 0){
      actions.app.getHostList()
      actions.app.getHostComponents()
    }
  }

  uninstallService = (row) => {
    return () => {
      this.setState({
        activeService: row.name,
        confirmModal: true
      })
    }
  }

  installService = (row) => {
    return () => {
      this.setState({
        modalVisible: true,
        serviceName: row.name
      })
    }
  }

  getColumns = () => (
    [
      {
        title: "组件",
        dataIndex: "display_name",
        key: "display_name",
        render: (value, row, index) => (
          <Link to={`/monitor/services/${row.name}`}>
            {value}
          </Link>
        )
      },
      {
        title: "版本",
        dataIndex: "versions",
        key: "versions"
      },
      {
        title: "类别",
        dataIndex: "releaseVersion",
        key: "releaseVersion",
        render: (value, row, index) => (
          <div>{"大数据基础组件"}</div>
        )
      },
      {
        title: "状态",
        dataIndex: "status",
        key: "status",
        render: (value, row, index) => (
          <Tag color={value ? "#1ACB40" : "#FF5858"}>
            {value ? "已安装" : "未安装"}
          </Tag>
        )
      },
    
      {
        title: "描述",
        dataIndex: "comment",
        key: "comment",
        width: 350
      },
      {
        title: "操作",
        key: "action",
        width: 80,
        render: (text, row) => {
          if (row.status) {
            return (
              <a href="javascript:;" onClick={this.uninstallService(row)}>
                <Icon type="delete" /> 卸载
              </a>
            )
          } else {
            return (
              <a href="javascript:;" style={{ color: "#1ACB40" }} onClick={this.installService(row)}>
                <Icon type="plus-circle" /> 安装
              </a>
            )
          }
        }
      }
    ]
  )

  modalClose = () => {
    this.setState({
      modalVisible: false
    })
  }

  cancelOperate = () => {
    this.setState({
      confirmModal: false
    })
  }

  operateOk = async () => {
    this.setState({
      confirmSubmitLoading: true
    })
    await actions.components.uninstallService({serviceName: this.state.activeService, type: 'tools'})
    this.setState({
      confirmModal: false,
      confirmSubmitLoading: false
    })
  }

  render() {
    const { modalVisible, serviceName, confirmModal, confirmSubmitLoading } = this.state
    const { status, componentsListData, servicesList, hostList, hostComponentsList } = this.props

    const installedServices = []

    servicesList.forEach(item => {
      if(item.status){
        installedServices.push(item.name)
      }
    })

    const installedMaster = hostComponentsList['master']
    const installedSlave = hostComponentsList['slave']

    return (
      <div className="devToolWrap">
        <Table
          rowKey={(record) => record.name}
          loading={status}
          bordered={true}
          dataSource={componentsListData}
          columns={this.getColumns()}
        />

        <InstallModal 
          modalVisible={modalVisible}
          callbackParent={this.modalClose}
          serviceName={serviceName}
          installType={'component'}
          installedServices={installedServices}
          installedMaster={installedMaster}
          installedSlave={installedSlave}
          hostList={hostList}
        />

        <Modal
          title="提示"
          visible={ confirmModal }
          onOk={this.operateOk}
          onCancel={this.cancelOperate}
          confirmLoading={confirmSubmitLoading}
          okText="确认"
          cancelText="取消"
        >
          <label>确认要卸载吗？</label>
        </Modal>
      </div>
    );
  }
}

const ConnComponentsList = connect(state => {
  return {
    status: state.components.status,
    componentsListData: state.components.componentsListData,
    servicesList: state.components.servicesList,
    currentCluster: state.app.currentCluster,
    hostList: state.app.hostList,
    hostComponentsList: state.app.hostComponentsList,
  }
})(ComponentsList);

export default ConnComponentsList;
