import React from 'react'
import {Menu} from 'antd'
const SubMenu = Menu.SubMenu;

function OperateMenu(props) {
  return (
    <Menu onClick={props.onChange} selectable={false} style={{ width: 160 }} mode="vertical" className="hostOperateMenu">
      {PublicMenu({ ...props, title: `已选择的主机(${props.choose || 0})`, disabled: props.choose ? false : true, keys: 'choose' })}
      {/* {PublicMenu({ ...props, title: `已过滤的主机(${props.filter || props.all || 0})`, disabled: false, keys: 'filter' })} */}
      {PublicMenu({ ...props, title: `所有主机(${props.all || 0})`, disabled: false, keys: 'all' })}
    </Menu>
  )
}
export default OperateMenu


function PublicMenu(props) {
  return <SubMenu className="hostSubMenu" 
        key={props.keys} 
        disabled={props.disabled} 
        title={<span>{props.title}</span>}>
    <SubMenu key='host' title={<span><span>主机</span></span>}>
      <Menu.Item key="startAll" data-text="启动所有组件">启动所有组件</Menu.Item>
      <Menu.Item key="stopAll" data-text="停止所有组件">停止所有组件</Menu.Item>
      <Menu.Item key="restartAll" data-text="重启已选择主机上的全部组件">重启所有组件</Menu.Item>
      <Menu.Item key="reinstall" data-text="重新安装失败组件">重新安装失败组件</Menu.Item>
      <Menu.Item key="open" data-text="为hosts打开维护模式">打开维护模式</Menu.Item>
      <Menu.Item key="close" data-text="为hosts关闭维护模式">关闭维护模式</Menu.Item>
      <Menu.Item key="setRack" data-text="设置Rack">设置Rack</Menu.Item>
    </SubMenu>
    {/* <SubMenu key='DataNodes' title={<span><span>DataNodes</span></span>}>
      <Menu.Item key="start" data-text="启动 DataNodes">启动</Menu.Item>
      <Menu.Item key="stop" data-text="停止 DataNodes">停止</Menu.Item>
      <Menu.Item key="restart" data-text="restart">重启</Menu.Item>
      <Menu.Item key="add">添加</Menu.Item>
      <Menu.Item key="abandon">弃用</Menu.Item>
      <Menu.Item key="reuse">重用</Menu.Item>
    </SubMenu>
    <SubMenu key='Regionservers' title={<span><span>Regionservers</span></span>}>
      <Menu.Item key="start" data-text="停止 Regionservers">启动</Menu.Item>
      <Menu.Item key="stop" data-text="停止 Regionservers">停止</Menu.Item>
      <Menu.Item key="restart" data-text="restart">重启</Menu.Item>
      <Menu.Item key="add">添加</Menu.Item>
      <Menu.Item key="abandon">弃用</Menu.Item>
      <Menu.Item key="reuse">重用</Menu.Item>
    </SubMenu> */}
  </SubMenu>
}