import mirror, {actions} from 'mirrorx'
import * as API from "api/alerts"

mirror.model({
    name: 'alerts',
    initialState: {
        status: true, // 加载状态
        alertsList: [],
        alertsListRes: [],
        alertsListFilter: []
    },
    reducers: {
        updateState(state, data) {
            return {
                ...state, ...data
            }
        },

        updateAlertsList(state, data) {
            return {
                ...state,
                ...data
            }
        },

        updateHostInfo(state, data) {
            return {
                ...state,
                ...data
            }
        },

    },
    effects: {
        // 获取列表
        async getAlertsList(data, getState) {
            const {updateAlertsList} = actions.alerts

            updateAlertsList({
                status: true
            })


            const {currentCluster} = getState().app
            let clusterName = currentCluster.clusterName

            const res = await API.getAlertsList({clusterName: clusterName})


            let _res = []
            let _resKey = {}

            res.data.items.map((item, idx) => {
                let _index = _res.length
                
                if(_resKey[item.Alert.label] != undefined){
                    _res[_resKey[item.Alert.label]].states.push(item.Alert.state)
                }else{
                    item.Alert['states'] = [item.Alert.state]
                    _res.push(item.Alert)
                    _resKey[item.Alert.label] = _index
                }
            })

            updateAlertsList({
                status: false,
                alertsList: _res,
                alertsListRes: _res
            })
        },

        // 改变状态
        async changeAlertStatus(data, getState) {
            const {updateAlertsList, getAlertsList} = actions.alerts
            const {currentCluster} = getState().app
            let clusterName = currentCluster.clusterName

            const res = await API.changeAlertStatus({id: data.id, status: data.status, clusterName: clusterName})

            getAlertsList()
        },

        filterAlertsList(data, getState){
            const {updateAlertsList} = actions.alerts
            const {alertsListRes} = getState().alerts

            let alertsListFilter = alertsListRes
            if(data.label != ''){
                alertsListFilter = alertsListRes.filter((item) => (
                    item.label.indexOf(data.label) > -1
                ))
            }
            if(data.states != 'all'){
                alertsListFilter = alertsListRes.filter((item) => (
                    item.states == data.states 
                ))
            }
            if(data.service_name != 'all'){
                alertsListFilter = alertsListRes.filter((item) => (
                    item.service_name == data.service_name 
                ))
            }
            if(data.latest_timestamp != 'all'){
                alertsListFilter = alertsListRes.filter((item) => (
                    item.latest_timestamp > new Date().getTime() - data.latest_timestamp
                ))
            }

            updateAlertsList({
                alertsList: alertsListFilter
            })
        }

    }
})
