import moment from 'moment'

let deepObj = (obj, keyName) => {
    let ret;
    if (keyName === '') {
        return obj;
    }
    if (!keyName && 'string' === typeof obj) {
        keyName = obj;
        obj = null;
    }
    if (!obj || keyName.indexOf('.') !== -1) {
        return getPath(obj, keyName);
    }
    ret = obj[keyName];
    return ret
}

function normalizeTuple(target, path) {
    let key;
    if (target === window) {
        key = firstKey(path);
        target = deepObj(target, key);
        path = path.slice(key.length + 1);
    }

    if (!path || path.length === 0) throw new Error('Invalid Path');
    return [target, path];
}

function getPath(root, path) {
    let parts, tuple, idx, len;

    if (root === null && path.indexOf('.') === -1) {
        return deepObj(window, path);
    }
    if (!root) {
        tuple = normalizeTuple(root, path);
        root = tuple[0];
        path = tuple[1];
        tuple.length = 0;
    }
    parts = path.split(".");
    len = parts.length;
    for (idx = 0; root && idx < len; idx++) {
        root = deepObj(root, parts[idx], true);
        if (root && root.isDestroyed) {
            return undefined;
        }
    }
    return root;
}

function mapProperty(arr = [], key) {
    return arr.map(function (next) {
        return deepObj(next, key);
    });
}

function timeConverter(timestamp) {
    let m = moment(new Date(timestamp));
    return [m.format('ddd MMM DD YYYY'), m.format('HH:mm:ss')];
}

function timingFormat(time, zeroValid) {

    let intTime = parseInt(time);
    if (zeroValid && intTime == 0) {
        return 0 + ' secs';
    }
    if (!intTime) {
        return null;
    }
    let timeStr = intTime.toString();
    let lengthOfNumber = timeStr.length;
    let oneMinMs = 60000;
    let oneHourMs = 3600000;
    let oneDayMs = 86400000;

    if (lengthOfNumber < 4) {
        return time + ' ms';
    }
    if (lengthOfNumber < 7) {
        time = (time / 1000).toFixed(2);
        return time + ' secs';
    }
    if (time < oneHourMs) {
        time = (time / oneMinMs).toFixed(2);
        return time + ' mins';
    }
    if (time < oneDayMs) {
        time = (time / oneHourMs).toFixed(2);
        return time + ' hours';
    }
    time = (time / oneDayMs).toFixed(2);
    return time + ' days';
}

function uptimeProcessing(uptime) {
    let uptimeString = timeConverter(uptime);
    let diff = new Date().getTime() - uptime;//todo 源代码做了判断，暂时不知道怎么弄的
    if (diff < 0) {
        diff = 0;
    }
    let formatted = timingFormat(diff);
    let timeUnit = null;
    if (formatted) {
        switch (formatted.split(" ")[1]) {
            case 'secs':
                timeUnit = 's';
                break;
            case 'hours':
                timeUnit = 'hr';
                break;
            case 'days':
                timeUnit = 'd';
                break;
            case 'mins':
                timeUnit = 'min';
                break;
            default:
                timeUnit = formatted.split(" ")[1];
        }
    }
    return {
        timeUnit: timeUnit,
        hiddenInfo: [formatted, uptimeString[0], uptimeString[1]],
        formatted
    };
}

export {
    deepObj, mapProperty, timeConverter, uptimeProcessing
}