import React from 'react'
import PropTypes from 'prop-types'
import { actions, Route, Switch, Link, Redirect } from 'mirrorx'
import { Layout, Menu, Icon, Button, Dropdown, Avatar, Spin } from 'antd'
import ConsoleHeader from 'components/consoleHeader'
import Monitor from 'pages/monitor'
import DevTools from 'pages/devTools'
import ComponentManage from 'pages/componentManage'
import NullCluster from 'pages/nullCluster'
import CreateCluster from 'pages/create'
import Hosts from 'pages/hosts'
import Host from 'pages/hosts/host/index.js'

import TaskProgress from '../taskProgress'

import Alerts from 'pages/alerts'

import Security from 'pages/security'

import Tools from 'pages/tools'

import Cookies from 'js-cookie'

import { userLogout } from 'api/user'
import './layout.less'



const SubMenu = Menu.SubMenu
const { Header, Sider, Content } = Layout

const LogoImg = require('assets/images/common/logo.png')
const LogoMni = require('assets/images/common/logo_mini.png')

const getNameBySelected = (selected, menuItems) => {
  let name = ''
  menuItems.forEach(item => {
    if (item.key === selected[0]) {
      name = item.name
    }
  })
  return name
}
const logout = () => {
  userLogout().then(res => {
    if(res.code === 1){
      Cookies.remove('LEAP-CLOUD-USERNAME')
      sessionStorage.removeItem('currentCluster')
      sessionStorage.removeItem('clusterList')
      actions.user.logout()
    }
  })
}

const UserMenu = () => (
  <Menu className="userMenu">
    <Menu.Item>
      <a onClick={logout}>退出</a>
    </Menu.Item>
  </Menu>
)


const LayoutMain = ({ layout, user, getClusterLoading, currentrouter, taskModalVisible }) => {
  return (
    <Layout>
      <Sider
        trigger={null}
        collapsible
        collapsed={layout.collapsed}
        width={layout.width}
        breakpoint="md"
        collapsedWidth={layout.xWidth}
        onCollapse={actions.layout.toggle}
        className="styledSider"
      >
        <div className="logo">
          <img src={layout.collapsed ? LogoMni : LogoImg} />
        </div>
        <Menu className="layoutMenu" defaultSelectedKeys={layout.selected}
          selectedKeys={layout.selectedKeys}
          defaultOpenKeys={layout.openKey} theme="dark" mode="inline">
          
          {layout.menuItems.map(subItem => {
            if(subItem['children']){
              return <SubMenu key={subItem.key} title={<span>
                <Icon className={subItem.icon} />
                <span className="menuName">{subItem.name}</span></span>}>
                {
                  subItem.children.map(item => (
                    <Menu.Item key={item.key}>
                      <Link to={`/${item.key}`}>
                        <span className="menuName">{item.name}</span>
                      </Link>
                    </Menu.Item>
                  ))
                }
              </SubMenu>
            }else{
              return <Menu.Item key={subItem.key} className="topMenu">
                <Link to={`/${subItem.key}`}>
                  <Icon className={subItem.icon} />
                  <span className="menuName">{subItem.name}</span>
                </Link>
              </Menu.Item>
            }
          })}
        </Menu>

      </Sider>

      <Layout style={{ marginLeft: !layout.collapsed ? layout.width : layout.xWidth }}>
        <div className="layoutHeader">
          <div className="trigger" onClick={actions.layout.toggle}>
            <Icon type={layout.collapsed ? 'menu-unfold' : 'menu-fold'} />
          </div>
          <div className="rightPart">
            <div className="newsInfo">
              <img src={require('../../assets/images/common/news.png')} onClick={()=>actions.host.updateState({taskModalVisible: true})} style={{cursor: 'pointer'}}/>
            </div>
            {/* <div className="langWrap">
              <img src={require('../../assets/images/common/lang.png')} />
              <span>中文（简体）</span>
            </div> */}
            <div className="userArea">
              {
                user.status === 'online'
                  ? <Dropdown overlay={<UserMenu />}>
                    <span style={{ cursor: 'pointer' }}>
                      {decodeURIComponent(user.username)} <Icon type="down" />
                    </span>
                  </Dropdown>
                  : <Link to="/signIn"><Button>登录</Button></Link>
              }
              &nbsp;<Avatar src={require("../../assets/images/common/defaultAvatar.png")} />
            </div>
          </div>
        </div>
        <Spin spinning={getClusterLoading} wrapperClassName="getClusterLoading">
        <Content className="layoutContent">
          {/* {currentrouter != '/createCluster' ? <ConsoleHeader /> : ''} */}
          <Switch>
            <Route path="/monitor" component={Monitor} />
            <Route path="/noCluster" component={NullCluster} />
            <Route path="/createCluster" component={CreateCluster} />
            <Route path="/componentManage" component={ComponentManage} />
            <Route path="/devTools" component={DevTools} />
            <Route exact path="/hosts" component={Hosts} />
            <Route path="/hosts/:host" component={Host}/>
            <Route path="/security" component={Security}/>
            <Route path="/alerts" component={Alerts}/>
            <Route path="/tools" component={Tools}/>
            
            <Redirect exact from="/" to="/monitor" />
          </Switch>
          </Content>
      </Spin>
    </Layout>
    {taskModalVisible && <TaskProgress />}
  </Layout>
  )
}

LayoutMain.propTypes = {
  collapsed: PropTypes.bool,
  selected: PropTypes.array,
  width: PropTypes.number,
  xWidth: PropTypes.number,
  menuItems: PropTypes.array
}

LayoutMain.defaultProps = {
  xWidth: 80
}

export default LayoutMain