import React, {Component} from "react"
import mirror, {actions, Link} from 'mirrorx'
import {SortableContainer, SortableElement, arrayMove} from 'react-sortable-hoc'
import * as API from "api/monitor"
import * as APIService from "api/services"
import {restartComponents} from 'api/host'

import {widgetsMapper} from './../config'
import {deepObj, mapProperty, uptimeProcessing} from './../utils/monitor'

import {Button, Input, Icon} from 'antd'

mirror.model({
    name: 'monitor',
    initialState: {
        dashboardVersion: 'new',
        status: 'loading', //获取组件状态接口
        visible: [], //显示的组件列表
        hidden: [], //隐藏的组件列表
        threshold: [], //显示的组件颜色范围设置
        modelInfo: {
            currentValue: '', //当前组件
            range: []
        },
        showDialog: false, //是否显示Dialog
        serviceComponentMetrics: [
            'host_components/metrics/jvm/memHeapUsedM',
            'host_components/metrics/jvm/HeapMemoryMax',
            'host_components/metrics/jvm/HeapMemoryUsed',
            'host_components/metrics/jvm/memHeapCommittedM',
            'host_components/metrics/mapred/jobtracker/trackers_decommissioned',
            'host_components/metrics/cpu/cpu_wio',
            'host_components/metrics/rpc/client/RpcQueueTime_avg_time',
            'host_components/metrics/dfs/FSNamesystem/*',
            'host_components/metrics/dfs/namenode/Version',
            'host_components/metrics/dfs/namenode/LiveNodes',
            'host_components/metrics/dfs/namenode/DeadNodes',
            'host_components/metrics/dfs/namenode/DecomNodes',
            'host_components/metrics/dfs/namenode/TotalFiles',
            'host_components/metrics/dfs/namenode/UpgradeFinalized',
            'host_components/metrics/dfs/namenode/Safemode',
            'host_components/metrics/runtime/StartTime'
        ],
        serviceSpecificParams: {
            'FLUME': "host_components/processes/HostComponentProcess",
            'YARN': "host_components/metrics/yarn/Queue,host_components/metrics/yarn/ClusterMetrics/N" +
                    "umActiveNMs,host_components/metrics/yarn/ClusterMetrics/NumLostNMs,host_componen" +
                    "ts/metrics/yarn/ClusterMetrics/NumUnhealthyNMs,host_components/metrics/yarn/Clus" +
                    "terMetrics/NumRebootedNMs,host_components/metrics/yarn/ClusterMetrics/NumDecommi" +
                    "ssionedNMs",
            'HBASE': "host_components/metrics/hbase/master/IsActiveMaster,host_components/metrics/hbas" +
                    "e/master/MasterStartTime,host_components/metrics/hbase/master/MasterActiveTime,h" +
                    "ost_components/metrics/hbase/master/AverageLoad,host_components/metrics/master/A" +
                    "ssignmentManger/ritCount",
            'STORM': 'metrics/api/v1/cluster/summary,metrics/api/v1/topology/summary,metrics/api/v1/ni' +
                    'mbus/summary'
        },
        loadMetric: false,
        metricViewData: {},
        serviceMetricList: [],
        flumeData: {},
        clusterInfoLoading: true,
        clusterInfo: []
    },
    reducers: {
        updateState(state, data) {
            return {
                ...state,
                ...data
            }
        },
        updateModelInfo(state, data) {
            return {
                ...state,
                modelInfo: {
                    ...state.modelInfo,
                    ...data
                }
            }
        },
        toggleDialog(state, flag) {
            return {
                ...state,
                showDialog: flag
            }
        },
        updateThreshold(state) {
            for (let item in state.threshold) {
                if (item == state.modelInfo.currentValue) {
                    state.threshold[item] = state.modelInfo.range
                }
            }
            return {
                ...state
            }
        },
        deleteWidgetVisible(state, data) {
            const {value} = data
            state
                .hidden
                .push([value, widgetsMapper[value].name])
            return {
                ...state,
                visible: state
                    .visible
                    .filter(item => {
                        return item != value
                    })
            }
        },
        updateMetricViewData(state, data) {
            return {
                ...state,
                metricViewData: {
                    ...state.metricViewData,
                    ...data
                }
            }
        },
        updateClusterInfo(state, data) {
            return {
                ...state,
                ...data
            }
        }

    },
    effects: {
        /**
         * 获取显示的组件
         * @param {*} data
         * @param {*} getState
         */
        async getClusterInfoComponent(data, getState) {
            const {updateState} = actions.monitor
            const {currentCluster} = getState().app
            let clusterName = currentCluster.clusterName

            let res = await API.getClusterStatteComponent(clusterName)

            res = res
                ? res.data
                : {
                    hidden: [],
                    visible: [],
                    threshold: [],
                    dashboardVersion: ''
                }

            updateState({
              status: "loaded",
              dashboardVersion: res.dashboardVersion || 'new',
              hidden: res.hidden || [],
              visible: res.visible || ["11", "4", "2", "1", "5", "3", "10"],
              threshold: res.threshold || {1: [80, 90], 2: [85, 95], 3: [90, 95], 4: [80, 90], 5: [1000, 3000], 6: [], 7: [], 8: [], 9: [], 10: [], 11: [], 12: [], 13: [70, 90], 14: [150, 250], 15: [3, 10], 16: [],
                17: [70, 90], 18: [], 19: [50, 75], 20: [50, 75], 21: [85, 95], 22: [85, 95], 23: [], 24: [75, 90], 25: []} 
            });

        },
        /**
         * 删除显示组件
         * @param {*} data
         * @param {*} getState
         */
        async deleteWidgetComplete(data, getState) {
            const {deleteWidgetVisible, changeVisibleOfComponent} = actions.monitor
            deleteWidgetVisible(data)
            changeVisibleOfComponent()
        },
        /**
         * 编辑显示范围
         * @param {*} data
         * @param {*} getState
         */
        async editThreshold(data, getState) {
            const {updateModelInfo, changeVisibleOfComponent, updateThreshold} = actions.monitor
            const {threshold, modelInfo} = getState().monitor
            if (data) {
                updateModelInfo({range: data})
                updateThreshold()
                await changeVisibleOfComponent()
            }
        },
        /**
         * toggleEditDialog
         * @param {*} data
         * @param {*} getState
         */
        toggleEditDialog(data, getState) {
            const {toggleDialog, updateModelInfo} = actions.monitor
            const {showDialog, threshold} = getState().monitor
            if (data) {
                const {value} = data
                updateModelInfo({currentValue: value, range: threshold[value]})
            }
            toggleDialog(!showDialog)
        },
        /**
         * 拖动后monitorInfo的值
         * @param {*} data  传过来的
         * @param {*} getState
         */
        async onSortMonitorInfoEnd(data, getState) {
            const {oldIndex, newIndex} = data
            const {visible} = getState().monitor
            const {updateState, changeVisibleOfComponent} = actions.monitor

            const afterSort = arrayMove(visible, oldIndex, newIndex)
            updateState({visible: afterSort})
            await changeVisibleOfComponent()

        },
        async addMonitorComponentInfo(data, getState) {
            const {updateState, changeVisibleOfComponent} = actions.monitor
            const {visible, dashboardVersion, threshold, hidden} = getState().monitor
            updateState({
                visible: visible.concat(data),
                hidden: hidden.filter(item => {
                    return data.indexOf(item[0]) < 0
                })
            })
            await changeVisibleOfComponent()
        },
        /**
         * 改变组件显示
         * @param {*} data
         * @param {*} getState
         */
        async changeVisibleOfComponent(data, getState) {
            const {visible, dashboardVersion, threshold, hidden} = getState().monitor
            const {currentCluster} = getState().app
            let clusterName = currentCluster.clusterName

            let dashboardData = {
                "user-pref-internaladmin-dashboard": JSON.stringify({dashboardVersion, visible, threshold, hidden})
            }
            await API.Persist(dashboardData, clusterName)
            await actions
                .monitor
                .getClusterInfoComponent()
        },

        getConditionalFields(data, getState) {
            let {serviceList} = getState().app;
            let {serviceComponentMetrics, serviceSpecificParams} = getState().monitor;
            let conditionalFields = serviceComponentMetrics.slice(0);
            let metricsKey = 'metrics/';
            serviceList.forEach(service => {
                let urlParams = serviceSpecificParams[service.ServiceInfo.service_name];
                if (urlParams) {
                    conditionalFields.push(urlParams);
                }
            })
            // todo 如果不是加载指标的话 return conditionalFields.filter(condition => {     return
            // condition.indexOf(metricsKey) === -1; });
            return conditionalFields
        },
        async getServiceMetric(data, getState) {
            const {getConditionalFields, updateState, loadMetricItemData} = actions.monitor;
            const {loadMetric} = getState().monitor;
            let {loadService, currentCluster} = getState().app;
            currentCluster = currentCluster.clusterName

            if (loadService) {
                let conditionalFields = getConditionalFields(),
                    conditionalFieldsString = conditionalFields.length > 0
                        ? ',' + conditionalFields.join(',')
                        : '',
                    isFlumeInstalled = false, //todo 需要确认是否有flume
                    isATSInstalled = false, //todo 需要确认是否有yarn,
                    isHa = false, //todo 需要确认是否有yarn,
                    flumeHandlerParam = isFlumeInstalled
                        ? 'ServiceComponentInfo/component_name=FLUME_HANDLER|'
                        : '',
                    atsHandlerParam = isATSInstalled
                        ? 'ServiceComponentInfo/component_name=APP_TIMELINE_SERVER|'
                        : '',
                    haComponents = isHa
                        ? 'ServiceComponentInfo/component_name=JOURNALNODE|ServiceComponentInfo/component_n' +
                            'ame=ZKFC|'
                        : '', //todo需要判断
                    realUrl = 'components/?' + flumeHandlerParam + atsHandlerParam + haComponents + 'ServiceComponentInfo/category=MASTER&fields=ServiceComponentInfo/service_name,ho' +
                            'st_components/HostRoles/display_name,host_components/HostRoles/host_name,host_co' +
                            'mponents/HostRoles/state,host_components/HostRoles/maintenance_state,host_compon' +
                            'ents/HostRoles/stale_configs,host_components/HostRoles/ha_state,host_components/' +
                            'HostRoles/desired_admin_state,' + conditionalFieldsString + '&minimal_response=true';

                let res = await API.getServiceMetric(realUrl, currentCluster)

                updateState({
                    loadMetric: true,
                    serviceMetricList: res.data.items || []
                })
                loadMetricItemData()
            }
        },

        /**
         * 获取dashbord指标
         */
        loadMetricItemData() {
            const {getNameNodeComponentData, getFlumeData, getYarnComponentData, getHbaseServiceData} = actions.monitor;
            getFlumeData()
            getNameNodeComponentData()
            getYarnComponentData()
            getHbaseServiceData()
        },
        /**
         * 获取flumeData
         * @param data
         * @param getState
         */
        getFlumeData(data, getState) {
            let {serviceMetricList, flumeData} = getState().monitor;
            const {currentCluster} = getState().app;
            const {updateMetricViewData} = actions.monitor;

            let flumeAgentComponents = serviceMetricList.filter(item => {
                return item.ServiceComponentInfo.component_name === 'FLUME_HANDLER'
            })

            function filterByStatus(dataArr, state) {
                return dataArr.filter(item => {
                    return item.HostRoles.state === state
                })
            }

            // let flumeAgentsAll = flumeAgentComponents[0].host_components
            let flumeAgentsAll = []
            let flumeAgentsLive = filterByStatus(flumeAgentsAll, 'STARTED');
            let flumeAgentsDead = filterByStatus(flumeAgentsAll, 'INSTALLED');
            updateMetricViewData({
                FlumeAgentUpView: {
                    flumeAgentsAll,
                    flumeAgentsLive,
                    flumeAgentsDead
                }
            })
        },
        /**
         * 获取nameNode的运行时间  todo 后面需要梳理
         * @param data
         * @param getState
         */
        getNameNodeComponentData(data, getState) {
            const {updateMetricViewData} = actions.monitor
            const {serviceMetricList} = getState().monitor;

            serviceMetricList.forEach(item => {
                if (item.ServiceComponentInfo.component_name === 'NAMENODE') {
                    let startTime = deepObj(item.host_components[0], 'metrics.runtime.StartTime')
                    let {timeUnit, hiddenInfo, formatted} = uptimeProcessing(startTime)
                    let timeData = formatted && parseFloat(formatted.split(" ")[0]);
                    let contentTime = formatted
                        ? timeData.toFixed(1) + ' ' + timeUnit
                        : ''
                    let cpuWio = deepObj(item.host_components[0], 'metrics.cpu.cpu_wio')
                    let rpcTime = deepObj(item.host_components[0], 'metrics.rpc.client.RpcQueueTime_avg_time')
                    let rpcContent = rpcTime
                        ? rpcTime.toFixed(2) + 'ms'
                        : 0 + 'ms'

                    let dfsInfo = deepObj(item.host_components[0], 'metrics.dfs.FSNamesystem')

                    let chartData = [
                        {
                            value: cpuWio,
                            name: ""
                        }, {
                            value: 100,
                            name: ""
                        }
                    ]
                    let jvmMemoryHeapUsed = deepObj(item.host_components[0], 'metrics.jvm.HeapMemoryUsed')
                    let jvmMemoryHeapMax = deepObj(item.host_components[0], 'metrics.jvm.HeapMemoryMax')
                    let heapChart = [
                        {
                            value: jvmMemoryHeapUsed || 0,
                            name: ""
                        }, {
                            value: jvmMemoryHeapMax || 1,
                            name: ""
                        }
                    ]
                    updateMetricViewData({
                        NameNodeUptimeView: {
                            contentTime,
                            hiddenInfo
                        },
                        NameNodeUptimeView: {
                            contentTime,
                            hiddenInfo
                        },
                        NameNodeCpuPieChartView: {
                            chartData
                        },
                        NameNodeRpcView: {
                            rpcContent
                        },
                        NameNodeHeapPieChartView: {
                            heapChart
                        },
                        NameNodeCapacityPieChartView: {
                            dfsInfo
                        }
                    })
                }
            })

        },
        getYarnComponentData(data, getState) {
            const {updateMetricViewData} = actions.monitor
            const {serviceMetricList} = getState().monitor;

            serviceMetricList.forEach(item => {
                if (item.ServiceComponentInfo.component_name === 'RESOURCEMANAGER') {
                    let resourceManagerComponent = item
                    let startTime = deepObj(item.host_components[0], 'metrics.runtime.StartTime')
                    if (startTime) {
                        let {timeUnit, hiddenInfo, formatted} = uptimeProcessing(startTime)
                        let timeData = parseFloat(formatted.split(" ")[0]);
                        let contentTime = formatted
                            ? timeData.toFixed(1) + ' ' + timeUnit
                            : ''
                        updateMetricViewData({
                            ResourceManagerUptimeView: {
                                contentTime,
                                hiddenInfo
                            }
                        })
                    }

                }
            })

        },
        getHbaseServiceData(data, getState) {
            const {updateMetricViewData} = actions.monitor
            const {serviceMetricList} = getState().monitor;
            serviceMetricList.forEach(item => {
                if (item.ServiceComponentInfo.component_name === 'HBASE_MASTER') {
                    let contentTime,
                        hiddenInfo;
                    let averageLoad = 0;

                    if(item.metrics){
                        item.host_components.forEach(item => {
                            if (item.metrics.hbase && item.metrics.hbase .master.IsActiveMaster === 'true') {
                                averageLoad += item.metrics.hbase.master.AverageLoad
                                let startTime = item.metrics.hbase.master.MasterStartTime
                                let {timeUnit, formatted} = uptimeProcessing(startTime)
                                hiddenInfo = uptimeProcessing(startTime)
                                let timeData = formatted && parseFloat(formatted.split(" ")[0]);
                                contentTime = formatted
                                    ? timeData.toFixed(1) + ' ' + timeUnit
                                    : ''
                            }
                        })
                        updateMetricViewData({
                            HBaseMasterUptimeView: {
                                contentTime,
                                hiddenInfo
                            },
                            HBaseAverageLoadView: {
                                averageLoad
                            }
                        })
                    }
                }
            })
        },

        async getClusterInfo(data, getState) {
            const {updateClusterInfo} = actions.monitor
            const {clusterList, currentCluster, serviceList} = getState().app


            const componentsEntity = [
                'HDFS',
                'YARN',
                'MAPREDUCE2',
                'HIVE',
                'HBASE',
                'SQOOP',
                'ZOOKEEPER',
                'FLUME',
                'AMBARI_METRICS',
                'KAFKA',
                'STORM',
                'LOGSEARCH',
                'SPARK2',
                'AMBARI_INFRA',
                'ELK',
                'HADOOP_KMS',
                'IMPALA',
                'SENTRY'
            ]

            const toolsEntity = [
                'LEAPDATAHUB',
                'LEAPID',
                'LEAPPROCESS',
                'LEAPSQL'
            ]

            let components = serviceList.filter(item => {
                return componentsEntity.indexOf(item.service_name) > -1
            })

            let tools = serviceList.filter(item => {
                return toolsEntity.indexOf(item.service_name) > -1
            })

            const hostRes = await APIService.getHostListInfo(currentCluster.clusterName)

            let clusterInfo = [
                {
                    key: "1",
                    type: "集群名称",
                    content: <div className="flexCenterBetween">{currentCluster.clusterName}</div>
                }, {
                    key: "2",
                    type: "节点（主机信息）",
                    content: (
                        <div className="flexCenterBetween">
                            <span>{hostRes.data.itemTotal}个</span>
                            <Button className="flexCenterBetween">
                                <Link to={'/hosts'}>
                                    <i className="hostManageIcon"/>主机管理
                                </Link>
                            </Button>
                        </div>
                    )
                }, {
                    key: "3",
                    type: "大数据组件",
                    content: (
                        <div className="flexCenterBetween">
                            <ul>
                                {components.map(item => (
                                    <li key={item.service_name}>
                                        <Link to={`/monitor/services/${item.service_name}`}>{item.display_name}</Link>
                                    </li>
                                ))
}
                            </ul>
                            <Button className="flexCenterBetween">
                                <Link to={'/componentManage'}>
                                    <i className="componentManageIcon"/>
                                    <span>组件管理</span>
                                </Link>
                            </Button>
                        </div>
                    )
                }, {
                    key: "4",
                    type: "开发工具",
                    content: (
                        <div className="flexCenterBetween">
                            <ul>
                                {
                                    tools.map(item => (
                                        <li key={item.service_name}>
                                            <Link to={`/monitor/services/${item.service_name}`}>{item.display_name}</Link>
                                        </li>
                                    ))
                                }
                            </ul>
                            <Button className="flexCenterBetween">
                                <Link to={'/devTools'}>
                                    <i className="devToolIcon"/>
                                    <span>开发工具</span>
                                </Link>
                            </Button>
                        </div>
                    )
                }
            ]

            updateClusterInfo({clusterInfoLoading: false, clusterInfo: clusterInfo})
        },
        async restartComponents(data, getState) {
            return await restartComponents(data)
            // if(res.code === 1){
            //     const d = res.data.items
            //     let summaryData
            //     d && d.map(rec => {
            //     if(rec.Hosts.host_name == data.host_name){
            //         summaryData = rec
            //     }
            //     })
            //     const {updateState} = actions.host
            //     updateState({
            //     summaryInfo: summaryData,
            //     taskModalVisible: true
            //     })
            // }
            return res
        }
    }
})
