import React from 'react'
import { Switch, Route, Redirect, Router, connect } from 'mirrorx'
import Layout from './pages/layout'
import Login from './pages/login'
import ForgetPwd from 'pages/forgetPwd'
import Register from 'pages/register'

import Product from 'pages/product'
import ProductCreate from 'pages/productCreate'

import { history } from './utils/'
import Cookies from 'js-cookie'

const PrivateRoute = ({ component: Component, status, ...rest }) => {
	return (
	<Route {...rest} render={props => {
		let cookie = Cookies.get('TRIPNOTE-KEY')
		if(cookie){
			return <Component {...props} />
		}else{
			return <Redirect to={{
				pathname: '/signIn',
				state: { from: props.location }
			}} />
		}
		// switch (status) {
		// 	case 'online':
		// 		return <Component {...props} />
		// 	case 'offline':
		// 		Cookies.remove('LEAP-CLOUD-USERNAME')
		// 		return <Redirect to={{
		// 			pathname: '/signIn',
		// 			state: { from: props.location }
		// 		}} />
		// 	default:
		// 		return null
		// }
	}}/>
)
}
const App = ({ userStatus }) => (
	<Router history={history}>
		<Switch>
			<Route path="/signIn" component={Login} />
			<Route path="/register" component={Register} />
			<Route path="/forgetPwd" component={ForgetPwd} />

			<PrivateRoute path="/product" status={userStatus} component={Product} />
			<PrivateRoute path="/productCreate" status={userStatus} component={ProductCreate} />
			<PrivateRoute path="/productEdit" status={userStatus} component={ProductCreate} />

			<Redirect exact from="/" to="/product" />
		</Switch>
	</Router>
)

const ConnApp = connect(state => {
	return {
		userStatus: state.user.status
	}
})(App)

export default ConnApp