import React, {Component} from 'react'
import {Tabs, Row, Col, Dropdown, Button, Select, Icon, Input, Menu, Carousel} from 'antd'
const Option = Select.Option

import {SortableContainer, SortableElement} from 'react-sortable-hoc'

const TabPane = Tabs.TabPane
import {connect, actions} from 'mirrorx'

const restartContent = (
    <div>sss</div>
)
const filterDropDown = (
    <Menu>
        <Menu.Item>
            <a>修改的熟悉</a>
        </Menu.Item>
        <Menu.Item>
            <a>最终的熟悉</a>
        </Menu.Item>
        <Menu.Item>
            <a>显示有问题的熟悉</a>
        </Menu.Item>
    </Menu>
)

class ConfigurationPage extends Component {
    constructor(props) {
        super(props)
        this.state = {
            filterKey: ''
        }
        this.filterIpt = null
    }

    handleChange = () => {

    }
    emitEmpty = () => {
        this.filterIpt.focus();
        this.setState({filterKey: ''});
    }
    onChangeFilterKey = (e) => {
        this.setState({filterKey: e.target.value});
    }

    render() {
        const {filterKey} = this.state
        return <div className="configurationPage">
            <div className="alertTip">
                <div>
                    <i className="warnIcon"/>
                    <span>重新启动要求：2台主机上的2个组件</span>
                </div>
                <div className="reStartBtn flexVerticalCenter">
                    <Dropdown overlay={restartContent} trigger={['click']}>
                        <a className="ant-dropdown-link" href="#">
                            重新启动 <Icon type="down"/>
                        </a>
                    </Dropdown>
                </div>
            </div>
            <div className="groupPartContent">
                <div className="contentHead groupPartHead">
                    <div className="left flexVerticalCenter">
                        <span>组</span>
                        <Select className="noBorderSelect" defaultValue="Default" style={{width: 120}}
                                onChange={this.handleChange}>
                            <Option value="Default">Default(5)</Option>
                            <Option value="leap">leap(6)</Option>
                            <Option value="datahub">datahub(4)</Option>
                        </Select>
                        <div className="manageConfig flexVerticalCenter">
                            <i/>
                            <span>管理配置组</span>
                        </div>
                    </div>
                    <div className="right flexVerticalCenter">
                        <Input
                            placeholder="过滤"
                            prefix={<Icon type="search" style={{color: 'rgba(0,0,0,.25)'}}/>}
                            suffix={filterKey ? <Icon type="close-circle" onClick={this.emitEmpty}/> : null}
                            value={filterKey}
                            onChange={this.onChangeFilterKey}
                            ref={node => this.filterIpt = node}
                        />
                        <Dropdown overlay={filterDropDown} trigger={['click']}>
                            <a className="ant-dropdown-link flexVerticalCenter ml20" href="#">
                                <Icon type="filter" style={{fontSize: 18, color: '#006cff'}}/>
                                <Icon type="caret-down" style={{fontSize: 12, color: '#82868b'}}/>
                            </a>
                        </Dropdown>
                    </div>
                </div>
                <div className="carouselBox">

                </div>
            </div>
        </div>
    }
}

const ConnConfigurationPage = connect(state => {
    return {}
})(ConfigurationPage)

export default ConnConfigurationPage