import request from 'utils/asyncService'

// http://demo1.leap.com:8080/api/v1/clusters/LEAP/stack_versions?fields=*,repository_versions/*,repository_versions/operating_systems/OperatingSystems/*,repository_versions/operating_systems/repositories/*&_=1527213965135

// 获取组件信息
const getComponentsList = (currentCluster) => request({
  url: '/platform/ForwardTo',
  data: {
    url: `/api/v1/clusters/${currentCluster}/stack_versions?fields=*,repository_versions/*,repository_versions/operating_systems/OperatingSystems/*,repository_versions/operating_systems/repositories/*`,
    clusterName: currentCluster,
    method: 'get'
  }
})

// 卸载
const uninstallService = (serviceName, currentCluster) => request({
  url: '/platform/deleteService',
  method: 'post',
  data: {
    clusterName: currentCluster,
    serviceName: serviceName
  }
})


// 获取组件安装
const getComponentsInstalledList = (currentCluster) => request({
  url: '/platform/ForwardTo',
  data: {
    url: `/api/v1/clusters/${currentCluster}/services?fields=ServiceInfo/state,ServiceInfo/maintenance_state,components/ServiceComponentInfo/component_name&minimal_response=true`,
    clusterName: currentCluster,
    method: 'get'
  }
})

// 获取主机信息
const getHostInfo = (currentCluster)  => request({
  url: '/platform/ForwardTo',
  data: {
    url: `/api/v1/clusters/${currentCluster}/hosts?fields=Hosts/rack_info,Hosts/host_name,Hosts/maintenance_state,Hosts/public_host_name,Hosts/cpu_count,Hosts/ph_cpu_count,alerts_summary,Hosts/host_status,Hosts/last_heartbeat_time,Hosts/ip,host_components/HostRoles/state,host_components/HostRoles/maintenance_state,host_components/HostRoles/stale_configs,host_components/HostRoles/service_name,host_components/HostRoles/display_name,host_components/HostRoles/desired_admin_state,metrics/disk,metrics/load/load_one,Hosts/total_mem,Hosts/os_arch,Hosts/os_type,metrics/cpu/cpu_system,metrics/cpu/cpu_user,metrics/memory/mem_total,metrics/memory/mem_free,stack_versions/HostStackVersions,stack_versions/repository_versions/RepositoryVersions/repository_version,stack_versions/repository_versions/RepositoryVersions/id,stack_versions/repository_versions/RepositoryVersions/display_name&minimal_response=true,host_components/logging&page_size=10&from=0&sortBy=Hosts/host_name.asc`,
    clusterName: currentCluster,
    method: 'get'
  }
})
// 
// 
// PUT
const startAllService = (data) => request({
  url: '/platform/ForwardTo',
  data: {
    url: `/api/v1/clusters/${data.cluster_name}/services?`,
    clusterName: data.cluster_name,
    params: {
      "RequestInfo": {
        "context": `_PARSE_.${data.type.toUpperCase()}.ALL_SERVICES`,  //_PARSE_.STOP.ALL_SERVICES
        "operation_level": {
          "level": "CLUSTER",
          "cluster_name": data.cluster_name
        }
      },
      "Body": {
        "ServiceInfo": {
          "state": data.type == 'start' ? "STARTED" : 'INSTALLED'
        }
      }
    },
    method: 'PUT'
  }
})
export {
  getComponentsList,
  getComponentsInstalledList,
  getHostInfo,
  uninstallService,
  startAllService
}