/**
 * 获取服务的安装信息
 * @returns {*}
 */
const getServicesStatusInfo = (clusterName) => request({
    url: '/platform/ForwardTo',
    method: 'post',
    data: {
        url: `/api/v1/clusters/${clusterName}/components/?ServiceComponentInfo/component_name=FLUME_HANDLER%7CServiceComponentInfo/component_name=APP_TIMELINE_SERVER%7CServiceComponentInfo/category=MASTER&fields=ServiceComponentInfo/service_name,host_components/HostRoles/display_name,host_components/HostRoles/host_name,host_components/HostRoles/state,host_components/HostRoles/maintenance_state,host_components/HostRoles/stale_configs,host_components/HostRoles/ha_state,host_components/HostRoles/desired_admin_state`,
        clusterName: clusterName,
        method: 'get'
    }
})




export {
    getHostListInfo,
    getUserActiveWidgetLayouts,
    getUserServiceLayout,
    getUserDropDownWidgetLayouts,
    loadRacks,
    getHostComponentsMetrics,
    getSummeryLayout,
    postDisplayWidgetNameId,
    getSummaryBaseInfo,
    getSummaryWidgeInfo,
    getSummaryBaseInfoMaster,
    getServicesStatusInfo
}