import React, { Component } from "react";
import { connect, actions, Link } from "mirrorx";
import { Table, Icon, Button, Tag } from "antd";

const columns = [
  {
    title: "名称",
    dataIndex: "property_display_name",
    key: "property_display_name",
  },
  {
    title: "值",
    dataIndex: "property_value",
    key: "property_value"
  }
];

class ServiceAccount extends Component {
  constructor(props) {
    super(props);
    this.state = {
      bbb: 123
    }
    this.currentClusterName = null
  }

  componentWillReceiveProps(props){
    if(props.currentCluster && props.currentCluster.clusterName != this.currentClusterName){
      this.currentClusterName = props.currentCluster.clusterName
      this.componentWillMount()
    }
  }

  componentWillMount() {
    if(this.props.currentCluster){
      actions.security.getServiceAccount()
    }else{
      actions.app.getClusterList().then(rec => {
        if(rec && rec.length > 0){
          actions.security.getServiceAccount()
        }
      })
    }
  }

  refreshData(){
    actions.security.getServiceAccount()
  }

  render() {
    const { status, serviceAccount } = this.props

    return (
      <div>
        <div className="topTitle">服务用户和组</div>
        <Table
          loading={status}
          style={{width: '50%'}}
          bordered={true}
          dataSource={serviceAccount}
          columns={columns}
          pagination={false}
        />
      </div>
    );
  }
}

const ConnServiceAccount = connect(state => {
  return {
    status: state.security.status,
    serviceAccount: state.security.serviceAccount,
    currentCluster: state.app.currentCluster
  }
})(ServiceAccount)

export default ConnServiceAccount;
