import {addLocaleData} from 'react-intl'
import en from 'react-intl/locale-data/en'
import zh from 'react-intl/locale-data/zh'
import antdEn from 'antd/lib/locale-provider/en_US'
import antdCN from 'antd/lib/locale-provider/zh_CN'
import cnMessages from './zh_CN'
import enMessages from './en_US'

addLocaleData([...en, ...zh])

const enUS = {
  messages: {
    ...enMessages,
  },
  antd: antdEn,
  locale: 'en',
  data: en
}
const zhCN = {
  messages: {
    ...cnMessages,
  },
  antd: antdCN,
  locale: 'zh',
  data: zh
}

const getCurrentAppLocale = () => {
  const language = 'zh'
  switch (language) {
    case 'zh':
      return zhCN
    default:
      return enUS
  }
}

export default getCurrentAppLocale