import mirror, {actions} from 'mirrorx'
import {SortableContainer, SortableElement, arrayMove} from 'react-sortable-hoc'
import * as API from "../../api/services"
import {deepObj, mapProperty} from '../../utils/monitor'
import _ from 'lodash'

const slotDefinitions = 5;
const BYTES_IN_MB = 1024 * 1024;
const slotColors = [
    '#0dba3e',
    '#9fee00',
    '#ffff00',
    '#ffc000',
    '#ff0000'
];

let VALUE_NAME_REGEX = /(\w+\s+\w+)?[\w\.\,\:\=\[\]]+/g;
let MATH_EXPRESSION_REGEX = /^[\d\s\+\-\*\/\(\)\.]+$/;
let EXPRESSION_REGEX = /\$\{([\w\s\.\,\+\-\*\/\(\)\:\=\[\]]*)\}/g;

const minimumValue = 0;
const indexByRackId = (hosts) => {
    let rackMap = {};
    hosts.forEach(function (host) {
        let rackId = host.rack;
        if (!rackMap[rackId]) {
            rackMap[rackId] = {
                name: rackId,
                rackId: rackId,
                hosts: [host]
            }
        } else {
            rackMap[rackId].hosts.push(host);
        }
    });
    return rackMap;
};
const toList = (rackMap) => {
    let racks = [];
    let i = 0;
    for (let rackKey in rackMap) {
        if (rackMap.hasOwnProperty(rackKey)) {
            racks.push(
                {
                    name: rackKey,
                    rackId: rackKey,
                    hosts: rackMap[rackKey].hosts,
                    isLoaded: false,
                    index: i++
                }
            );
        }
    }
    return racks;
};

/**
 * 格式化显示的label
 * @param num
 * @param units
 * @returns {*}
 */
const formatLegendLabel = (num, units) => {
    const fraction = num % 1;
    if (num >= 100) {
        num = Math.round(num);
    } else if (num >= 10 && fraction > 0) {
        num = parseFloat(num.toFixed(1));
    } else if (fraction > 0) {
        num = parseFloat(num.toFixed(2));
    }
    if (units === 'ms') {
        return num;
    }
    return num + units;
};

const convertNumber = (number, units) => {
    if (units === 'MB') {
        return Math.round(number * BYTES_IN_MB);
    }
    return number;
}
/**
 * 生成左边显示的Slot
 * @param min
 * @param max
 * @param units
 * @param slotColor
 * @returns {{hasBoundaries: boolean, from: *, to: *, label: string, cssStyle: *}}
 */
const generateSlot = (min, max, units, slotColor) => {
    const fromLabel = formatLegendLabel(min, units);
    const toLabel = formatLegendLabel(max, units);
    const from = convertNumber(min, units);
    const to = convertNumber(max, units);

    return {
        hasBoundaries: true,
        from: from,
        to: to,
        label: fromLabel + " - " + toLabel,
        cssStyle: slotColor
    }
};
/**
 *
 * @param input
 * @returns {Array}
 */
const extractExpressions = (input) => {
    let pattern = EXPRESSION_REGEX,
        expressions = [],
        match;

    if (!input) return expressions;

    while (match = pattern.exec(input.value)) {
        expressions.push(match[1])
    }
    return expressions;
};

const computeExpression = (expressions, metrics) => {
    let hostToValueMap = {};
    let hostNames = mapProperty(metrics, 'hostName');
    let metricsMap = {};

    metrics.forEach(function (_metric) {
        metricsMap[_metric.name + "_" + _metric.hostName] = _metric;
    });

    hostNames.forEach(function (_hostName) {
        expressions.forEach(function (_expression) {
            let validExpression = true;
            let beforeCompute = _expression.replace(VALUE_NAME_REGEX, function (match) {
                let _metric;
                if (_.isNaN(match - 0)) {
                    _metric = metricsMap[match + "_" + _hostName];
                    if (_metric) {
                        return _metric.data;
                    } else {
                        validExpression = false;
                    }
                } else {
                    return match;
                }
            });
            if (validExpression && MATH_EXPRESSION_REGEX.test(beforeCompute)) {
                let value = Number(window.eval(beforeCompute)).toString();
                if (value === "NaN") {
                    value = 0
                }
                hostToValueMap[_hostName] = value;
            } else {
            }
        });
    });
    return hostToValueMap;
}

const calculateValues = (exp, metrics) => {
    return computeExpression(extractExpressions(exp), metrics)
}

const calculateSlot = (hostToValueMap, hostName, slotDefinitions) => {
    let slot = -1;
    let slotDefs = slotDefinitions;

    if (hostName in hostToValueMap) {
        let value = hostToValueMap[hostName];
        if (isNaN(value)) {
            slot = slotDefs.length - 2;
        } else {
            for (let slotIndex = 0; slotIndex < slotDefs.length - 2; slotIndex++) {
                let slotDef = slotDefs[slotIndex];
                if (value >= slotDef.from && value <= slotDef.to) {
                    slot = slotIndex;
                }
            }
            if (slot < 0) {
                slot = slotDefs.length - 3;
            }
        }
    } else {
        slot = slotDefs.length - 1;
    }
    return slot;
}
const hostToSlotMap = (exp, metrics, hostNames, slotDefinitions) => {
    let hostToValueMap = calculateValues(exp, metrics);
    let hostToSlotMap = {};
    if (hostToValueMap && hostNames) {
        hostNames.forEach(function (hostName) {
            let slot = calculateSlot(hostToValueMap, hostName, slotDefinitions);
            if (slot > -1) {
                hostToSlotMap[hostName] = slot
            }
        }, this);
    }
    return hostToSlotMap;
}


mirror.model({
    name: 'heatMap',
    initialState: {
        slotDefinitions: [],
        isLoaded: true,//是否加载完
        inputMaximum: '',//最大值
        activeWidgetInfo: {},
        widgetLayoutInfo: {},
        hostListInfo: [],
        loadRack: false,
        loadMetrics: false,
        metrics: [],
        hostNames: [],
        serviceName: '',
        rackList: [],//右边的rack
        dropDownLayoutInfo: {
            loaded: false,
            list: []
        }
    },
    reducers: {
        updateState(state, data) {
            return {
                ...state, ...data
            }
        },
        updateActiveWidgetInfo(state, data) {
            return {
                ...state,
                activeWidgetInfo: {
                    ...state.activeWidgetInfo,
                    ...data
                }
            }

        },
        updateDropDownLayoutInfo(state, data) {
            return {
                ...state,
                dropDownLayoutInfo: {
                    ...state.dropDownLayoutInfo,
                    ...data
                }
            }
        }
    },
    effects: {
        /**
         * 更新最大值
         * @param data
         * @param getState
         */
        updateIptMax(data, getState) {
            const {updateState, calSlotDefinitions} = actions.heatMap;
            updateState({
                inputMaximum: data
            })
            calSlotDefinitions()

        },
        /**
         * 获取=下拉菜单的数据
         * @param name
         * @param getState
         * @returns {Promise<void>}
         */
        async getUserDropDownWidgetLayouts(name, getState) {
            const {updateState, updateDropDownLayoutInfo} = actions.heatMap;
            const {serviceName} = getState().heatMap;
            const {currentCluster} = getState().app;
            updateDropDownLayoutInfo({
                loaded: false,
                list: []
            })
            if (name) {
                updateState({
                    serviceName: name
                })
            } else {
                name = serviceName
            }
            let res = await API.getUserDropDownWidgetLayouts(name, currentCluster.clusterName);
            res = res.data
            updateDropDownLayoutInfo({
                loaded: true,
                list: res.items
            })
        },
        
        /**
         * 改变当前显示的组件
         * @param id
         * @param getState
         * @returns {Promise<void>}
         */
        async changeDisplayWidgetName(id, getState) {
            const {updateState} = actions.heatMap;
            const {activeWidgetInfo, widgetLayoutInfo,serviceName} = getState().heatMap
            const {currentCluster} = getState().app
            let widgetLayoutInfoData = {
                "WidgetLayoutInfo": {
                    display_name: activeWidgetInfo.display_name,
                    id: widgetLayoutInfo.id,
                    layout_name: activeWidgetInfo.layout_name,
                    section_name: widgetLayoutInfo.section_name,
                    scope: activeWidgetInfo.scope,
                    widgets: [{
                        id: id
                    }]
                }
            };
            await API.postDisplayWidgetNameId(widgetLayoutInfo.id, JSON.stringify(widgetLayoutInfoData), currentCluster.clusterName);
            actions.heatMap.getActiveWidget(serviceName);
            actions.heatMap.getRackList()
            actions.heatMap.getUserDropDownWidgetLayouts()

        },
        /**
         * 获取当前活跃的组件
         * @param data
         * @param getState
         * @returns {Promise<void>}
         */
        async getActiveWidget(data, getState) {
            const {updateState, updateActiveWidgetInfo, calSlotDefinitions, getHostComponentsMetrics} = actions.heatMap;
            const {currentCluster} = getState().app

            let res = await API.getUserActiveWidgetLayouts(data, currentCluster.clusterName);
            res = res.data
            let widgetLayoutInfo = res.items[0].WidgetLayoutInfo;
            let activeWidgetInfo = widgetLayoutInfo.widgets[0].WidgetInfo;
            let inputMaximum = JSON.parse(activeWidgetInfo.properties).max_limit;
            let displayUnit = JSON.parse(activeWidgetInfo.properties).display_unit;
            updateState({
                widgetLayoutInfo: widgetLayoutInfo,
                activeWidgetInfo: activeWidgetInfo,
                inputMaximum: inputMaximum
            })
            updateActiveWidgetInfo({
                inputMaximum,
                displayUnit
            })
            calSlotDefinitions();
            await getHostComponentsMetrics(data)

        },
        getHostList(data, getState) {

        },
        /**
         * 获取RackList
         * @param data
         * @param getState
         * @returns {Promise<void>}
         */
        async getRackList(data, getState) {
            const {updateState} = actions.heatMap;
            const {hostNames} = getState().heatMap;
            const {currentCluster} = getState().app
            
            let res = await API.loadRacks(currentCluster.clusterName);
            res = res.data
            let hosts = [],
                rackMap = {},
                rackList = [];
            res.items.forEach(function (item) {
                hostNames.push(item.Hosts.host_name);
                hosts.push({
                    hostName: item.Hosts.host_name,
                    publicHostName: item.Hosts.public_host_name,
                    osType: item.Hosts.os_type,
                    ip: item.Hosts.ip,
                    rack: item.Hosts.rack_info,
                    diskTotal: item.metrics ? item.metrics.disk.disk_total : 0,
                    diskFree: item.metrics ? item.metrics.disk.disk_free : 0,
                    cpuSystem: item.metrics ? item.metrics.cpu.cpu_system : 0,
                    cpuUser: item.metrics ? item.metrics.cpu.cpu_user : 0,
                    memTotal: item.metrics ? item.metrics.memory.mem_total : 0,
                    memFree: item.metrics ? item.metrics.memory.mem_free : 0,
                    hostComponents: mapProperty(item.host_components, 'HostRoles.component_name')
                });
            });
            rackMap = indexByRackId(hosts);
            rackList = toList(rackMap);
            updateState({
                loadRack: true,
                rackList,
                hostNames
            })
        }
        ,
        /**
         * 计算Slot颜色
         * @param data
         * @param getState
         */
        calSlotDefinitions(data, getState) {
            const {updateState} = actions.heatMap;
            let inputMaximum = getState().heatMap.inputMaximum;
            let units = getState().heatMap.activeWidgetInfo.displayUnit;
            let max = inputMaximum ? parseFloat(inputMaximum) : 0;
            let delta = (max - minimumValue) / slotDefinitions;
            let defs = [];
            let slotColorIndex = 0;
            let start, end, slotColor;
            for (let c = 0; c < slotDefinitions - 1; c++) {
                slotColor = slotColors[slotColorIndex++];
                start = (c * delta);
                end = ((c + 1) * delta);
                defs.push(generateSlot(start, end, units, slotColor));
            }
            slotColor = slotColors[slotColorIndex++];
            start = ((slotDefinitions - 1) * delta);
            defs.push(generateSlot(start, max, units, slotColor));

            defs.push({
                invalidData: true,
                index: defs.length,
                label: '无效的数据',
                cssStyle: '#e87fff'
            });
            defs.push({
                notApplicable: true,
                index: defs.length,
                label: '不可用',
                cssStyle: '#c8c8c8'
            });
            updateState({
                slotDefinitions: defs
            });
        }
        ,
        /**
         * 获取Metrics
         * @param data
         * @param getState
         * @returns {Promise<void>}
         */
        async getHostComponentsMetrics(data, getState) {
            const {updateState} = actions.heatMap;
            const {activeWidgetInfo, serviceName} = getState().heatMap;
            const {currentCluster} = getState().app;
            const metricsArr = JSON.parse(activeWidgetInfo.metrics);
            let component_name;
            let name = '';
            if (data) {
                updateState({
                    serviceName: data
                });
                name = data;
            } else {
                name = serviceName
            }

            let path = [];
            metricsArr.forEach(item => {
                component_name=item&&item.component_name;
                path.push(`host_components/${item&&item.metric_path}`)
            });
            const urlParams = `fields=${path.join(',')}&format=null_padding`;

            let res = await API.getHostComponentsMetrics({
                serviceName: name,
                component_name: component_name,
                currentCluster: currentCluster.clusterName
            }, urlParams);

            res = res.data
            let metrics = [];
            let widgetMetrics = JSON.parse(activeWidgetInfo.metrics);
            res.host_components.forEach((item, idx) => {
                widgetMetrics.forEach((_metric, index) => {
                    if (_metric && deepObj(item, _metric.metric_path.replace(/\//g, '.'))) {
                        let metric = _.extend({}, _metric);
                        metric.data = deepObj(item, _metric.metric_path.replace(/\//g, '.'));
                        metric.hostName = item.HostRoles.host_name;
                        metrics.push(metric);
                    }
                })
            });
            updateState({
                loadMetrics: true,
                metrics
            });
            calculateValues(JSON.parse(activeWidgetInfo.values)[0], metrics)
        },
        /**
         * 计算颜色
         * @param data
         * @param getState
         * @returns {*}
         */
        hostTemperatureStyle(data, getState) {
            let {hostName} = data;
            let {metrics, hostNames, slotDefinitions, activeWidgetInfo} = getState().heatMap;
            let exp = JSON.parse(activeWidgetInfo.values)[0];
            let h2sMap = hostToSlotMap(exp, metrics, hostNames, slotDefinitions);
            if (h2sMap) {
                if (hostName) {
                    let slot = h2sMap[hostName];
                    if (slot > -1) {
                        let slotDefs = slotDefinitions;
                        if (slotDefs && slotDefs.length > slot) {
                            return slotDefs[slot]['cssStyle'];
                        }
                    }
                }
            }
            return '';
        }
    }
})
