import React, { Component } from 'react'
import {Table, Select, Input, Spin, Tooltip, Button} from 'antd'
import { connect, actions} from 'mirrorx'
import moment from 'moment'
import './index.less'
const Option = Select.Option
class Alerts extends Component {
  constructor(props){
    super(props)
    this.state = {
      filterValues: {}
    }
    this.stateToCN = {
      CURRENT: '当前的',
      
    }
  }
  async componentDidMount(){
    await actions.host.getVersions()
  }

  filterValueChange = (key, value) => {
    const val = typeof value == 'object' ? value.target.value : value
    const {filterValues} = this.state
    if(val !== 'all'){
      filterValues[key] = val
    }else{
      delete filterValues[key]
    }
    actions.host.filterAlertsList(filterValues)
    this.setState({
      filterValues
    })
  }

  columns = () => {
    // const {alertFilter} = this.props
    const {filterValues} = this.state
    return [{
      title: <span className="tableHeaderOperate">服务: 
        <Select allowClear value={filterValues['service_name']|| 'all'} onChange={this.filterValueChange.bind(this, 'service_name')}>
          <Option value='all'>全部</Option>
          {/* {
            alertFilter && alertFilter.server.map(rec => {
              return <Option value={rec} key={rec}>{rec}</Option>
            })
          } */}
        </Select></span>,
      dataIndex: 'HostRoles.service_name',
    },{
      title: <span className="tableHeaderOperate">组件: 
        <Select allowClear value={filterValues['display_name']|| 'all'} onChange={this.filterValueChange.bind(this, 'display_name')}>
          <Option value='all'>全部</Option>
          {/* {
            alertFilter && alertFilter.server.map(rec => {
              return <Option value={rec} key={rec}>{rec}</Option>
            })
          } */}
        </Select></span>,
      dataIndex: 'HostRoles.display_name',
    }, {
      title: <span className="tableHeaderOperate">扩展: 
        <Select allowClear value={filterValues['state']|| 'all'} onChange={this.filterValueChange.bind(this, 'state')}>
          <Option value='all'>全部</Option>
          {/* {
            alertFilter && alertFilter.server.map(rec => {
              return <Option value={rec} key={rec}>{rec}</Option>
            })
          } */}
        </Select></span>,
      dataIndex: 'logging',
      render: (record) => {
        return <div>
          <a href="#" style={{marginRight: 5}}>{record.logs[0].name}</a>
          <a href="#" title={record.logFileTailURL}>在搜索日志中打开</a>
        </div>
      }
    }]
  }

  render() {
    const {host_components} = this.props
    let logsList = []
    host_components && host_components.map(rec => {
      if(rec.logging)
      logsList.push(rec)
    })
    // const versionsList = versions.data
    if(!logsList){
      return <Spin style={{width: '100%', height: '100%'}}/>
    }
    return (
      <div className="hostAlerts">
        <Table dataSource={logsList} columns={this.columns()} rowKey={record => record.href}/>
      </div>
    )
  }
}

export default connect(state => ({
  host_components: state.host.summaryInfo && state.host.summaryInfo.host_components
}))(Alerts)
