import React, {Component} from 'react'
import styled from 'styled-components'

let echarts = require('echarts/lib/echarts')
require('echarts/lib/chart/pie')
require('echarts/lib/component/title')

export default class ReactPie extends Component {

    constructor(props) {
        super(props)
        this.setPieOption = this.setPieOption.bind(this)
        this.initPie = this.initPie.bind(this)
    }

    initPie() {
        const {data,color} = this.props
        let myChart = echarts.init(this.pieReact)
        let options = this.setPieOption(data,color)
        myChart.setOption(options)
    }

    componentDidMount() {
        this.initPie()
    }

    componentDidUpdate() {
        this.initPie()
    }

    setPieOption(data,color) {

        return {
            title: {
                text: data.length>0?Math.ceil((data[0].value / data[1].value)*100)+'%':'N/A',
                textStyle: {
                    color: '#16bf00',
                },
                x: 'center',
                y: 'center'
            },
            series: [
                {
                    name: '使用情况',
                    type: 'pie',
                    radius: ['75%', '90%'],
                    avoidLabelOverlap: true,
                    data: data,
                    label: {
                        normal: {
                            show: false,
                            position: 'center',
                            textStyle: {
                                fontSize: '18'
                            },
                            emphasis: {
                                show: true,
                                textStyle: {
                                    fontSize: '30',
                                    fontWeight: 'bold'
                                }
                            },
                        },

                    },
                    labelLine: {
                        normal: {
                            show: false
                        }
                    }
                }
            ],
            color: [color, '#e5e5e5']

        }
    }

    render() {
        return (
            <div  className="chartReact">
                <div ref={(c) => this.pieReact = c} style={{width: "100px", height: "100px"}}/>
            </div>
        )
    }
}