import request from 'utils/asyncService'

// http://demo1.leap.com:8080/api/v1/clusters/LEAP/stack_versions?fields=*,repository_versions/*,repository_versions/operating_systems/OperatingSystems/*,repository_versions/operating_systems/repositories/*&_=1527213965135

// 获取告警列表
const getAlertsList = (data) => request({
  url: '/platform/ForwardTo',
  data: {
    url: `/api/v1/clusters/${data.clusterName}/alerts?fields=*`,
    // url: `/api/v1/clusters/${data.clusterName}/alert_definitions?fields=AlertDefinition/component_name,AlertDefinition/description,AlertDefinition/enabled,AlertDefinition/repeat_tolerance,AlertDefinition/repeat_tolerance_enabled,AlertDefinition/id,AlertDefinition/ignore_host,AlertDefinition/interval,AlertDefinition/label,AlertDefinition/name,AlertDefinition/scope,AlertDefinition/service_name,AlertDefinition/source,AlertDefinition/help_url`,
    clusterName: data.clusterName,
    method: 'get'
  }
})

// 启动&停止
const changeAlertStatus = (data) => request({
  url: '/platform/ForwardTo',
  data: {
    url: `/api/v1/clusters/${data.clusterName}/alert_definitions/${data.id}`,
    clusterName: data.clusterName,
    method: 'put',
    params: JSON.stringify({"AlertDefinition/enabled": data.status})
  }
})

export {
  getAlertsList,
  changeAlertStatus
}