import request from 'utils/asyncService'

// http://demo1.leap.com:8080/api/v1/clusters/LEAP/stack_versions?fields=*,repository_versions/*,repository_versions/operating_systems/OperatingSystems/*,repository_versions/operating_systems/repositories/*&_=1527213965135

// 获取service账号
const getServiceAccount = (data) => request({
  // return request({
  //   url: '/api/v1/stacks/${data.clusterName}/versions/3.4.4/services?StackServices/service_name.in(HDFS,YARN,MAPREDUCE2,HIVE,HBASE,SQOOP,ZOOKEEPER,FLUME,AMBARI_METRICS,KAFKA,LOGSEARCH,SPARK2,AMBARI_INFRA,ELK,HADOOP_KMS,IMPALA,LEAPDATAHUB,LEAPID,LEAPPROCESS,LEAPSQL,SENTRY)&fields=configurations/StackConfigurations/*',
  //   data
  // })

  url: '/platform/ForwardTo',
  data: {
    url: `/api/v1/stacks/LEAP/versions/3.4.4/services?StackServices/service_name.in(${data.services})&fields=configurations/*,configurations/dependencies/*,StackServices/config_types/*`,
    clusterName: data.clusterName,
    method: 'get',
  }
})

// 获取许可证详情
const getLicenseDetail = (data) => request({
  url: '/platform/ForwardTo',
  data: {
    url: '/api/v1/license/detail',
    clusterName: data.clusterName,
    method: 'get',
  }
})

// 获取许可证key
const getLicenseKey = (data) => request({
  url: '/platform/ForwardTo',
  data: {
    url: '/api/v1/license/serverkey',
    clusterName: data.clusterName,
    method: 'get',
  }
})

export {
  getServiceAccount,
  getLicenseDetail,
  getLicenseKey  
}