const data = [{
    value: 1,
    name: "使用"
}, {
    value: 2,
    name: "总额"
}]

const VerticalCenter = ({children}) => <div className="verticalCenter">{children}</div>

//todo 暂时
const calColor = (range, percent) => {
    if (percent < range[0]) {
        return '#16bf00'
    } else if (percent > range[1]) {
        return '#f83b3b'
    } else {
        return '#fe8f04'
    }
}
//todo 暂时
const calPercent = (data) => {
    if (data.length > 0) {
        return Math.floor((data[0].value / data[1].value)* 100)
    }
}

class TextView extends Component {
    render() {
        const {
            children,
            value,
            data,
            threshold
        } = this.props
        return <div className="widgetWrap">
            {children}
            {data?<div className="contentText">{data}</div>:<div className="naText">N/A</div>}

        </div>
    }
}

class PieChartView extends Component {
    render() {
        const {
            children,
            value,
            data,
            threshold
        } = this.props
        const range = threshold[value]
        const percent = calPercent(data)
        const color = calColor(range, percent)
        return <div className="widgetWrap">
            {children}
            {data.length>0?<ReactPie color={color} data={data}/>:<div className="naText">N/A</div>}

        </div>
    }
}

/**
 * namenode堆
 */
export const NameNodeHeapPieChartView = ({value, threshold, sourceData={} }) => {

    let {heapChart=[]}=sourceData
    return <PieChartView data={heapChart} value={value} threshold={threshold}>
        <div className="itemName">
            <FormattedMessage id='dashboard.widgets.NameNodeHeap'/>
        </div>
        <div className="opcWrap fs14">
            <VerticalCenter>
                <div>196.8 MB of 1004.0 MB</div>
            </VerticalCenter>
        </div>
    </PieChartView>
}
/**
 * hdfs磁盘使用情况
 */
export const NameNodeCapacityPieChartView = ({ value, threshold, sourceData = {} }) => {
    let { dfsInfo } = sourceData

    let bytesToSize = function (bytes) {
        if (bytes === 0) return '0 B';
        var k = 1000, // or 1024
            sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
            i = Math.floor(Math.log(bytes) / Math.log(k));
        return (bytes / Math.pow(k, i)).toPrecision(3) + sizes[i];
    }

    if(!dfsInfo){
        return ''
    }

    const data = [{
        value: dfsInfo.CapacityUsed + dfsInfo.CapacityNonDFSUsed,
        name: "使用"
    }, {
        value: dfsInfo.CapacityTotal,
        name: "总额"
    }]

    return <PieChartView data={data} value={value} threshold={threshold}>
        <div className="itemName">
            <FormattedMessage id='dashboard.widgets.HDFSDiskUsage'/>
        </div>
        <div className="opcWrap">
            <FormattedMessage id='dashboard.widgets.HDFSDiskUsage.DFSused'/>
            <div>{bytesToSize(dfsInfo.CapacityUsed)}()</div>
            <FormattedMessage id='dashboard.widgets.HDFSDiskUsage.nonDFSused'/>
            <div>{bytesToSize(dfsInfo.CapacityNonDFSUsed)}</div>
            <FormattedMessage id='dashboard.widgets.HDFSDiskUsage.remaining'/>
            <div>{bytesToSize(dfsInfo.CapacityRemaining)}</div>
        </div>

    </PieChartView>
}
