/**
 * 活动的flume
 */
export const FlumeAgentUpView = ({value, threshold, sourceData}) => {
    let data = ''
    let liveLen = 0, deadLen = 0, allLen = 0

    if (sourceData) {
        liveLen = sourceData.flumeAgentsLive.length
        allLen = sourceData.flumeAgentsAll.length
        data = liveLen + '/' + allLen
    }

    return <TextView data={data} value={value} threshold={threshold}>
        <div className="itemName">
            <FormattedMessage id='dashboard.widgets.FlumeAgentUp'/>
        </div>
        <div className="opcWrap fs14">
            <VerticalCenter>
                <div>{liveLen} 活跃</div>
                <div>{deadLen} 离线</div>
            </VerticalCenter>
        </div>
    </TextView>
}
