// 格式化数据 1: 组件对象   2: 组件字符串  0: 主机列表字符
function formatPostData (data, type = 0) {
  let components = []
  const hostsStr = data.map(rec => rec.Hosts.host_name).join(',')
  if(type == 1){
    let allComponents = []
    data && data.map(rec => {
      const host_components = rec.host_components
      allComponents = allComponents.concat(host_components)
    })
    allComponents && allComponents.map(rec => {
      const record = rec.HostRoles
      const curData = {
        service_name: record.service_name,
        component_name: record.component_name,
        hosts: record.host_name
      }
      let isHas = false
      for(let idx = 0, len = components.length; idx < len; idx++){
        const item = components[idx]
        if(
          item.service_name == curData.service_name && 
          item.component_name == curData.component_name){
            if(item.hosts.indexOf(curData.hosts) < 0){
              components[idx].hosts += `,${curData.hosts}`
            }
            isHas = true
            break
        }else{
          isHas = false
        }
      }
      if(!components.length || !isHas){
        components.push(curData)
      }
    })
  }else if (type == 2){
    data && data.map(rec => {
      let curStr = []
      const host_components = rec.host_components
      host_components && host_components.map(item => {
        curStr.push(item.HostRoles.component_name)
      })
      components.push(`(HostRoles/component_name.in(${curStr.join(',')})&HostRoles/host_name=${rec.Hosts.host_name})`)
    })
    components = components.join('|')
  }else{
    components = hostsStr
  }
  return {data: components, hostsStr }
}

export default formatPostData