import React, { Component } from 'react'
import {Popover, Button, Icon, Modal, Input, message} from 'antd'
import {actions} from 'mirrorx'
import OperateMenu from './operateMenu'
import formatPostData from './formatdata'

message.config({top: 200})

export default class Operate extends Component {
  constructor(props){
    super(props)
    this.state = {
      newRack: '/'
    }
  }
  menuChange = (e) => {
    const operateType = e.keyPath
    const text = e.domEvent.target.dataset.text || e.domEvent.target.getAttribute('data-text')
    const { hostLists, selectedRowKeys, chooseHost } = this.props
    let curHost = []
    if (operateType[2] == 'choose' && selectedRowKeys && selectedRowKeys.length > 0) {
      hostLists.map(host => {
        if (selectedRowKeys.includes(host.Hosts.host_name)) {
          curHost.push(host)
        }
      })
    } else if(operateType[2] == 'filter') {
      curHost = chooseHost
    } else {
      curHost = hostLists
    }
    let setState = {
      operateText: text,
      operateData: curHost,
      operateType: operateType
    }
    this.setState(setState)
  }

  // 确认执行操作
  modalSureHandle = () => {
    const { operateText, operateData, operateType } = this.state
    const type = operateType[0]
    const category = operateType[1]
    if(type.indexOf('start') == 0 || type.indexOf('stop') >= 0 || type == 'reinstall'){
      let formatdata
      let query
      if(category == 'host' && type != 'reinstall'){
        formatdata = formatPostData(operateData, 2)
        query = formatdata
      }else if(type == 'reinstall'){
        formatdata = formatPostData(operateData, 0)
        // query = `HostRoles/component_name=DATANODE&HostRoles/host_name.in(${formatdata.hostsStr})&HostRoles/maintenance_state=${operateData["0"].Hosts.maintenance_state}"`
        if(type == 'reinstall'){
          query = `HostRoles/host_name.in(${formatdata.hostsStr})&HostRoles/state=INSTALL_FAILED`
        }
      }
      actions.host.postOperate({
        state: type.indexOf('start') >= 0 ? 'STARTED' : 'INSTALLED',
        clusterName: operateData[0].Hosts.cluster_name,
        context: operateText,
        level: category == 'host' ? 'CLUSTER' : 'SERVICE',
        query
      }).then(rec => {
        if(rec.code == 1){
          this.setState({
            operateText: null
          })
        }
      })
    }
    if(type == 'restartAll'){
      const formatdata = formatPostData(operateData, 1)
      actions.host.restartAllComponents({
        clusterName: operateData[0].Hosts.cluster_name,
        filters: formatdata.data
      }).then(rec => {
        if(rec.code == 1){
          this.setState({
            operateText: null
          })
        }
      })
    }
    if(type == 'open' || type == 'close'){
      const formatdata = formatPostData(operateData, 0)
      let data = ''
      let state = type == 'open' ? 'ON' : 'OFF'
      operateData.map((rec,idx) => {
        if(rec.Hosts.maintenance_state == state){
          data += `${idx != 0 ? ',' : ''}${rec.Hosts.host_name}`
        }
      })
      if(data == formatdata.data){
        message.error(type == 'open' ? '所选主机已处于维护模式' : '所选主机已关闭维护模式')
        this.setState({
          operateText: null
        })
        return
      }
      actions.host.openOrCloseMaintenance({
        clusterName: operateData[0].Hosts.cluster_name,
        context: operateText,
        hostsStr: formatdata.data,
        isOpen: type == 'open' ? 'ON' : 'OFF'
      }).then(rec => {
        if(rec.code == 1){
          this.setState({
            operateText: null
          })
        }
      })
    }
    if(type == 'setRack'){
      const {operateData, operateText, newRack} = this.state
      if(!newRack){
        return
      }
      const formatdata = formatPostData(operateData, 0)
      actions.host.setHostRack({
        cluster_name: operateData[0].Hosts.cluster_name,
        context: operateText,
        hosts: formatdata.data,
        rack: newRack
      }).then(rec => {
        if(rec.code == 1){
          this.setState({
            operateText: null
          })
        }
      })
    }
  }

  // 关闭modal
  closeOerateModal = () => {
    this.setState({ operateText: null })
  }
  rackChange = (e) => {
    let newRack = e.target.value
    newRack = newRack.indexOf('/') == 0 ? newRack : '/' + newRack
    this.setState({ newRack })
  }
  render() {
    const {hostLists, selectedRowKeys, chooseHost} = this.props
    const {operateText, newRack} = this.state
    const theOperateHost = chooseHost || hostLists
    return (
      <div>
        <Popover overlayClassName="hostOperate"
        placement="bottomLeft"
        content={<OperateMenu onChange={this.menuChange} all={hostLists && hostLists.length} choose={selectedRowKeys.length || 0} />}
        trigger="click">
        <Button style={{ marginBottom: '10px' }}>操作 <Icon type="caret-down" /></Button>
      </Popover>
      <Modal
        title={operateText == '设置Rack' ? operateText : '确认批量操作'}
        className={operateText == "设置Rack" ? "setRackModal" : "hostOperateModal"}
        visible={!!operateText}
        onCancel={this.closeOerateModal}
        maskClosable={false}
        footer={
          <Button type="primary" loading={!operateText} onClick={this.modalSureHandle}>确定</Button>
        }
      >
      {
        operateText && operateText != '设置Rack' ?
        <div>
          确认要在下面{theOperateHost && theOperateHost.length}个主机上<b>{operateText}</b>吗?
          <ul>
            {
              theOperateHost && theOperateHost.map(rec => {
                return <li key={rec.href}>{rec.Hosts.host_name}</li>
              })
            }
          </ul>
        </div> :
        <div>
          <label>Rack:</label> 
          <Input ref={(el) => this.rackIpt = el} value={newRack} onChange={this.rackChange}/>
        </div>
      }
      </Modal>
      </div>
    )
  }
}
