import React, { Component } from 'react'
import {Select,Table, Checkbox, Button} from 'antd'
import {actions, connect} from 'mirrorx'
import _ from 'lodash'

import './index.less'
class ChooseHost extends Component {
  constructor(props){
    super(props)
    this.state = {}
    this.needntSelect = ['Grafana', 'WebHCat Server']
  }
  componentWillMount(){
    const master = this.props.value
    if(master){
      this.setState({master})
      if(!this.props.installedMaster){
        return
      }
    }
    let {masterList, hostList, hostListInstalled, installedMaster} = this.props

    hostList = hostListInstalled || hostList

    let hostIdx = 0
    let newHost = {}
    this.hostMaxmemory = {}

    hostList.map(rec => {
      this.hostMaxmemory[rec.vmHost] = 0
      newHost[rec.vmHost] = rec
    })
    
    masterList = masterList && masterList.map((record, index) => {
      newHost = _.cloneDeep(newHost)
      record.choose = {}
      if (installedMaster && installedMaster[record.name]) {
        installedMaster[record.name].map(host => {
          let choose = {...newHost[host]}
          choose.isInstall = true
          record.choose[host] = choose
        })
      }else{
        let allHosts = _.cloneDeep(hostList)
        if(typeof +record.cardinality == 'number' && !isNaN(+record.cardinality) || record.cardinality == 'ALL'){
          let count = record.cardinality == 'ALL' ? hostList.length : +record.cardinality
          if(+record.cardinality == 1){
            const _finally = this.isOverflow(allHosts, record, hostIdx)
            record = _finally.record
            hostIdx = _finally.hostIdx
            allHosts = _finally.allHosts
          }else{
            '0'.repeat(count).split('').map((rec, idx) => {
              const _finally = this.isOverflow(allHosts, record, hostIdx)
              record = _finally.record
              hostIdx = _finally.hostIdx
              allHosts = _finally.allHosts
            })
          }
        }else{
          const _finally = this.isOverflow(allHosts, record, hostIdx)
          record = _finally.record
          hostIdx = _finally.hostIdx
          allHosts = _finally.allHosts
        }
        hostIdx ++
        let recordHosts = {}
        allHosts.map(rec => {
          recordHosts[rec.vmHost] = rec
        })
        record.hosts = recordHosts
      }
      return record
    })
    masterList = this.mustTogetherComponent(masterList)
    this.setState({
      master: masterList,
      hosts : hostList
    })
  }
  isOverflow = (allHosts, record, hostIdx) => {
    let host = allHosts[hostIdx % 4]
    if(this.hostMaxmemory[host.vmHost] + record.minMemory >= 1024 * host.totalMem){
      hostIdx ++
      host = allHosts.splice(hostIdx % 4, 1)[0]
    }else{
      allHosts.splice(hostIdx % 4, 1)
    }
    record.choose[host.vmHost] = host
    this.hostMaxmemory[host.vmHost] += record.minMemory
    return {record, hostIdx, allHosts}
  }
  // 新增配置行
  addRecord = (item, idx, e) => {
    let {master} = this.state
    let count = Object.keys(item.choose).length
    let key = Object.keys(item.hosts)[0]
    const host = item.hosts[key]
    delete master[idx].hosts[key]
    host.isnew = true
    master[idx].choose[host.vmHost] = host
    this.setState({master})
  }
  // 删除配置行
  subRecord = (item, rec, idx) => {
    let {master} = this.state
    const host = item.choose[rec]
    delete master[idx].choose[rec]
    master[idx].hosts[host.vmHost] = host
    this.setState({master})
  }
  // 更改配置
  selectChange = (record, masterIdx, val) => {
    let {master} = this.state
    // master = _.cloneDeep(master)
    const curChoose  = master[masterIdx].hosts[val]
    const lastChoose = master[masterIdx].choose[record]
    const choose     = master[masterIdx].choose
    const hosts      = master[masterIdx].hosts
    curChoose.isnew = true
    master[masterIdx].choose = this.setObjectValue(choose, val, curChoose, record)
    master[masterIdx].hosts  = this.setObjectValue(hosts, record, lastChoose, val)
    master = this.mustTogetherComponent(master)
    this.setState({master})
  }
  // 修改数据对象(类似于数组, 不改位置, 只改变对应位置上的键值)
  setObjectValue = (obj, key, value, newKey) => {
    let keys = []
    let values = []
    let newObj = {}
    for(let attr in obj){
      keys.push(attr)
      values.push(obj[attr])
    }
    const index = keys.indexOf(newKey)
    keys[index] = key
    values[index] = value
    values.map((rec, idx) => {
      newObj[keys[idx]] = rec
    })
    return newObj
  }
  // 根据用户配置  重新组装右侧展示数据
  getComponentsByHost = (master, hosts) => {
    let newHosts = {}
    hosts.map(rec => {
      newHosts[rec.vmHost] = {services: [], max: 0, min: 0, host: rec.vmHost}
    })
    let memoryOverflow = []
    master && master.map(rec => {
      rec.choose && Object.keys(rec.choose).map(vmHost => {
        const record = _.cloneDeep(rec)
        if(record.choose[vmHost].isnew){
          record.isnew = true
        }else{
          record.isnew = false
        }
        newHosts[vmHost].services.push(record)
        newHosts[vmHost].max += record.maxMemory
        newHosts[vmHost].min += record.minMemory
        const vmConfig = (record.choose[vmHost].totalMem / 1024).toFixed(2)//vmConfig.split('-')[1]
        newHosts[vmHost].vmConfig = vmConfig
        if(newHosts[vmHost].min >= vmConfig * 1024 * 1024){
          newHosts[vmHost].isOver = true
          memoryOverflow.push(vmHost)
        }else{
          newHosts[vmHost].isOver = false
        }
      })
    })
    return {newHosts, memoryOverflow: memoryOverflow.length == 0 ? false : memoryOverflow}
  }
  // 遍历展示主机配置
  renderMasterDom = (item, masterIdx, hosts) => {
    const times = item.cardinality
    let Dom = []
    const chooseKeys = Object.keys(item.choose)
    if(chooseKeys.length > 0){
      Dom = chooseKeys.map((rec, index) => {
        return <li className="masterRow" key={`${item.id}-${rec}`}>
          <span className="leftLabel" title={item.displayName}>{item.displayName}:</span>
          <HostSelect disabled={item.choose[rec].isInstall || this.needntSelect.includes(item.displayName)} data={item.hosts} current={item.choose[rec]} value={item.choose[rec].vmHost} onChange={this.selectChange.bind(this, rec, masterIdx)}/>
          {  
            (times.includes('-') || times.includes('+')) && !times.includes('0-') && !item.choose[rec].isInstall &&
            <span className="addOrSubBtn">
              { times == '1+' && chooseKeys.length < hosts.length && index + 1 == chooseKeys.length &&
                <Button onClick={this.addRecord.bind(this, item, masterIdx)}>+</Button>
              }
              { chooseKeys.length > 1 && <Button onClick={this.subRecord.bind(this, item, rec, masterIdx)}>-</Button>}
            </span>
          }
        </li>
      })
    }
    return Dom
  }
  // 遍历展示主机下的服务
  renderHostService = services => {
    return <div className="servicesBox">
      {
        services.map(rec => {
          return <label key={rec.id} style={{background: rec.isnew ? '#69be28' : '#a9b4bb'}}>{rec.displayName}</label>
        })
      }
    </div>
  }
  // 两个特殊组件处理(不能手动改, 只能随着另外一个组件变动而变动)
  mustTogetherComponent = (master) => {
    const theComponents = {
      'HiveServer2': 'WebHCat Server', 
      'Metrics Collector': 'Grafana'
    }
    let theResult1 = {}
    // this.hostMaxmemory
    let theResult2 = {}
    const needntSelect = Object.values(theComponents)
    master && master.map((rec, idx) => {
      if(theComponents[rec.displayName]){
        theResult1[theComponents[rec.displayName]] = rec
      }
      if(needntSelect.includes(rec.displayName)){
        rec.idx = idx
        theResult2[rec.displayName] = rec
      }
    })
    const result = Object.keys(theResult2)
    if(result.length > 0){
      result && result.map(rec => {
        let item = theResult2[rec]
        const choose = Object.keys(theResult1[rec].choose)
        // 若是绑定的组件有多个值, 默认选择第一个
        master[item.idx].choose = {[choose[0]]: theResult1[rec].choose[choose[0]]}
        master[item.idx].hosts = theResult1[rec].hosts
      })
    }
    return master
  }
  // 组装数据并执行下一步
  onNextStep = () => {
    const {master} = this.state
    const needData = {}
    master && master.map(rec => {
      let data = needData[rec.name] || []
      rec.choose && Object.values(rec.choose).map(host => {
        data.push(host.vmHost)
      })
      needData[rec.name] = data
    })
    this.props.onsubmit(master, 1, "master")
  }

  render() {
    const {master, hosts} = this.state
    let {hostList, hostListInstalled} = this.props
    hostList = hostListInstalled || hostList
    const isOverflow = this.getComponentsByHost(master, hostList) || {}
    const hostInfo = isOverflow.newHosts
    const memoryOverflow = isOverflow.memoryOverflow
    return (
      <div className="chooseMasterSlave stepContent">
        <h2>指派 MASTER</h2>
        <div className="choosemasterContent clearfix">
          <ul className="chooseMaster fl">
            {
              master && master.map((rec,idx) => {
                return this.renderMasterDom(rec, idx, hostList)
              })
            }
          </ul>
          <ul className="hostComponentDetail fr"> 
           {
             hostInfo && Object.values(hostInfo).map(rec => {
               return <li key={rec.host}>
                 <p>{rec.host}({rec.vmConfig}GB)</p>
                 <p>所有组件所需最大内存: {rec.max ? (rec.max / 1024 / 1024).toFixed(2) : 0}GB</p>
                 <p style={{color: rec.isOver ? 'red' : '#666'}}>所有组件所需最小内存: {rec.min ? (rec.min / 1024 / 1024).toFixed(2) : 0}GB</p>
                 {rec.services.length > 0 && this.renderHostService(rec.services)}
               </li>
             })
           }
          </ul>
        </div>
        <div className="btnGroup">
          {memoryOverflow && <p style={{color: 'red', fontSize: 12}}>主机 {memoryOverflow.shift()} 内存超出, 请重新分配服务</p>}
          <Button onClick={() => this.props.onsubmit(null, -1, "master")}>
            上一步
          </Button>
          <Button disabled={memoryOverflow} className="stepNext" onClick={this.onNextStep} type="primary">
            下一步
          </Button>
        </div>
      </div>
    )
  }
}

const ConnChooseHost = connect(state=>{
  return{
    masterList: state.create.masterList,
    hostList  : state.create.hostList
  }
})(ChooseHost)
export default ConnChooseHost

function HostSelect ({data, onChange, value, current, disabled=false}) {
  return (
    <Select value={value} onChange={onChange} className="hostSel" style={{width: 300}} disabled={disabled}>
      <Select.Option value={current.vmHost}>{current.vmHost}</Select.Option>
      {
        data && Object.keys(data).map((rec, idx) => {
          return <Select.Option value={rec} key={rec}>{data[rec].vmHost}</Select.Option>
        })
      }
    </Select>
  )
} 