import request from 'utils/asyncService'

//获取所有组件指标信息
const getServiceMetric = (url, clusterName) => request({
    url: '/platform/ForwardTo',
    data: {
        url: `/api/v1/clusters/${clusterName}/${url}`,
        clusterName: clusterName,
        method: 'get'
    }
})
/**
 * 获取组件显示隐藏信息
 */
const getClusterStatteComponent = (clusterName) => request({
    url: '/platform/ForwardTo',
    data: {
        url: `/api/v1/persist/user-pref-internaladmin-dashboard`,
        clusterName: clusterName,
        method: 'get'
    }
})

/**
 * 改变组件显示状态
 * @param {*} data
 */
const Persist = (data, clusterName) => request({
    url: '/platform/ForwardTo',
    method: 'post',
    data: {
        url: `/api/v1/persist`,
        clusterName: clusterName,
        method: 'post',
        params: JSON.stringify(data)
    }
})


/**
 * 获取hbase链接
 */
const getHbaseLink = (data) => {
    return request({
        url: `/api/v1/stacks/${cluster}/versions/3.4.4/services/HBASE/quicklinks`,
        data
    })
}

const getHdfsLink = (data) => {
    return request({
        url: `/api/v1/stacks/${cluster}/versions/3.4.4/services/HBASE/quicklinks`,
        data
    })
}

// 获取状态监控下的组件告警信息
const getCOmponentStatus = (data) => request({
    url: '/platform/ForwardTo',
    method: 'get',
    data: {
        url:  `/api/v1/clusters/${data.clusterName}/alerts?fields=Alert/component_name,Alert/definition_id,Alert/definition_name,Alert/host_name,Alert/id,Alert/instance,Alert/label,Alert/latest_timestamp,Alert/maintenance_state,Alert/original_timestamp,Alert/scope,Alert/service_name,Alert/state,Alert/text,Alert/repeat_tolerance,Alert/repeat_tolerance_remaining&Alert/state.in(CRITICAL,WARNING)&Alert/maintenance_state.in(OFF)&from=0&page_size=1000`,
        clusterName: data.clusterName,
        method: 'get',
    }
})

// 获取服务指标
// http://172.17.171.94:8080/api/v1/clusters/LPC-4-1530756137/services/AMBARI_METRICS/components/METRICS_COLLECTOR?fields=metrics/hbase/master/AverageLoad[1530776928,1530780528,15],metrics/hbase/regionserver/storefiles[1530776928,1530780528,15],metrics/hbase/regionserver/regions[1530776928,1530780528,15],metrics/hbase/regionserver/requests[1530776928,1530780528,15],metrics/hbase/regionserver/blockCacheHitPercent[1530776928,1530780528,15],metrics/hbase/regionserver/compactionQueueSize[1530776928,1530780528,15]&_=1530780689462



//http://172.17.171.94:8080

const getServiceSummary = (data) => request({
    url: '/platform/ForwardTo',
    method: 'get',
    data: {
        url: `/api/v1/users/internaladmin/activeWidgetLayouts?WidgetLayoutInfo/section_name=${data.service_name}_SUMMARY`,
        clusterName: data.cluster_name,
        method: 'get'
    }
})

const getServiceSummaryChartInfo= (data) => request({
    url: '/platform/ForwardTo',
    method: 'post',
    data: {
        url: `/api/v1/clusters/LPC-4-1530756137/host_components?HostRoles/component_name=RESOURCEMANAGER&HostRoles/ha_state=ACTIVE&fields=${data.params}&format=null_padding`,
        clusterName: data.cluster_name,
        method: 'get'
    }
})
// 
const getMetricsSummaryChartInfo = (data) => {
    let {start, end} = data
    end = String(end || new Date().getTime()).substr(0, 10)
    start = String(start || end).substr(0, 10) - 3600
    return request({
        url: '/platform/ForwardTo',
        method: 'post',
        data: {
            url: `/api/v1/clusters/${data.cluster_name}/services/${data.service_name || 'AMBARI_METRICS'}/components/${data.component_name || 'METRICS_COLLECTOR'}?fields=metrics/hbase/master/AverageLoad[${start},${end},15],metrics/hbase/regionserver/storefiles[${start},${end},15],metrics/hbase/regionserver/regions[${start},${end},15],metrics/hbase/regionserver/requests[${start},${end},15],metrics/hbase/regionserver/blockCacheHitPercent[${start},${end},15],metrics/hbase/regionserver/compactionQueueSize[${start},${end},15]`,
            clusterName: data.cluster_name,
            method: 'get'
        }
    })
}

const getServiceNumber = (data) => request({
    url: '/platform/ForwardTo',
    method: 'post',
    data: {
        url: `/api/v1/clusters/${data.cluster_name}/services/${data.service_name}/components/${data.component_name}?fields=${data.fields}&format=null_padding`,
        
        clusterName: data.cluster_name,
        method: 'get'
    }
})
    // 
// 操作服务
const operateService = (data) => request({
    url: '/platform/ForwardTo',
    method: 'post',
    data: {
        url: `/api/v1/clusters/${data.cluster_name}/services/${data.service_name}`,  
        clusterName: data.cluster_name,
        method: 'PUT',
        params: JSON.stringify({
            "RequestInfo": {
                "context": data.context, 
                "operation_level": {
                    "level": "SERVICE",
                    "cluster_name": data.cluster_name,
                    "service_name": data.service_name
                }
            },
            "Body": {
                "ServiceInfo": {
                    "state": data.state 
                }
            }
        })
    }
})
// 重启服务下所有组件
const restartAllComponents = (data) => request({
    url: '/platform/ForwardTo',
    method: 'post',
    data: {
      url: `/api/v1/clusters/${data.cluster_name}/requests`,
      clusterName: data.cluster_name,
      method: 'post',
      params: JSON.stringify({
            "RequestInfo": {
            "command": "RESTART",
            "context": `重启${data.service_name}的全部组件`,
            "operation_level": {
                "level": "SERVICE",
                "cluster_name": data.cluster_name,
                "service_name": data.service_name
            }
        },
        "Requests/resource_filters": data.filters
      })
    }
})

export {
    getClusterStatteComponent, Persist, getHbaseLink, getHdfsLink, getServiceMetric, getCOmponentStatus, getServiceSummary, getServiceSummaryChartInfo, getMetricsSummaryChartInfo, getServiceNumber, operateService, restartAllComponents
}
