import React, { Component } from 'react'
import {Select, Icon, Button, Input} from 'antd'
import {actions, connect, Link} from 'mirrorx'

const Option = Select.Option
import './index.less'

import { history } from 'utils'


class ConsoleHeader extends Component {
  constructor(props){
    super(props)
    this.state = {
      selectValue: '',
      width: 0,
      isCreate: history.location.pathname != '/createCluster'
    }
  }
  componentWillMount(){
    if(!this.props.clusterList){
      actions.app.getClusterList()
    }
  }
  
  handleChange = (value) => {
    const {clusterList} = this.props
    clusterList && clusterList.map(rec => {
      if(rec.clusterName == value){
        actions.app.changeCluster(rec)
        this.props.selectChange && this.props.selectChange(rec)
      }
    })
  }

  getHelpHandle = () => {
    
  }

  toggleIpt = (e) => {
    const width = this.state.width || 0
    this.setState({
      width: 200 - width
    })
  }

  render() {
    const {currentCluster, clusterList, refresh, getHelpHandle} = this.props
    const {selectValue, width} = this.state
    const defaultValue = currentCluster && currentCluster.clusterName

    return <div className="devToolsHeader">
      {clusterList && clusterList.length > 0 && 
        <span className="fl">
          <label>当前集群: </label> 
          <Select value={selectValue || defaultValue} onChange={this.handleChange} style={{width: 200}}>
          {
            clusterList && clusterList.map(rec => {
              return <Option key={rec.clusterId} value={rec.clusterName}>{rec.clusterAlias}</Option>
            }) 
          }
          </Select>
        </span>
      }
      <ul className="fr">
        {/* <li title="搜索">
          <Input placeholder="请输入搜索条件" type="text" className="searchIptHide" style={{width: width || 0}}/>
          <Icon type="search" onClick={this.toggleIpt}/>
        </li> */}
        <li title="刷新" onClick={refresh || (() => location.reload())}><Icon type="sync" /></li>
        <li title="帮助" onClick={this.getHelpHandle}><Icon type="question-circle-o" /></li>
        <li>
          <Button icon="plus" className="createBtn"><Link to={'/createCluster'}>创建集群</Link></Button>
        </li>
      </ul>
    </div>
  }
}

const ConnConsoleHeader = connect(state=>{
  return{
    currentCluster: state.app.currentCluster,
    clusterList: state.app.clusterList
  }
})(ConsoleHeader)

export default ConnConsoleHeader