import React from 'react'
import {connect, actions} from 'mirrorx'
import LayoutMain from '../../components/layout'

const LayoutConnected = connect(state => {
    return {
        layout: state.layout,
        user: state.user,
        getClusterLoading: state.app.getClusterLoading,
        currentrouter: state.app.currentrouter,
        taskModalVisible: state.host.taskModalVisible
    }
})(LayoutMain)

class LayoutContainer extends React.Component {
    state = {
        unlisten: null
    }

    constructor(props, context) {
        super(props)
    }


    componentWillMount() {
        this.selectMenu()
        actions.app.routeChange(this.props.location)
        this.unlisten = this.props.history.listen((location) => {
            this.selectMenu(location.pathname)
            actions.app.routeChange(location)
        })
    }

    componentDidMount() {
    }

    getPathName(path) {
        const matched = path.match(/\/([^/]+)/)
        if (matched && matched[1]) {
            return matched[1]
        }
        return 'monitor'
    }

    selectMenu(pathname) {
        const name = this.getPathName(pathname || this.props.location.pathname)
        actions.layout.changeMenuSelectedKey([name])
    }


    componentWillUnmount() {
        this.unlisten()
    }

    render() {
        return (
            <LayoutConnected/>
        )
    }
}


export default LayoutContainer