import React, {Component} from 'react'
import styled from 'styled-components'
import {Icon, Menu, Dropdown, Modal, Button, Alert, Slider, InputNumber} from 'antd'
import {connect, actions} from 'mirrorx'

const SubMenu = Menu.SubMenu

const ChangeSliderWrap = styled.div`
    margin-top: 40px;
    margin-bottom: 40px;
    margin-left: 38px;
    max-width: 630px;
    max-height: 42px;
    .ant-slider{
      .ant-slider-step,.ant-slider-rail{
        height:15px;
      }
      .ant-slider-rail{
        background-image: -webkit-linear-gradient(left, #16bf00, #16bf00 ${props => props.value[0]}%, #ff9c00 ${props => props.value[0]}%, #ff9c00 ${props => props.value[1]}%, #ff4849 ${props => props.value[1]}%, #ff4849);
      }
      .ant-slider-track{
        height:15px;
        background:#ff9c00
      }
      .ant-slider-dot{
        display:none;
      }
      .ant-slider-handle{
        position: absolute;
        z-index: 2;
        width: 1.2em;
        height: 1.2em;
        cursor: default;
        background-color: #e6e6e6;
        background-repeat: no-repeat;
        background-image: linear-gradient(#ffffff, #ffffff 25%, #e6e6e6);
        color: #333;
        font-size: 13px;
        line-height: normal;
        border: 1px solid #ccc;
        border-bottom-color: #bbb;
        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
        transition: 0.1s linear background-image;
        overflow: visible;
        border-radius:1px;
        margin-top:0
      }
      &:hover .ant-slider-track {
        background-color: #ff9c00;
        }
      
    }
`

const marks = {
    0: {
        style: {
            color: '#000',
            fontSize: '16px',
        },
        label: <strong>0</strong>,
    },
    100: {
        style: {
            color: '#000',
            fontSize: '16px',
        },
        label: <strong>100</strong>,
    },
}

class MonitorModal extends Component {
    constructor(props) {
        super(props)
    }

    modifyFirstRange = (value) => {
        const {modelInfo} = this.props
        actions.monitor.editThreshold([value, modelInfo.range[1]])
    }
    modifyLastRange = (value) => {
        const {modelInfo} = this.props

        actions.monitor.editThreshold([modelInfo.range[0], value])
    }
    confirmApplyBtn = () => {
        actions.monitor.changeVisibleOfComponent()
        actions.monitor.toggleEditDialog()
    }
    cancelBtn = () => {
        actions.monitor.toggleEditDialog()
    }

    render() {
        const {showDialog, modelInfo} = this.props
        return <Modal
            title="自定义控件"
            visible={showDialog}
            onOk={this.confirmApplyBtn}
            onCancel={this.cancelBtn}

            width="60%"
            okText="应用"
            cancelText="取消"
        >
            <Alert type="info" description={<div>
                <div>编辑阈值更改当前控件的颜色。</div>
                <div>输入两个介于0到100之间的数字。</div>
            </div>}>
            </Alert>
            <ChangeSliderWrap value={modelInfo.range}>
                <Slider range value={modelInfo.range} marks={marks} onChange={actions.monitor.editThreshold}/>
                {modelInfo.range && <div className="flexBetween">
                    <InputNumber value={modelInfo.range[0]} onChange={this.modifyFirstRange}/>
                    <InputNumber value={modelInfo.range[1]} onChange={this.modifyLastRange}/>
                </div>}
            </ChangeSliderWrap>
        </Modal>
    }
}
const ConnectMonitorModal = connect(state => {
    return {
        showDialog: state.monitor.showDialog,
        modelInfo: state.monitor.modelInfo
    }
})(MonitorModal)

export default ConnectMonitorModal