import React, { Component } from 'react'
import {Select,Table, Checkbox, Button, message} from 'antd'
// import axios from 'axios'
import {actions, connect} from 'mirrorx'
import _ from 'lodash'
import './index.less'
class ChooseSlave extends Component {
  constructor(props){
    super()
    this.state = {
      allKey: {},
    }
  }
  componentWillMount(){
    const allKey = this.props.value
    if(allKey){
      this.setState({allKey})
      return
    }

    let {installedSlave,slaveList,hostListInstalled, hostList } = this.props
    hostList = hostListInstalled || hostList
    let ky = {}
    if(slaveList){
      let hostIdx = 0
      let hosts = hostList.map(rec => rec.vmHost)
      Object.values(slaveList).map(rec => {
        if(rec.name == 'DATANODE' || rec.name == 'NODEMANAGER'){
          ky[rec.name] = hosts
        }else{
          if(rec.displayName.includes(' Client')){
            ky[rec.name] = hosts
          }else{
            ky[rec.name] = [hosts[hostIdx]]
            hostIdx == 3 ? hostIdx = 0 : hostIdx++
          }
        }
      })
    }
    if(installedSlave){
      this.setState({allKey: JSON.parse(JSON.stringify(installedSlave))})
      return
    }else{
      this.setState({allKey: ky})
    }
  }
  componentWillReceiveProps(np){
    if(np.createStatus && np.createStatus == 'SUCCESS'){
      this.props.onsubmit(this.state.postData, 1, "slave")
    }
  }
  columns = (slave) => {
    if(! slave) return []
    const {allKey} = this.state
    const {installedServices} = this.props

    const slaveFirstData = slave[0].slave
    const c = slaveFirstData && Object.keys(slaveFirstData).map((item, idx) => {
      const rec = slaveFirstData[item]
      const isAllCheck = (allKey[rec.name] && allKey[rec.name].length == slave.length) || false
      const disabledAll = installedServices ? installedServices.includes(rec.serverName) : false
      return {
        title: <Checkbox disabled={disabledAll} checked={isAllCheck} onChange={this.checkboxChange.bind(this, rec, 'all')}>全选</Checkbox>,
        dataIndex: `slave.${rec.name}`,
        render: (record, row) => {
          const isCheck = allKey[record.name] ? allKey[record.name].includes(row.host.vmHost) : false

          const disabled = installedServices ? installedServices.includes(record.serverName) : false
          return <Checkbox disabled={disabled} checked={isCheck} onChange={this.checkboxChange.bind(this, row, record)}>{record.displayName}</Checkbox>
        }
      }
    })
    return [{
        title: '主机',
        dataIndex: 'host',
        fixed: 'left',
        width: 130,
        render: (record) => {
          return <span>{record.vmHost}</span>
        }
      },
      ...c
    ]
  }
  formatSlaveData = (hosts, slave) => {
    let newHosts = []
    hosts && hosts.map(host => {
      Object.keys(slave).map(rec => {
        slave[rec].host = host
      })
      newHosts.push({host, slave})
    })
    return newHosts.length > 0 ? newHosts : undefined
  }
  checkboxChange = (record, type, e) => {
    const {allKey} = this.state
    let {hostList, hostListInstalled, installedSlave} = this.props
    hostList = hostList || hostListInstalled

    const val = e.target.checked

    if(val){
      if(type === 'all'){
        allKey[record.name] = hostList.map(rec => {
          return rec.vmHost
        })
      }else{
        const curVMId = type.name
        const host = record.host.vmHost
        allKey[curVMId] ? 
          allKey[curVMId].includes(host) ? allKey[curVMId] = allKey[curVMId] : allKey[curVMId].push(host) :
          allKey[curVMId] = [host]
        
      }
    }else{
      if(type === 'all'){
        delete allKey[record.name]

        if(installedSlave && installedSlave[record.name]){
          allKey[record.name] = installedSlave[record.name]
        }
      }else{
        const curVMId = type.name
        const host = record.host.vmHost
        if(allKey[curVMId]){
          const idx = allKey[curVMId].indexOf(host)
          allKey[curVMId].splice(idx, 1)
        }
        if(!allKey[curVMId] || allKey[curVMId].length == 0){
          delete allKey[curVMId]
        }
      }

    }
    this.setState({allKey})
  }
  onNextStep = () => {
    const {master, slaveList, clustername, installedServices, currentCluster} = this.props
    const {allKey} = this.state
    let needData = {}

    Object.keys(allKey).map(rec => {
      const serverName = slaveList[rec].serverName
      if(needData[serverName]){
        needData[serverName].push({[rec]: allKey[rec]})
      }else{
        needData[serverName] = [{[rec]: allKey[rec]}]
      }
    })
    
    master && master.map(rec => {
      const serverName = rec.serverName
      let data = []
      rec.choose && Object.values(rec.choose).map(host => {
        data.push(host.vmHost)
      })
      if(needData[serverName]){
        needData[serverName].push({[rec.name]: data})
      }else{
        needData[serverName] = [{[rec.name]: data}]
      }
    })
    
    
    
    
    this.setState({
      postLoading: true
    })

    if(installedServices){
      Object.keys(needData).map(rec => {
        if(installedServices.includes(rec)){
          delete needData[rec]
        }
      })

      actions.create.postReServiceList({services: JSON.stringify(needData), clustername: currentCluster.clusterName})
      .then(res => {
        if(res.code == 1){
          setTimeout(() => {
            this.setState({
              postLoading: false
            })
            this.props.onsubmit(allKey, 1, "slave")
          }, 3000)
        }
      })
    }else{
      actions.create.postServiceList({services: JSON.stringify(needData), clustername})
      .then(res => {
        // if(res.code == 1){
          this.setState({
            postLoading: false
          })
          this.props.onsubmit(allKey, 1, "slave")
        // }
      })
    }
  }
  render() {
    let {slaveList, hostList, createStatus, hostListInstalled} = this.props
    hostList = hostListInstalled || hostList

    const {postData, postLoading} = this.state

    const slaveData = this.formatSlaveData(hostList, slaveList)
    const columns = this.columns(slaveData)
    // const isLoading = createStatus && createStatus !== 'SUCCESS' && createStatus !== 'FAILED' || (postData && !createStatus)
    return (
      <div className="chooseSlaveClient stepContent">
        <h2>指派SLAVE和CLIENT</h2>
        <Table bordered
          className="slaveAndClient"
          columns={columns} 
          dataSource={slaveData}
          scroll={{ x: 1500 }}
          pagination={false}
          rowKey={(r) => r.host.vmId}/>
        {/* {
          isLoading && <p style={{margin: '10px 0 0 0'}}>{createStatus || '服务配置中'}<span className="dotdotdot"></span></p>
        } */}
        {
          createStatus == 'FAILED' && <p style={{color: 'red', margin: '10px 0 0 0'}}>配置主机失败, 请检查配置或稍候再试</p>
        }
        <div className="btnGroup">
          <Button disabled={postLoading} onClick={() => this.props.onsubmit(null, -1, "slave")}>
            上一步
          </Button>
          <Button loading={postLoading} className="stepNext" onClick={this.onNextStep} type="primary">
            下一步
          </Button>
        </div>
      </div>
    )
  }
}
const ConnChooseSlave = connect(state=>{
  return{
    slaveList: state.create.slaveList,
    hostList : state.create.hostList,
    createStatus: state.create.createStatus,
    currentCluster: state.app.currentCluster
  }
})(ChooseSlave)
export default ConnChooseSlave