import React, {Component} from 'react'
import {connect, actions} from 'mirrorx'
import {Table, Icon, Button, Tag} from 'antd'
import VersionModal from 'components/versionModal'


class ComponentVersion extends Component {
    constructor(props) {
        super(props)
        this.state = {
            showVersionModal: false,
            versionModalInfo: 123,
        }
    }

    componentDidMount() {
        actions.components.getHostInfo()
    }

    showVersionDetail = () => {
        this.setState({
            showVersionModal: true
        })
    }

    hideVersionModal = () => {
        this.setState({
            showVersionModal: false
        })
    }

    getColumns = () => {
        return [{
            title: '',
            dataIndex: 'name',
            key: 'name',
            width: 150
        }, {
            title: 
                <div>
                    {this.props.version}&nbsp;&nbsp;
                    {/* <span style={{color: '#999'}}>(<a href="javascript:;" onClick={this.showVersionDetail} style={{color: '#999'}}>显示详细信息</a>)</span><br/> */}
                    <span style={{color: '#999'}}>当前</span>
                </div>
            ,
            dataIndex: 'versions',
            key: 'versions',
            render: (value, row, index) => (
                <Tag color="#1ACB40">
                  {value}
                </Tag>
              )
        }]
    }

    render() {
        const { componentsListData } = this.props
        const { showVersionModal, versionModalInfo } = this.state
        return <div>
            kerberos
        </div>
    }
}

const ConnComponentVersion = connect(state => {
    return {
        version: state.components.version,
        status: state.components.status,
        componentsListData: state.components.componentsListData,
    }
})(ComponentVersion)
export default ConnComponentVersion