import axios from 'axios'
import qs from 'qs'
import lodash from 'lodash'
import {history} from 'utils'
import Cookies from 'js-cookie'
import {
  message
} from 'antd'

axios.interceptors.request.use(config => {
  if (config.method == 'GET') {
    config.url = config.url.indexOf('?') > 0 ? config.url + '&clearCache=' + new Date().valueOf() : config.url + '?clearCache=' + new Date().valueOf()
  }
  if (config.method == "post" || config.method == "put") {
    if (!config.headers['Content-Type']) {
      if(!config.isDataString){
        config.data = qs.stringify(config.data)
      }
      config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
      config.headers['X-Requested-By'] = 'X-Requested-By'
      config.headers['X-Requested-With'] = 'X-Requested-With'
    }
  }
  return config
}, error => {
  return Promise.reject(error)
})

axios.interceptors.response.use(config => {
	if(config.request.responseURL.indexOf('/loginPage') >= 0 && config.data.data == -2){
    Cookies.remove('LEAP-CLOUD-USERNAME')
    sessionStorage.removeItem('currentCluster')
    sessionStorage.removeItem('clusterList')
		history.go('/signin')
	}
	return config
})

const fetch = (options) => {
  let {
    method = 'get',
      data,
      fetchType,
      url,
  } = options


  const cloneData = lodash.cloneDeep(data)
  switch (method.toLowerCase()) {
    case 'get':
      return axios.get(url, {
        params: cloneData,
      })
    case 'delete':
      return axios.delete(url, {
        data: cloneData,
      }, {isDataString: options.isDataString})
    case 'post':
      return axios.post(url, cloneData, {isDataString: options.isDataString})
    case 'put':
      return axios.put(url, cloneData, {isDataString: options.isDataString})
    case 'patch':
      return axios.patch(url, cloneData, {isDataString: options.isDataString})
    default:
      return axios(options)
  }
}

export default function asyncService(options) {
  if (options.url && options.url.indexOf('//') > -1) {
    const origin = `${options.url.split('//')[0]}//${options.url.split('//')[1].split('/')[0]}`
  }

  return fetch(options).then((response) => {
    const {
      statusText,
      status
    } = response
    let data = response.data

    return Promise.resolve({
      success: true,
      message: statusText,
      statusCode: status,
      ...data,
    })
  }).catch((error) => {
    
    const {
      response
    } = error
    let msg
    let statusCode
    if (response && response instanceof Object) {
      const {
        data,
        statusText
      } = response
      statusCode = response.status
      msg = data.message || statusText
    } else {
      statusCode = 600
      msg = error.message || 'Network Error'
    }

    return Promise.reject({
      success: false,
      statusCode,
      message: msg
    })
  })
}