import React, { Component } from 'react'
import { connect, actions, Link } from "mirrorx"

import { Form, Input, Button, Icon, Checkbox, message, Table, Tag} from 'antd'
import Header from 'components/loginHeader'
import axios from 'axios'
import './index.less'
import Cookies from 'js-cookie'



class Product extends Component {
  constructor(props){
    super(props)
    this.state = {
      loading: true,
      productList: [],
      userKey: Cookies.get('TRIPNOTE-KEY')
    }
  }

  componentWillMount(){
    this.getProductList()
  }

  getProductList = () => {
    let self = this
    console.log(self)
    axios.post(
      '/token/product/doSearch', 
      {isMy: true},
      {
        headers: {
          'authorization': this.state.userKey,
        }
      }
    ).then(res => {
      if (res.data.code == 1) {
        self.setState({
          loading: false,
          productList: res.data.data.data
        })
      }
    })
  }
  formatStatus = (status) => {
    if(status == -1 || status == 0){
      return '未发布'
    }else if(status == 1){
      return <Tag color="#87d068">已发布</Tag>
    }
  }
  timestampToTime = (timestamp) => {
    var date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
    var Y = date.getFullYear() + '-';
    var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
    var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
    var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
    var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
    var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
    return Y+M+D+h+m+s;
  }


  setStatusDown = (item) => {
    return () => {
      console.log(item)
      axios.post(
        '/token/product/status/doUpdate', 
        {"statuses": [{id: item.id, status: -1}]},
        {
          headers: {
            'Content-Type': 'application/json',
            'authorization': this.state.userKey
          }
        }
      )
      .then(res => {
        message.success('操作成功！');
        this.getProductList()
      })
    }
  }

  setStatusUp = (item) => {
    return () => {
      console.log(item)
      axios.post(
        '/token/product/status/doUpdate', 
        {"statuses": [{id: item.id, status: 1}]},
        {
          headers: {
            'Content-Type': 'application/json',
            'authorization': this.state.userKey
          }
        }
      )
      .then(res => {
        message.success('操作成功！');
        this.getProductList()
      })
    }
  }

  getColumns = () => (
    [
      {
        title: '产品图片',
        dataIndex: "label",
        key: "label",
        width: 90,
        render: (value, row, index) => (
          <img src={row.imageUrl} className="pic" />
        )
      },
      {
        title: '名称',
        dataIndex: "name",
        key: "name",
        width: 300,
      },
      {
        title: '类型',
        dataIndex: "priceType",
        key: "priceType",
        render: (value, row, index) => (
          value == 1 ? '限时特价' : '拼团'
        )
      },
      {
        title: '原价',
        dataIndex: "rawPrice",
        key: "rawPrice",
        render: (value, row, index) => (
          <span>￥{value}</span>
        )
      },
      {
        title: '折扣价',
        dataIndex: "discountPrice",
        key: "discountPrice",
        render: (value, row, index) => (
          value ? <span>￥{value}</span> : ''
        )
      },
      {
        title: '创建时间',
        dataIndex: "createTime",
        key: "createTime",
        render: (value, row, index) => (
          this.timestampToTime(value)
        )
      },
      {
        title: '状态',
        dataIndex: "status",
        key: "status",
        render: (value, row, index) => {
          if(value == -1 || value == 0){
            return <Tag color="#2db7f5">未发布</Tag>
          }else if(value == 1){
            return <Tag color="#87d068">已发布</Tag>
          }
        }
      },
      {
        title: '操作',
        dataIndex: "status",
        key: "opreate",
        render: (value, row, index) => {
          if(value == 0 || value == -1){
            return (
              <div>
                <a href="javascript:;">
                  编辑
                </a>
                &nbsp;&nbsp;
                <a href="javascript:;" onClick={this.setStatusUp(row)}>
                  上架
                </a>
              </div>
            )
          }

          if(value == 1){
            return (
              <a href="javascript:;" onClick={this.setStatusDown(row)}>
                下架
              </a>
            )
          }
        }
      }
    ]
  )

  render() {
    const {loading, productList} = this.state
    return (
      <div className="productWrap">
        <Header />
        <div className="productList">
          <div className="title">
            <span className="name">产品列表</span>
            <Link to="/productCreate" className="link">添加产品</Link>
          </div>
          <Table
            className="productListTable"
            loading={loading}
            bordered={true}
            dataSource={productList}
            columns={this.getColumns()}
            // pagination={false}
          />
        </div>
      </div>
    )
  }
}
export default Product