import React from 'react'
import mirror,  { render, history } from 'mirrorx'
import App from './app'
import { IntlProvider, addLocaleData } from 'react-intl'
import { LocaleProvider } from 'antd'
import './assets/css/common.less'
import './store'
import getCurrentAppLocale from './locals/index'

window.appLocale = getCurrentAppLocale()

render(<LocaleProvider locale={window.appLocale.antd}>
  <IntlProvider locale={window.appLocale.locale}
    messages={window.appLocale.messages}
  >
    <App />
  </IntlProvider>
</LocaleProvider>
  , document.getElementById('Root'))
