import FileSaver from 'file-saver';
import categoryName from 'pages/hosts/summary/chartsName.js'
import getIEVersion from 'utils/getIEVersion'

const exportFile = (data, name, type, title) => {
  name = name || new Date().getTime()
  type = type || 'json'
  // 输出表格时的标题
  let str = Array.isArray(title) && title.map(rec => {
    return categoryName[name][rec]
  }).join(',')
  str = '时间戳,' + str + '\r\n'

  let newData = data
  if(type == 'csv'){
    const _data = Object.keys(data)
    newData = data[_data[0]].map((item, idx) => {
      const val = _data.map((k, index) => {
        return `${data[_data[index]][idx][0]}`
      }).join(',')
      return `${item[1]},${val}`
    }).join('\r\n')
  }else if(type == 'json'){
    newData = Object.keys(data).map(rec => {
      return {
        name: categoryName[name][rec],
        data: data[rec]
      }
    })
  }
  //Excel打开后中文乱码添加如下字符串解决
  let exportContent = "\uFEFF";
  let blob = new Blob([type == 'csv' ? exportContent + str + newData : JSON.stringify(newData)], {
    type: "text/plain;charset=utf-8"
  })
  // if(getIEVersion(10)){
    // FileSaver.saveTextAs(blob, `${name}.${type}`);
  // }
  // else{
    FileSaver.saveAs(blob, `${name}.${type}`);
  // }
};

export default exportFile


