import React, { Component } from "react";
import { Icon, Menu, Dropdown, Checkbox, Button, Table, Divider } from "antd";
import { connect, actions, Link } from "mirrorx";
import { SortableContainer, SortableElement } from "react-sortable-hoc";
import * as MapperView from "../../components/monitor/widgetsMapperView";
import { widgetsMapper, showEdit } from "../../config/index";
import ConnectMonitorModal from "./monitorModal"

import './monitor.less'

const SubMenu = Menu.SubMenu;
const TimeMenu = (
  <Menu>
    <Menu.Item key="0">
      <div>过去两个小时</div>
    </Menu.Item>
    <Menu.Item key="1">
      <div>过去三个小时</div>
    </Menu.Item>
  </Menu>
);

const ComponentInfoColumns = [
  {
    title: "类型",
    dataIndex: "type",
    key: "type"
  },
  {
    title: "内容",
    dataIndex: "content",
    width: "60%",
    key: "content"
  }
];

/**
 * 可拖动的块
 */
const SortableItem = SortableElement(data => {
  let { value, threshold, metricViewData } = data;
  let viewName = widgetsMapper[value].view;
  let WidgetsComponent = MapperView[viewName];
  let sourceData = metricViewData[viewName];
  return (
    <div className="sortItemContent">
      <WidgetsComponent sourceData={sourceData} {...data} />
      <Icon
        type="close-circle"
        className="closeIcon"
        onClick={() => actions.monitor.deleteWidgetComplete({ value })}
      />
      {showEdit.indexOf(value) > -1 && (
        <Icon
          type="form"
          className="edit"
          onClick={() => actions.monitor.toggleEditDialog({ value })}
        />
      )}
    </div>
  );
});

/**
 * 可拖动的列表
 */
const SortableList = SortableContainer(data => {
  let { items, metricViewData } = data;

  return (
    <div className="sortContainer">
      {items.map((value, index) => (
        <SortableItem
          key={`item-${index}`}
          index={index}
          value={value}
          {...data}
        />
      ))}
    </div>
  );
});

class ClusterState extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showDropdown: false,
      visibleComponent: []
    };
    this.currentClusterName = null
  }

  componentWillReceiveProps(props){
    if(props.currentCluster && props.currentCluster.clusterName != this.currentClusterName){
      this.currentClusterName = props.currentCluster.clusterName
      this.componentWillMount()
    }
  }

  componentWillMount() {
    let self = this;
    if (this.props.currentCluster) {
      self.needLoad();
    } else {
      actions.app.getClusterList().then(rec => {
        if (rec && rec.length > 0) {
          self.needLoad();
        }
      });
    }
  }

  needLoad = async () => {
    await actions.app.getAllService();
    await actions.monitor.getClusterInfoComponent();
    await actions.monitor.getServiceMetric();
    await actions.monitor.getClusterInfo();
  }

  onSortEnd = ({ oldIndex, newIndex }) => {
    actions.monitor.onSortMonitorInfoEnd({
      oldIndex,
      newIndex
    });
  };

  handleMenuClick = e => {
    e.nativeEvent.stopImmediatePropagation();
    this.setState({
      showDropdown: false
    });
  };

  handleVisibleChange = e => {
    e.nativeEvent.stopImmediatePropagation();
    this.setState({
      showDropdown: !this.state.showDropdown
    });
  };

  onChange = checkedValues => {
    this.setState({
      visibleComponent: checkedValues
    });
  };
  ChangeMonitorCOmponent = () => {
    actions.monitor.addMonitorComponentInfo(this.state.visibleComponent);
  };

  render() {
    const { threshold, hidden, metricViewData, currentCluster, clusterInfoLoading, clusterInfo } = this.props

    const DealMenu = (
      <Menu onClick={this.handleMenuClick}>
        <SubMenu
          title={
            <span>
              <Icon type="plus" />
              <span className="menuName">添加</span>
            </span>
          }
        >
          <div className="compsAble">
            <Checkbox.Group style={{ width: "100%" }} onChange={this.onChange}>
              {hidden.map((item, idx) => (
                <div className="checkDiv" key={idx}>
                  <Checkbox value={item[0]}>{item[1]}</Checkbox>
                </div>
              ))}
            </Checkbox.Group>
            {hidden.length === 0 && <div className="checkDiv">没有可添加的组件</div>}
          </div>
          <div className="menuButtonWrap">
            <Button type="default">取消</Button>
            {hidden.length > 0 && (
              <Button type="primary" onClick={this.ChangeMonitorCOmponent}>
                确定
              </Button>
            )}
          </div>
        </SubMenu>
        <SubMenu
          title={
            <span>
              <Icon type="edit" />
              <span className="menuName">编辑</span>
            </span>
          }
        >
          <Menu.Item>
            <Icon type="sync" />
            <span>将所有控件重置为默认值 </span>
          </Menu.Item>
        </SubMenu>
      </Menu>
    );

    return (
      <div className="clusterStateWrap">
        <div className="clusterHeader">
          <Dropdown
            overlay={DealMenu}
            trigger={["hover"]}
            onClick={this.handleVisibleChange}
            visible={this.state.showDropdown}
          >
            <span className="ant-dropdown-link deal">
              操作 <Icon type="down" />
            </span>
          </Dropdown>
          <Dropdown overlay={TimeMenu} trigger={["click"]}>
            <span className="ant-dropdown-link">
              过去一个小时 <Icon type="down" />
            </span>
          </Dropdown>
        </div>

        <SortableList
          items={this.props.visible}
          metricViewData={metricViewData}
          threshold={threshold}
          onSortEnd={this.onSortEnd}
          axis="xy"
          distance={10}
        />

        <ConnectMonitorModal className="dialogStyle" />

        <div className="componentInfo">
          <p>集群信息</p>
          <Table
            loading={clusterInfoLoading}
            dataSource={clusterInfo}
            columns={ComponentInfoColumns}
            bordered={true}
            pagination={false}
          />
        </div>
      </div>
    );
  }
}

const ConnectClusterState = connect(state => {
  return {
    hidden: state.monitor.hidden,
    visible: state.monitor.visible,
    threshold: state.monitor.threshold,
    status: state.monitor.status,
    showDialog: state.monitor.showDialog,
    metricViewData: state.monitor.metricViewData,
    currentCluster: state.app.currentCluster,
    clusterInfo: state.monitor.clusterInfo,
    clusterInfoLoading: state.monitor.clusterInfoLoading
  };
})(ClusterState);

export default ConnectClusterState;
