import React, { Component } from 'react'
import {Card, Modal, Spin, Select, DatePicker, Tooltip} from 'antd'
import { connect, actions} from 'mirrorx'

// 图表名称配置(根据字段匹配中文名)
import {chartName} from './chartsName'
import _ from 'lodash'
import exportFile from 'utils/fileDownload'
import './index.less'
// 引入 ECharts 主模块
import echarts from 'echarts/lib/echarts'
// 引入柱状图
import  'echarts/lib/chart/line'
// 引入提示框,标题,图例组件
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import "echarts/lib/component/legend"
// svg 渲染器
import 'zrender/lib/svg/svg'
import {echatOptions} from './echartOptions'

import Components from './components'
import HostSuammary from './summary'

const Option = Select.Option
const { RangePicker } = DatePicker;
class Summary extends Component {
  constructor(props){
    super(props)
    this.state = {
      chatsLoading: false,
      bigChartShow: false
    }
    this.charts = {}
    this.chartsEl = {}
    this.chartsOptions = {}
  }
  async componentDidMount(){
    const {currentCluster} = this.props
    const host_name = this.props.match.path.split('/')[2]
    this.host_name = host_name
    // this.setState({refreshDataLoading:true})
    const getSummary = actions.host.getHostSummary
    const getHostStatus = actions.host.getHostStatus
    this.setState({
      isloading: true
    })
    await Promise.all([getSummary({host_name}), getHostStatus({host_name})])
    .then(([summary, hostStatus]) => {
      if(hostStatus){
        this.renderChats(hostStatus)
      }
      this.setState({ hostStatus: hostStatus || [], isloading: false})
      this.setTimer()
    })
    //  
    
  }
  setTimer = (start, end) => {
    const getSummary = actions.host.getHostSummary
    const getHostStatus = actions.host.getHostStatus
    const host_name = this.host_name
    clearInterval(this.timer)
    this.timer = null
    this.timer = setInterval(() => {
      const {start_timestamp, end_timestamp} = this.state
      Promise.all([getSummary({host_name}), getHostStatus({host_name})])
      .then(([summary, hostStatus]) => {
        if(hostStatus){
          this.renderChats(hostStatus)
          this.setState({hostStatus})
        }
      })
    }, 5000)
  }
  renderChats = (hostStatus, timeLen) => {
    const {curBigChartType} = this.state
    if(curBigChartType){
      // // this.charts[curBigChartType] = echarts.init(this.chartsEl[curBigChartType]) : ''
      // this.chartsOptions[curBigChartType] = echatOptions(_.cloneDeep(hostStatus[curBigChartType]), curBigChartType === 'cpu', curBigChartType, timeLen)
      // this.bigChart.setOption(this.chartsOptions[curBigChartType])
      // this.showBigChart(curBigChartType)()
      this.setBigChart(hostStatus, curBigChartType)
      return 
    }
    Object.keys(this.chartsEl).map(type => {
      this.charts[type] = echarts.init(this.chartsEl[type])
      this.chartsOptions[type] = echatOptions(hostStatus[type], type === 'cpu', type, timeLen)
      this.charts[type].setOption(this.chartsOptions[type])
    })
  }
  componentWillUnmount(){
    clearTimeout(this.timer)
    this.timer = null
    echarts.dispose(this.bigChart)
    Object.keys(this.chartsEl).map(rec => {
      echarts.dispose(this.chartsEl[rec])
    })
  }
  // 展示大号统计图
  showBigChart = (type,e ) => async(start, end) => {
    if(typeof start == 'object' && start.target.nodeName == 'A'){
      return
    }
    const {start_timestamp, end_timestamp} = this.state
    // clearInterval(this.timer)
    // this.timer = null
    let setData = {
      bigChartShow: true,
      chatsLoading: false,
      bigChartLoading: false,
      curModalName: chartName[type],
      curBigChartType: type,
    }
    if(typeof start != 'object' && (start || end)){
      this.setState({bigChartLoading: true})
      await actions.host.getHostStatus({
        host_name: this.host_name, 
        start: start || start_timestamp, 
        end: end || end_timestamp
      }).then(hostStatus => {
        if(hostStatus){
          this.setBigChart(hostStatus, type)
        }else{
          setData.hostStatus = null
        }
      })
    }else{
      this.setBigChart(this.props.hostStatus, type)
      setData = Object.assign({}, setData, {
        old_start_timestamp: start, 
        old_end_timestamp: end,
        old_agoValue: this.state.agoValue
      })
    }
    this.setState(setData)
    // const {hostStatus} = this.props
    // const bigChartOpts = Object.assign({},this.chartsOptions[type])//echatOptions(hostStatus[type], type === 'cpu', type)
    // this.bigChart.style.height = `${30 * Object.keys(hostStatus[type]).length + 200}px`

  }
  // 
  setBigChart = (hostStatus, type) => {
    const bigChart = echarts.init(this.bigChart)
    const bigChartOpts = echatOptions(_.cloneDeep(hostStatus[type]), type === 'cpu', type)
    bigChartOpts.legend.show = true
    bigChartOpts.tooltip.show = true
    bigChartOpts.xAxis.axisLabel.show = true
    bigChartOpts.legend.bottom = 10
    bigChartOpts.legend.left = 0
    bigChartOpts.legend.orient = 'vertical'
    const len = Object.keys(hostStatus[type]).length
    const bottom = 30 * len + (30 / len * 1.5)
    bigChartOpts.grid.bottom = bottom
    bigChart.setOption(bigChartOpts)
  }
  // 隐藏大号统计图
  hideBigChart = () => {
    echarts.dispose(this.bigChart)
    const {old_start_timestamp, old_end_timestamp, old_agoValue} = this.state
    this.setState({
      bigChartShow: false,
      curBigChartType: null,
      start: old_start_timestamp,
      end: old_end_timestamp,
      agoValue: old_agoValue
    }, () => {
      this.setTimer()
    })
  }
  // 设置时间选择按钮
  hostCharts = (key = 'out') => {
    const {start, end} = this.state
    return <Select key={key} className="timeAgo" dropdownClassName="timeAgo" value={this.state.agoValue || '1h'} onChange={this.agoChange} style={{width: 120}}>
      <Option value='1h' hours="1">过去的1小时</Option>
      <Option value='2h' hours="2">过去的2小时</Option>
      <Option value='4h' hours="4">过去的4小时</Option>
      <Option value='12h' hours="12">过去的12小时</Option>
      <Option value='24h' hours="24">过去的24小时</Option>
      <Option value='1w' hours="168">过去的一星期</Option>
      <Option value='1m' hours="720">过去的一个月</Option>
      <Option value='1y' hours="8760">过去的一年</Option>
      <Option value='custom'>自定义</Option>
      <Option value='customVal' style={{display: this.state.customVal ? 'block' : 'none'}}>
        <Tooltip placement="bottom" trigger="hover" title={`开始时间: ${start} \r\n结束时间: ${end}\r\n总计时长: ${this.timesFormat(start, end)}`} overlayStyle={{whiteSpace: 'pre'}}>
          {this.state.customVal || null}     
        </Tooltip>
      </Option>
    </Select>
  }
  timesFormat = (start, end) => {
    const times = new Date(end).getTime() - new Date(start).getTime()
    let showTimes
    let m = Math.floor(times / 1000 / 60)
    if(m / 60 >= 1){
      if(m / 60 / 24 >= 1){
        showTimes = `${(m / 60 / 24).toFixed(1)} 天`
      }else{
        showTimes = `${(m / 60).toFixed(1)} 小时`
      }
    }else{
      showTimes = `${m} 分钟`
    }
    return showTimes
  }
  // 选择时间
  rangeOk = (value, str) => {
    const start = value[0].format('YYYY/MM/DD HH:mm')
    const end = value[1].format('YYYY/MM/DD HH:mm')
    this.setState({
      start, end
    })
  }
  // 确认选择图表数据的时间段
  timeAgoSure = async(type) => {
    const {start, end} = this.state
    if(!start || !end || type == 'cancle'){
      this.setState({
        agoValue: this.state.lastAgoVal
      })
      return
    }
    if(start && end){
      const start_timestamp = String(new Date(start).getTime()).substr(0, 10)
      const end_timestamp = String(new Date(end).getTime()).substr(0, 10)

      clearInterval(this.timer)
      this.timer = null

      let setData = {
        agoValue: 'customVal',
        customVal: `自定义: ${start}`,
        chatsLoading: false
      }

      if(this.state.bigChartShow){
        const {curBigChartType} = this.state
        this.showBigChart(curBigChartType)(start_timestamp, end_timestamp)
      }else{
        this.setState({chatsLoading: true})
        const hostStatus = await actions.host.getHostStatus({
          host_name: this.host_name, 
          start: start_timestamp, 
          end: end_timestamp
        })
        this.renderChats(hostStatus)
        this.setTimer(start_timestamp, end_timestamp)
        setData = Object.assign({}, setData, { start_timestamp, end_timestamp })
      }
      this.setState(setData)
    }
  }
  // 设置获取多久之前的数据
  agoChange = async(val, optDom) => {
    const hours = optDom.props.hours
    //             out   4h   obj      4
    // console.log(key, val, optDom, hours)
    // return 
    let start
    let end
    let setData = {
      lastAgoVal: this.state.agoValue, 
      agoValue: val, 
      customVal: null,
      chatsLoading: false
    }

    if(val && hours){
      end = String(new Date().getTime()).substr(0, 10)
      start = end - 3600 * hours
      clearInterval(this.timer)
      this.timer = null
      if(this.state.bigChartShow){
        const { hostStatus} = this.props
        this.showBigChart(this.state.curBigChartType)(start, end)
      }else{
        this.setState({chatsLoading: true})
        const hostStatus = await actions.host.getHostStatus({
          host_name: this.host_name, 
          start, 
          end
        })
        this.renderChats(hostStatus)
        this.setTimer(start, end)
        setData = Object.assign({}, setData, { 
          start_timestamp: start, 
          end_timestamp: end
        })
      }
    }
    this.setState(setData)
  }
  hide = () => {
    this.setState({
      visible: false,
    });
  }

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  }
  downloadToFile = (hostStatus, key, type) => {
    exportFile(hostStatus[key], key, type, Object.keys(hostStatus[key]))
    this.hide()
  }
  render() {
    const {summaryInfo, currentCluster} = this.props
    const {bigChartShow, curModalName, agoValue, chatsLoading,curBigChartType, isloading } = this.state
    const hostStatus = this.state.hostStatus || this.props.hostStatus
    let host_components
    let host_info
    if(summaryInfo){
      host_components = _.cloneDeep(summaryInfo.host_components)
      host_info = summaryInfo.Hosts
      // host_components.push({last: true})
    }else{
      // return <Spin style={{width: '100%', height: '100%'}}/>
    }
    const modalShow = bigChartShow ? 'block' : 'none'
    return (
      <div className="hostSummary clearfix">
        <Spin spinning={isloading || !summaryInfo}>
          <div className="hostSummaryLeft half">
            <Components host_components={host_components} match={this.props.match} currentCluster={currentCluster}/>
            <HostSuammary host_info={host_info}/>
          </div>
          <div className="hostSummaryRight half">
            <Card className="hostDetailCard hostCharts" title="主机指标" extra={this.hostCharts()}>
                <ul className="clearfix">
                  {
                    Object.keys(chartName).map(rec => {
                      return <Tooltip key={rec} placement="left" trigger="hover" title={(!hostStatus || hostStatus.length == 0) ? "没有可用数据, Metrics可能没有安装" : "点击可查看大图"}>
                        <li key={rec} 
                          className={(!hostStatus || hostStatus.length == 0) ? 'echartWrapper noData' : 'echartWrapper'}
                          onClick={this.showBigChart(rec)}
                          >
                          
                          <Spin spinning={isloading || chatsLoading || !hostStatus}>
                            {(!hostStatus || hostStatus.length == 0) && <p className="noDataTitle">没有可用数据</p>}
                            <div className="chart" ref={(el) => this.chartsEl[rec] = el}></div>
                            <p className="chartName download2FileMenu">
                              {rec != 'load' && rec != 'process' ? chartName[rec] + '使用情况' : chartName[rec]}
                              { hostStatus &&
                                <Tooltip placement="bottom" title={<div>
                                  <a href="javascript:;" onClick={this.downloadToFile.bind(this, hostStatus, rec, 'json')}>json</a>
                                  <a href="javascript:;" onClick={this.downloadToFile.bind(this, hostStatus, rec, 'csv')}>csv</a>
                                </div>} getPopupContainer={()=>document.querySelector('.download2FileMenu')}>
                                  <a href="javascript:;" className="downloadDatatoFile">下载</a>
                                </Tooltip>
                              }
                            </p>
                          </Spin>
                        </li>
                      </Tooltip>
                    })
                  }
                </ul>
            </Card>
          </div>
          <Modal
            title={curModalName}
            visible={true}
            style={{display: modalShow}}
            bodyStyle={{display: modalShow}}
            maskStyle={{display: modalShow}}
            wrapClassName={`hostWrapClassName echartWrapper ${modalShow}`}
            onOk={this.hideBigChart}
            onCancel={this.hideBigChart}
            okText="确认"
            cancelText=""
            width={800}
          >
            <div className="chooseTime download2FileMenu" style={{margin: '0 auto'}}>{this.hostCharts('modal')}
              <Tooltip placement="bottom" title={<div>
                <a href="javascript:;" onClick={this.downloadToFile.bind(this, hostStatus, curBigChartType, 'json')}>json</a>
                <a href="javascript:;" onClick={this.downloadToFile.bind(this, hostStatus, curBigChartType, 'csv')}>csv</a>
              </div>} getPopupContainer={()=>document.querySelector('.download2FileMenu')}>
                <a href="javascript:;" className="downloadDatatoFile fr">下载</a>
              </Tooltip>
            </div>
            <Spin spinning={this.state.bigChartLoading}>
              <div className="chart" ref={(el) => this.bigChart = el }></div>
            </Spin>
          </Modal>
          <Modal
          title='选择时间范围'
          visible={agoValue === 'custom'}
          onOk={this.timeAgoSure}
          onCancel={this.timeAgoSure.bind(this, 'cancle')}
          okText="确认"
          cancelText="取消"
          >
            <p>请选择起止时间</p>
            <RangePicker
              showTime={{ format: 'HH:mm' }}
              format="YYYY-MM-DD HH:mm"
              placeholder={['开始时间', '结束时间']}
              onChange={this.rangeChange}
              onOk={this.rangeOk}
            />
          </Modal>
        </Spin>
      </div>
    )
  }
}

const ConnSummary = connect(state=>{
  return{
    summaryInfo: state.host.summaryInfo,
    hostStatus : state.host.hostStatus,
    currentCluster: state.app.currentCluster
  }
})(Summary)

export default ConnSummary