// 获取主机详细信息
const getSummaryInfo = (data) => {
  return request({
    url: '/platform/ForwardTo',
    data: {
      url: `/api/v1/clusters/${data.cluster_name}/${SummaryUrl}`,
      clusterName: data.cluster_name,
      method: 'get',
      params: JSON.stringify({
        "RequestInfo": {
            "query": "Hosts/host_name.in(" + data.host_name + ")"
        }
      })
    }
  })
}
// 获取主机指标
const getHostStatus =  (data = {}) => {
  return request({
    url: '/platform/ForwardTo',
    data: {
      url: `/api/v1/clusters/${data.cluster_name}/hosts/${data.host_name}?fields=${encodeURIComponent(formatData(data.start, data.end))}`,
      clusterName: data.cluster_name,
      method: 'get',
    }
  })
}
function formatData(start, end){
  end = (!end || typeof +end != 'number') ? +String(new Date().getTime()).substr(0,10) : end
  start = (start && typeof +start == 'number') ? String(start).substr(0, 10) : end - 60 * 60
  return `metrics/cpu/cpu_user[${start},${end},15],metrics/cpu/cpu_wio[${start},${end},15],metrics/cpu/cpu_nice[${start},${end},15],metrics/cpu/cpu_aidle[${start},${end},15],metrics/cpu/cpu_system[${start},${end},15],metrics/cpu/cpu_idle[${start},${end},15],metrics/disk/disk_total[${start},${end},15],metrics/disk/disk_free[${start},${end},15],metrics/load/load_fifteen[${start},${end},15],metrics/load/load_one[${start},${end},15],metrics/load/load_five[${start},${end},15],metrics/memory/swap_free[${start},${end},15],metrics/memory/mem_shared[${start},${end},15],metrics/memory/mem_free[${start},${end},15],metrics/memory/mem_cached[${start},${end},15],metrics/memory/mem_buffers[${start},${end},15],metrics/network/bytes_in[${start},${end},15],metrics/network/bytes_out[${start},${end},15],metrics/network/pkts_in[${start},${end},15],metrics/network/pkts_out[${start},${end},15],metrics/process/proc_total[${start},${end},15],metrics/process/proc_run[${start},${end},15]`
}
// 获取主机组件信息(client)
const getAvailableComponentOnVmnode = (data) => {
  return request({
    url: '/platform/getAvailableComponentOnVmnode',
    data
  })
}
// 启动停止主机下(所有)组件
const startOrStopComponents = (data) => request({
  url: '/platform/ForwardTo',
  data: {
    url: data.fromHost ? `/api/v1/clusters/${data.clusterName}/hosts/${data.host_name}/host_components` : `/api/v1/clusters/${data.clusterName}/host_components`,
    clusterName: data.clusterName,
    method: 'put',
    params: JSON.stringify({
      "RequestInfo": {
        "context": data.context,
        "operation_level": {
            "level": data.level,
            "cluster_name": data.clusterName,
            "host_names": data.host_name || ''
        },
        "query": data.query
      },
      "Body": {
        "HostRoles": {
          "state": data.state
        }
      }
    })
  },
  method: 'get'
})
// 重启已选择主机上的全部组件
const restartAllComponents = (data) => request({
  url: '/platform/ForwardTo',
  data: {
    url: `/api/v1/clusters/${data.clusterName}/requests`,
    clusterName: data.clusterName,
    method: 'post',
    params: JSON.stringify({
      "RequestInfo": {
        "command": "RESTART",
        "context": data.context || '重启已选择主机上的全部组件',
        "operation_level": data.context ? '' : {
            "level": 'HOST',
            "cluster_name": data.clusterName
        },
      },
      "Requests/resource_filters": data.filters
    })
  },
  method: 'post'
})
// 打开关闭已选择主机的维护模式
const openOrCloseMaintenance = (data) => request({
  url: '/platform/ForwardTo',
  data: {
    url: `/api/v1/clusters/${data.clusterName}/hosts`,
    clusterName: data.clusterName,
    method: 'PUT',
    params: JSON.stringify({
      "RequestInfo": {
        "context": data.context,
        "query": `Hosts/host_name.in(${data.hostsStr})`
      },
      "Body": {
        "Hosts": {
          "maintenance_state": data.isOpen //'OFF'
        }
      }
    })
  },
  method: 'get'
})
const getClusterVmnodesWithComponents = (data) => {
  return request({
    url: '/platform/getClusterVmnodesWithComponents',
    data
  })
}
// 开启/关闭单个服务
const startComponents = (data) => {
  return request({
    url: '/platform/ForwardTo',
    data: {
      url: `/api/v1/clusters/${data.cluster_name}/hosts/${data.host_name}/host_components/${data.component_name}`,
      clusterName: data.cluster_name,
      method: 'put',
      params: JSON.stringify({
        "RequestInfo": {
          "context": data.text,
          "operation_level": {
            "level": "HOST_COMPONENT",
            "cluster_name": data.cluster_name,
            "host_name": data.host_name,
            "service_name": data.service_name
          }
        },
        "Body": {
          "HostRoles": {
            "state": data.state == 'INSTALLED' ? 'STARTED' : 'INSTALLED'
          }
        }
      })
    }
  })
}
// 重启服务
const restartComponents = (data) => {
  return request({
    url: '/platform/ForwardTo',
    method: 'post',
    data: {
      url: `/api/v1/clusters/${data.cluster_name}/requests`,
      clusterName: data.cluster_name,
      method: 'post',
      params: JSON.stringify({
            "RequestInfo": {
            "command": "RESTART",
            "context": data.text,
            "operation_level": {
                "level": data.level || "HOST_COMPONENT",
                "cluster_name": data.cluster_name,
                "service_name": data.service_name,
                "hostcomponent_name": data.component_name
            }
        },
        "Requests/resource_filters": data.filter || [{
            "service_name": data.service_name,
            "component_name": data.component_name,
            "hosts": data.host_name
        }]
      })
    }
  })
}
// 打开关闭选中服务的维护模式
const openOrCloseMaintenanceState = (data) => {
  return request({
    url: '/platform/ForwardTo',
    data: {
      url: `/api/v1/clusters/${data.cluster_name}//hosts/${data.host_name}/host_components/${data.component_name}`,
      clusterName: data.cluster_name,
      method: 'put',
      params: JSON.stringify({
      "RequestInfo": {
        "context": data.text
      },
      "Body": {
          "HostRoles": {
              "maintenance_state": data.text.indexOf('打开') >= 0 ? "ON" : 'OFF'
          }
      }
      })
    }
  })
} 
