// 重新安装所有失败组件
const installFailedComponents = (data) => request({
  url: '/platform/ForwardTo',
  data: {
    url: `/api/v1/clusters/${data.clusterName}/host_components`,
    clusterName: data.clusterName,
    method: 'post',
    params: JSON.stringify({
      "RequestInfo": {
        "context": data.context,
        "operation_level": {
            "level": 'CLUSTER',
            "cluster_name": data.clusterName
        },
        "query": `HostRoles/host_name.in(${data.hostsStr})&HostRoles/state=INSTALL_FAILED`
      },
      "Body": {
        "HostRoles": {
          "state": "INSTALLED"
        }
      }
    })
  },
  method: 'get'
})






// 添加服务
