// 停用服务
const stopService = (data) => request({
  url: '/platform/ForwardTo',
  data: {
    url: `/api/v1/clusters/${data.clusterName}/requests`,
    clusterName: data.clusterName,
    method: 'PUT',
    params: JSON.stringify({
      "RequestInfo": {
        "context": "停用DataNode",
        "command": "DECOMMISSION",
        "parameters": {
            "slave_type": "DATANODE",
            "excluded_hosts": ""
        },
        "operation_level": {
            "level": "CLUSTER",
            "cluster_name": "LEAP"
        }
      },
      "Requests/resource_filters": [{
          "service_name": "HDFS",
          "component_name": "NAMENODE"
      }]
    })
  },
  method: 'get'
})

