const getVersions = () => request({
  url: versionUrl,
  method: 'get'
})



const operateComponent = (data) => request({
  url: '/api/v1/clusters/LEAP/host_components',
  data: {
    RequestInfo: {
      context: data.context,
      operation_level: {
        level: data.level, 
        cluster_name: data.cluster_name
      },
      query: `(HostRoles/component_name.in(APP_TIMELINE_SERVER,DATANODE,ELASTICSEARCH,FLUME_HANDLER,HBASE_MASTER,HBASE_REGIONSERVER,HISTORYSERVER,IMPALA_DAEMON,KAFKA_BROKER,KIBANA_SERVER,LOGSEARCH_LOGFEEDER,LOGSTASH_AGENT,METRICS_MONITOR,NAMENODE,NFS_GATEWAY,NODEMANAGER,RESOURCEMANAGER,SQLS,ZOOKEEPER_SERVER)&HostRoles/host_name=demo1.leap.com)`,
    },
    Body: {HostRoles: {state: data.state}}
  },
  method: 'put',
  isDataString: 1
})

