import React, { Component } from 'react'
import { connect, actions } from 'mirrorx'
import { Table, Icon, Tooltip, Modal, Button, Checkbox } from 'antd'
import styled from 'styled-components'
import {getCOmponentStatus} from 'api/monitor'

import WidgetInfo from '../../components/monitor/widgetInfo'

class ComponentStatus extends Component {
  constructor(props) {
    super(props)
    this.state = {
      alerts: null,
    }
    this.currentClusterName = null
    this.defaultDevToolsList = ['DATA HUB', 'User Admin', 'Task Scheduler', 'SQL Editor']
  }

  componentWillReceiveProps(props){
    if(props.currentCluster && props.currentCluster.clusterName != this.currentClusterName){
      this.currentClusterName = props.currentCluster.clusterName
      this.componentWillMount()
    }
  }


  async componentWillMount() {

    let {currentCluster, serviceList, currentType} = this.props
    this.setState({
      loading: true
    })
    const clusterName = currentCluster.clusterName
    if(!serviceList || serviceList.length == 0){
      await actions.app.getAllService()
      serviceList = this.props.serviceList
    }
    getCOmponentStatus({clusterName})
    .then(res => {
      if(res.code == 1){
        const alerts = res.data.items
        let formatAltersData = {}
        let needData = []
        alerts.map(rec => {
          if(formatAltersData[rec.Alert.service_name]){
            formatAltersData[rec.Alert.service_name].push(rec.Alert)
          }else{
            formatAltersData[rec.Alert.service_name] = [rec.Alert]
          }
        })
        serviceList.map(rec => {
          if(rec.ServiceInfo.state == 'STARTED' || rec.ServiceInfo.state == 'INSTALLED'){
            needData.push({
              ...rec.ServiceInfo,
              version: rec.StackServices.service_version,
              alerts: formatAltersData[rec.service_name],
              display_name: rec.display_name
            })
          }
        })
        if(currentType){
          needData = needData.filter(rec => this.defaultDevToolsList.includes(rec.display_name))
        }else{
          needData = needData.filter(rec => !this.defaultDevToolsList.includes(rec.display_name))
        }
        this.setState({
          usefulData: needData,
          loading: false
        })
      }
    })
    // http://172.17.171.73:8080&_=1530691581325

  }
  columns1 = () => {
    return [{
      title: '名称',
      dataIndex: 'display_name',
    }, {
      title: '状态',
      dataIndex: 'maintenance_state',
      render: (text, record) => {
        let state = 'check' 
        let _text = '服务已启动'
        if(record.state == 'INSTALLED'){
          state = 'close'//'exclamation'
          _text = '服务已停止'
        }
        if(record.state == 'INIT' || (record.state == 'STARTED' && record.alerts)){
          _text = record.state == 'INIT' ? '服务待安装' : '服务有异常'
          state = 'exclamation'
        }
        const style = {
          'border': '1px solid',
          'padding': '3px 7px',
          'borderRadius': '6px',
          'fontWeight': 'bold',
          'fontSize': '12px'
        }
        
        return <div className='monitor_componentState'>
          <Tooltip placement="right" title={_text}>
            <label className={state} style={style}>{record.state.toLowerCase()}</label>
          </Tooltip>  
        </div>
      }
    }, {
      title: '版本',
      dataIndex: 'version',
    }, {
      title: '告警',
      dataIndex: 'alerts',
      render: (text) => {
        const len = text ? text.length : null
        return <div className="monitor_componentAlert">
          <span onClick={this.alertInfo.bind(this, text)} className={len ? 'error' : 'success'}>{len ? `${len} ${len > 1 ? 'alerts' : 'alert'}` : '没有告警'}</span>
        </div>  
      }
    }, {
      title: '操作',
      dataIndex: 'state',
      render: (text, record) => (
        <a href="javascript:;" onClick={this.reStartService.bind(this, record)}>
          <Icon type="poweroff" className="mr10 ml25" />
          <span>重新启动</span>
        </a>
      )
    }]
  }
  alertInfo = alerts => {
    if(alerts && alerts.length > 0){
      this.setState({
        alerts
      })
    }
  }
  reStartService = record => {
    this.setState({
      operateData: record
    })
  }
  sureModal = async () => {
    const {operateData} = this.state
    const filter = operateData.alerts && operateData.alerts.map(rec => {
      return {
        "service_name": rec.service_name,
        "component_name": rec.component_name,
        "hosts": rec.host_name
      }
    })
    this.setState({
      modalloading: true
    })
    await actions.monitor.restartComponents({
      text: `重启${operateData.display_name}的全部组件`,
      level: 'SERVICE',
      cluster_name: operateData.alerts[0].cluster_name,
      service_name: operateData.service_name,
      filter
    }).then(res => {
      if(res.code == 1){
        actions.host.updateState({
          taskModalVisible: true
        })
        this.setState({
          modalloading: false,
          operateData: null
        })
      }
    }).catch(err => {
      this.setState({
        modalloading: false
      })
    })
  }
  handleCancel = () => {
    this.setState({
      alerts: null,
      operateData: null
    })
  }
  getTimeAgo = (latest_timestamp, original_timestamp) => {
    let ago = ''
    const stateChangeTime = new Date(original_timestamp).getTime()
    const lastChooseTime = new Date(latest_timestamp).getTime()
    const howDays = Math.ceil((lastChooseTime - stateChangeTime) / (60 * 60 * 1000 * 24)) // 多少天
    const y = (howDays / 365).toFixed(1)
    const m = Math.floor(howDays / 30)
    if(y >= 1){
      ago = `for  ${y > 1 ? y + ' years' : ' about a year'} `
    } else if(m >= 1) {
      ago = `for ${m > 1 ? m + ' months' : ' about a month'} `
    } else if(howDays >= 1) {
      ago = `for ${howDays > 1 ? howDays + ' days' : ' about a day'} `
    }
    return ago
  }
  render() {
    let dataSource = this.state.usefulData
    const {alerts, loading, operateData, modalloading} = this.state


    return <div className="devToolWrap">
      <WidgetInfo />
      {/* <div className="flexEnd m10">
        <Button icon="file-text">查看配置历史</Button>
      </div> */}
      <Table loading={loading} bordered={true} pagination={false} dataSource={dataSource} columns={this.columns1()} rowKey={(r) =>r.service_name}/>

      {
        alerts && alerts.length > 0 && 
        <Modal
          title="告警信息"
          visible={true}
          wrapClassName="alertsModal"
          onCancel={this.handleCancel}
          footer={
            <Button type="primary" onClick={this.handleCancel}>取消</Button>  
          }
        >
          {
            alerts && alerts.map(rec => {
              return <li key={rec.id} className="alertsModalItem">
                <div className="alertsModalLeft">
                  <h4>{rec.label}</h4>
                  <p>{rec.text}</p>
                </div>
                <div className="alertsModalRight">
                  {/* repeat_tolerance_remaining */}
                  <span className={rec.state}>{rec.state}</span>
                  <label>{this.getTimeAgo(rec.latest_timestamp, rec.original_timestamp)}</label>
                </div>
              </li>
            })
          }
        </Modal>
      }
      {
        operateData && 
        <Modal
          title="操作"
          visible={true}
          wrapClassName="operateModal"
          onCancel={this.handleCancel}
          footer={
            <Button type="primary" loading={modalloading} onClick={this.sureModal}>确认</Button>  
          }
        >
          {/* <p>您要重新启动 {operateData.display_name}</p>
          <p>由于服务重启，此操作将触发告警。要避免告警，请在重启所有服务之前，为{operateData.display_name}打开维护模式。</p>
          <Checkbox>为{operateData.display_name}打开维护模式</Checkbox> */}
          <p>确认重新启动 {operateData.display_name}? 该操作会触发告警, 可设置维护模式取消告警.</p>
        </Modal>
      }
    </div>
  }
}
/*
您要重新启动 Leap Infra

由于服务重启，此操作将触发告警。要避免告警，请在重启所有服务之前，为Leap Infra打开维护模式。

*/
const ConnComponentStatus = connect(state => ({
  serviceList: state.app.serviceList,
  currentCluster: state.app.currentCluster
}))(ComponentStatus)
export default ConnComponentStatus