import React, {Component} from 'react'
import styled from 'styled-components'

let echarts = require('echarts/lib/echarts')
require('echarts/lib/chart/pie')
require('echarts/lib/component/title')

const ComponentChartWrap = styled.div`
    display:flex;
    height:140px;
    align-items:center;
    justify-content:center;
`

export default class ComponentInfoChart extends Component {
    constructor(props) {
        super(props)
        this.setPieOption = this.setPieOption.bind(this)
        this.initPie = this.initPie.bind(this)
    }

    initPie() {
        const {data} = this.props
        let myChart = echarts.init(this.pieReact)
        let options = this.setPieOption(data)
        myChart.setOption(options)
    }

    componentDidMount() {
        this.initPie()
    }
    componentDidUpdate() {
        this.initPie()
    }
    setPieOption(data) {
        return {
            legend: {
                orient: 'vertical',
                x: 'right',
                data:['内存占比','待升级组件','组件数量']
            },
            series: [
                {
                    name: '访问来源',
                    type: 'pie',
                    radius: '80%',
                    center: ['50%', '50%'],
                    data: [
                        {value: 125, name: '内存占比'},
                        {value: 135, name: '待升级组件', selected: true},
                        {value: 335, name: '组件数量'},
                    ],
                    label: {
                        normal: {
                            show:false,
                        }
                    },
                }
            ],
            color: ['#0caf33', '#ff9b00', "#006cff"]
        }
    }

    render() {
        return (
            <ComponentChartWrap className="pie-react">
                <div ref={(c) => this.pieReact = c} style={{width: "400px", height: "140px"}}/>
            </ComponentChartWrap>
        )
    }
}