import React, { Component } from 'react'
import {Table, Select, Input, Spin, Tooltip, Button} from 'antd'
import { connect, actions} from 'mirrorx'
import moment from 'moment'
import './index.less'
const Option = Select.Option
class Alerts extends Component {
  constructor(props){
    super(props)
    this.state = {
      filterValues: {}
    }
  }
  async componentDidMount(){
    const host_name = this.props.match.path.split('/')[2]
    await actions.host.getAlertsLists({hostName: host_name})
  }

  filterValueChange = (key, value) => {
    const val = typeof value == 'object' ? value.target.value : value
    const {filterValues} = this.state
    if(val !== 'all'){
      filterValues[key] = val
    }else{
      delete filterValues[key]
    }
    actions.host.filterAlertsList(filterValues)
    this.setState({
      filterValues
    })
  }

  columns = () => {
    const {alertFilter} = this.props
    const {filterValues} = this.state
    return [{
      title: <span className="tableHeaderOperate">服务: 
        <Select allowClear value={filterValues['service_name']|| 'all'} onChange={this.filterValueChange.bind(this, 'service_name')}>
          <Option value='all'>全部</Option>
          {
            alertFilter && alertFilter.server.map(rec => {
              return <Option value={rec} key={rec}>{rec}</Option>
            })
          }
        </Select></span>,
      dataIndex: 'service_name',
      width: 160
    }, {
      title: <span className="tableHeaderOperate">告警定义名称: 
        <Input placeholder='Any' value={filterValues['fuzzy_definition_name'] || ''} onChange={this.filterValueChange.bind(this,'fuzzy_definition_name')}/></span>,
      dataIndex: 'label',
      width: 360,
      render: (record) => <a href="#">{record}</a>
    }, {
      title: <span className="tableHeaderOperate">状态: 
        <Select allowClear value={filterValues['state'] || 'all'} onChange={this.filterValueChange.bind(this, 'state')}>
          <Option value="all">全部</Option>
          <Option value="OK">OK</Option>
          <Option value="warning">WARNING</Option>
          <Option value="CRITICAL">CRITICAL</Option>
          <Option value="UNKONWON">UNKONWON</Option>
        </Select></span>,
      key: 'original_timestamp',
      width: 240,
      render: (record) => {
        let ago = ''
        const stateChangeTime = new Date(record.original_timestamp).getTime()
        const lastChooseTime = new Date(record.latest_timestamp).getTime()
        const howDays = Math.ceil((lastChooseTime - stateChangeTime) / (60 * 60 * 1000 * 24)) // 多少天
        const y = (howDays / 365).toFixed(1)
        const m = Math.floor(howDays / 30)
        if(y >= 1){
          ago = `for  ${y > 1 ? y + ' years' : ' about a year'} `
        } else if(m >= 1) {
          ago = `for ${m > 1 ? m + ' months' : ' about a month'} `
        } else if(howDays >= 1) {
          ago = `for ${howDays > 1 ? howDays + ' days' : ' about a day'} `
        }
        const stateTime = moment(new Date(record.original_timestamp)).format('YYYY/MM/DD hh:mm')
        const lastTime = moment(new Date(record.latest_timestamp)).format('YYYY/MM/DD hh:mm')
        return <div>
          <label className={`statusBtn ${record.state}`}>{record.state.length > 4 ? record.state.substring(0,4) : record.state}</label>
          <Tooltip placement="top" title={<span>状态改变: {stateTime}<br/>最后选中: {lastTime}</span>}>
            {ago}
          </Tooltip>
        </div>
      }
    }, {
      title: <span className="tableHeaderOperate">响应: 
        <Input placeholder='Any' value={filterValues['fuzzy_text'] || ''} onChange={this.filterValueChange.bind(this,'fuzzy_text')}/></span>,
      dataIndex: 'text',
      render: record => {
        return <p className="alertContent" title={record}>{record}</p>
      }
    }]
  }

  render() {
    const {alertsList} = this.props
    if(!alertsList){
      return <Spin style={{width: '100%', height: '100%'}}/>
    }
    return (
      <div className="hostAlerts">
        <Table dataSource={alertsList} columns={this.columns()} rowKey={record => record.id}/>
      </div>
    )
  }
}

export {Alerts}
export default connect(state => ({
  alertsList : state.host.alertsList,
  alertFilter: state.host.alertFilter
}))(Alerts)
