import React, { Component } from 'react'
import { Form, Input, Button, Icon, Checkbox, message} from 'antd'
import Header from 'components/loginHeader'
import RegisterPic from 'assets/images/common/register_bg.png'
import axios from 'axios'
import './index.less'
const FormItem = Form.Item

class Regist extends Component {
  constructor(props){
    super(props)
    this.state = {}
  }
  // 注册
  handleSubmit = (e) => {
    e.preventDefault()
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const {username, password} = values
        this.setState({
          submitLoading: true
        })
        axios.post('/user/register', {username, password, nickname: 'nickname'})
        .then(rec => {
          this.setState({
            submitLoading: false
          })
          if(rec.data.code == 1){
            this.props.history.push("/signIn")
          }
        })
        .catch(err => {
          this.setState({
            submitLoading: false
          })
          this.props.form.setFields({
            password: {
              value: password,
              errors: [new Error('登录失败, 请稍候再试')]
            },
          })
        })
      }
    })
  }
  // 获取验证码
  getVCode = () => {
    let username = this.props.form.getFieldsValue().username
    let vCode = this.props.form.getFieldsValue().vCode || ''
    if(!username){
      this.props.form.validateFields(['username'])
    }else{
      this.setState({
        getVCodeLoading: true
      })
      axios.post('/user/getMailVcode', {username, forCreate: true})
      .then(res => {
        this.setState({
          getVCodeLoading: false
        })
        if(res.data.code === 1 || res.data.message === 'Code has been send'){
          // this.props.form.setFields(
          this.setState({
            vCodeBtnDisable: true
          })
        }
        if(res.data.code !== 1){
          const message = res.data.message == 'Used mail address' ? '邮箱已经被使用过' : '验证码已经发送过(可查看最近 30 分钟内的邮件)'
          const keys = res.data.message == 'Used mail address' ? 'username' : 'vCode'
          this.props.form.setFields({
            [keys]: {
              value: keys == 'username' ? username : vCode,
              errors: [new Error(message)]
            },
          })
        }
      })
      .catch(err => {
        this.setState({
          getVCodeLoading: false
        })
        this.props.form.setFields({
          username: {
            value:  username,
            errors: [new Error('验证码获取失败, 请稍候再试')]
          },
        })
      })
    }
  }
  // 密码验证
  checkThePwd = (rule, value, callback) => {
    const reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/
    if(! reg.test(value)){
      callback('请输入8~12位数字字母组成的密码')
    }
    callback()
    return
  }
  // 必须勾选已阅读隐私协议
  checkCheckbox = (rule, value, callback) => {
    if(!value){
      callback('请确认已阅读隐私协议')
    }
    callback()
    return
  }
  // 验证 验证码是否正确
  checkVCode = (rule, value, callback) => {
    let username = this.props.form.getFieldsValue().username
    if(username && value && value.length === 4){
      axios.post('/user/checkVcode',{username, vcode: value}).then(rec => {
        console.log(rec)
        if(rec.data.code == 1){
          callback()
        }else{
          callback('验证码输入有误')
        }
      })
      return
    }
    callback() //'请输入正确的验证码'
    return
  }
  render() {
    const { getFieldDecorator } = this.props.form
    const {vcode, errInfo, vCodeBtnDisable, getVCodeLoading, submitLoading} = this.state
    return (
      <div className="register">
        <Header />
        <div className="registerContent">
          <div className="rigisterWrapper">
            <div className="leftContent fl">
              <h2>注册</h2>
              <Form onSubmit={this.handleSubmit}>
                <FormItem >
                    {getFieldDecorator('username', {
                        rules: [{ 
                          required: true, 
                          message: '请输入正确的邮箱地址',
                          type: 'email'
                        }],
                        validateFirst: true
                    })(
                        <Input placeholder="请输入邮箱" prefix={"邮  箱:"}/>
                    )}
                </FormItem>
                <FormItem className="registervCode">
                    {getFieldDecorator('vCode', {
                        rules: [{ 
                          required: true, 
                          message: '请输入验证码'
                        }, { validator: this.checkVCode }],
                    })(
                        <Input placeholder="请输入验证码" prefix={"验 证 码: "} className={getVCodeLoading && 'width240'}/>
                    )}
                    <Button loading={ getVCodeLoading} title={vCodeBtnDisable ? '验证码已发送' : '发送验证码'} disabled={vCodeBtnDisable} onClick={this.getVCode}>获取验证码</Button>
                </FormItem>
                <FormItem>
                    {getFieldDecorator('password', {
                        rules: [{ 
                          required: true, 
                          message: '请输入8~12位数字字母组成的密码' 
                        },{ validator: this.checkThePwd }],
                    })(
                        <Input type="password" placeholder="请输入密码" prefix={"新 密 码: "}/>
                    )}
                </FormItem>
                <FormItem>
                    {getFieldDecorator('rePassword', {
                        rules: [{ 
                          required: true, 
                          message: '请输入正确的密码'
                        },{ validator: this.checkThePwd }],
                    })(
                        <Input type="password" placeholder="请确认密码" prefix={"确认密码: "}/>
                    )}
                </FormItem>
                <FormItem>
                  {getFieldDecorator('remember',{
                    valuePropName: 'checked',
                    initialValue: true,
                    rules: [{ validator: this.checkCheckbox }]
                  })(
                    <Checkbox>阅读并勾选</Checkbox>
                  )}
                  <label>《<a href="#">隐私协议</a>》</label>
                </FormItem>
                <Button loading={submitLoading} type="primary" htmlType="submit" className="registerBtn">注册</Button>
              </Form>
              <p className="hasAccount"><Icon type="left" /> 已有账户, 点击<a href="/signIn">登录</a></p>
            </div>
            <div className="rightPic fl">
              <img src={RegisterPic} alt="注册"/>
            </div>
          </div>
          
        </div>
      </div>
    )
  }
}
const Register = Form.create()(Regist)
export default Register