import React, { Component } from 'react'
import {Button, Steps,Input, Radio, Select,Table, InputNumber} from 'antd'
// import Select
import Configuration from './configuration'
import InstallLeapComponent from './installLeapComponents'
import InstallDevToos from './installDevTools'
import CreateSuccess from './success'
import ChooseHost from './chooseHost'
import ChooseSlave from './chooseSlave'
import './index.less'
const Step = Steps.Step
const RadioGroup = Radio.Group
const Option = Select.Option
const RadioButton = Radio.Button
export default class Create extends Component {
  constructor(props){
    super(props)
    this.state = {
      currentStep: 0
    }
    this.tipsObj = {}
  }
  stepHandle = (value, derection, keys) => {
    let {currentStep} = this.state
    currentStep = currentStep + derection
    document.querySelector('.createstepWrapper').scrollTop = 0
    value = value || this.state[keys]
    this.setState({currentStep, [keys]: value })
  }
  render() {
    const {currentStep, config, installComponent, installDevTools, master, slave} = this.state
    const steps = [
      '配置服务器',
      '安装组件',
      '安装开发工具',
      '指派Master',
      '指派slave和client',
      '创建完成'
    ]
    return (
      <div className="createLeapPlatForm">
        <div className="createClusterInner">
          <h1 className="createHeader">创建LEAP平台 {/*<Button icon="like-o">一键配置</Button>*/}</h1>
          <div className="createstepWrapper">
            <StepsComponent className="createSteps" currentStep={currentStep || 0} steps={steps}>
              { (step, index) => <Step key={index} title={step} icon={<i>{index + 1}</i>}/> }
            </StepsComponent>
            <div className="createClusterScorllWrapper">
              {currentStep == 0 && <Configuration value={config} onsubmit={this.stepHandle}/> }
              {currentStep == 1 && <InstallLeapComponent value={installComponent} onsubmit={this.stepHandle}/>}
              {currentStep == 2 && <InstallDevToos value={installDevTools} components={installComponent} onsubmit={this.stepHandle}/>}
              {currentStep == 3 && <ChooseHost value={master} onsubmit={this.stepHandle}/>}
              {currentStep == 4 && <ChooseSlave value={slave} master={master} clustername={config && config._clusterName} onsubmit={this.stepHandle}/>}
              {currentStep == 5 && <CreateSuccess title="创建已完成" pathTo="/"/>}
            </div>
          </div>
        </div>
      </div>
    )
  }
}

function StepBtn(props){
  return( 
    <div>
      <Button type="primary" htmlType="submit" className="login-form-button">下一步</Button>
      { props.step > 0 && <Button type="primary" className="login-form-button" onClick={props.onPrev}>上一步</Button>}
    </div>
  )
}

function StepsComponent(props){
  return (<Steps current={props.currentStep} className={props.className}>
    {props.steps.map((rec, index) => props.children(rec, index))}
  </Steps >)
}