import mirror,{actions} from "mirrorx"
import * as API from 'api/createCluster'

const neednt = ['STORM', 'LOGSEARCH']
mirror.model({
  name: 'create',
  initialState: {
    clusterTypes     : null,      // 可选服务器实例
    backupData       : null,      // 可选服务器实例备份
    serviceList      : null,      // 组件列表
    hostList         : null,      // 创建完成后生产的主机信息
    masterList       : null,      // 可指派的master列表
    slaveList        : null,      // 可指派的slave和client列表
    devTools         : null,      // 开发工具列表
    clusterFilterList: null,      // 筛选数据列表
    createStatus     : null,      // 创建集群的状态
  },
  reducers: {
    updateState(state, data) {
      return {
        ...state, ...data
      }
    }
  },
  effects: {
    // 获取集群规格
    async getClusterType(data, getState){
      const {updateState} = actions.create
      return await API.getClusterTypes()
      .then(res => {
        if(res.code == 1){
          let types = res.data
          updateState({
            clusterTypes: types,
            createStatus: null
          })
          return types
        }
        return null
      })
    },
    // 获取服务组件列表
    async getService(data, getState){
      const {updateState} = actions.create
      const {hostList} = getState().create
      return await API.getService().then(res => {
        if(res.code === 1){
          const requestData = res.data
          let serviceList = []
          let devTools    = []
          requestData && requestData.map(rec => {
            console.log(rec)
            if(neednt.includes(rec.name)){
              return 
            }
            if(defaultDevToolsList.includes(rec.displayName)){
              devTools.push(rec)
            }else{
              // if(rec.displayName != 'Kerberos'){
              serviceList.push(rec)
              // }
            }
          })
          updateState({
            serviceList,
            devTools,
            allServiceData: requestData
          })
          return Promise.resolve(true)
        }
      })
    },
    // 保存主机列表数据
    setHostList(data){
      const {updateState} = actions.create
      updateState({
        hostList: data
      })
    },
    // 获取服务下的组件
    getComponents (data, getState) {
      const {updateState} = actions.create
      const {allServiceData, hostList} = getState().create
      let masterList  = []
      let slaveList   = {}

      allServiceData && allServiceData.map(rec => {
        if(rec.components){
          data && Object.keys(data).map(key => {
            if(key === rec.name){
              rec.components.map(record => {
                record.serverName = key
                if(record.category === 'MASTER'){
                  masterList.push(record)
                }
                
                if(record.category === 'CLIENT' || record.category === 'SLAVE'){
                  if(record.displayName !== 'ZKFailoverController'){
                    record.times = record.cardinality
                    slaveList[record.name] = record
                  }
                }
              })
            }
          })
        }
      })
      updateState({
        masterList,
        slaveList
      })
    },
    // 提交需安装的服务列表 postServiceList
    async postServiceList (data, getState) {
      const {updateState, checkCreateStatus} = actions.create
      const {allServiceData} = getState().create
      return await API.postServiceList(data).then(res => {
        // console.log(res)
        if(res.code == 1){
          // return Promise.resolve(res.data)
          return checkCreateStatus(data.clustername)
        }
      })
    },

    // 再次提交需安装的服务列表 postServiceList
    async postReServiceList (data, getState) {
      const {updateState} = actions.host
      delete data.noCheck
      return await API.postReServiceList(data).then(res => {
        if(res.code == 1){
          updateState({taskModalVisible: true})
          return res
        }
      })
    },
    
    checkCreateStatus(clustername, getstate) { 
      const {checkCreateStatus, updateState} = actions.create
      const {createStatus} = getstate().create
      return API.checkCreateStatus({clustername})
      .then(res => {
        if((res.data && res.data != 'SUCCESS' && res.data.indexOf('FAILED') < 0) || Object.prototype.toString.call(res.data) == "[object Null]"){
          setTimeout(function(){
            checkCreateStatus(clustername)
          }, 3000)
        }

        updateState({
          createStatus: res.data || ''
        })
        return res
      }).catch(err => {
        updateState({
          createStatus: "FAILED"
        })
      })
    },

    checkAddingStatus(clustername, getstate) { 
      const {checkAddingStatus, updateState} = actions.create
      const {createStatus} = getstate().create
      return API.checkAddingStatus({clustername})
      .then(res => {
        if(res.data && res.data != 'SUCCESS' && res.data.indexOf('FAILED') < 0){
          setTimeout(function(){
            checkAddingStatus(clustername)
          }, 3000)
        }

        updateState({
          createStatus: res.data || ''
        })
        return res
      }).catch(err => {
        updateState({
          createStatus: "FAILED"
        })
      })
    },
  }
})

const defaultDevToolsList = ['DATA HUB', 'User Admin', 'Task Scheduler', 'SQL Editor']