import mirror, {actions} from 'mirrorx'
import * as API from "api/security"
import * as APIComponent from "api/components"

mirror.model({
    name: 'security',
    initialState: {
        status: true, // 加载状态
        serviceAccount: [],
        licenseData: [],
    },
    reducers: {
        updateState(state, data) {
            return {
                ...state, ...data
            }
        },

        updateServiceAccount(state, data) {
            return {
                ...state,
                ...data
            }
        },

        updateLicenseData(state, data) {
            return {
                ...state,
                ...data
            }
        },

    },
    effects: {
        // 获取服务用户及账号
        async getServiceAccount(data, getState) {
            const {updateServiceAccount} = actions.security

            updateServiceAccount({
                status: true
            })

            let {currentCluster} = getState().app

            currentCluster = currentCluster.clusterName
            let installRes = await APIComponent.getComponentsInstalledList(currentCluster)

            let installList = []

            installRes.data.items.forEach(element => {
                installList.push(element.ServiceInfo.service_name)
            })

            installList = installList.join(',')

            let res = await API.getServiceAccount({clusterName: currentCluster, services: installList})
            res = res.data

            let _res = []

            res.items.map((item, idx) => {
                item.configurations.map((value, index) => {
                    if((value.StackConfigurations.property_type[0] == 'USER' 
                        || value.StackConfigurations.property_type[0] == 'GROUP')
                        && value.StackConfigurations.property_display_name
                    ){
                        _res.push({
                            property_display_name: value.StackConfigurations.property_display_name,
                            property_value: value.StackConfigurations.property_value
                        })
                    }
                })
            })

            updateServiceAccount({
                status: false,
                serviceAccount: _res
            })
        },

        // 获取许可证信息
        async getLicenseData(data, getState) {
            const {updateLicenseData} = actions.security
            let {currentCluster} = getState().app

            currentCluster = currentCluster.clusterName

            // const res = await API.getLicenseDetail({clusterName: currentCluster})
            const res = {}
            const resKey = await API.getLicenseKey({clusterName: currentCluster})

            let resObj = {}

            Object.assign(resObj, {}, resKey.data.items[0].license)

            updateLicenseData({
                status: false,
                licenseData: resObj
            })
        },

    }
})
