import React from 'react'
import { Form, Input, Button, Icon, Checkbox } from 'antd'
import { actions, Redirect } from 'mirrorx'
import axios from 'axios'
import Cookies from 'js-cookie'

const FormItem = Form.Item
import './index.less'

class LoginPage extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			getCodeAble: true,
			time: 60
		}
	}
	componentDidMount() {
		// const { cookies } = this.props
		// const username = cookies.get('LEAP-CLOUD-USERNAME')
		// if (username) {
		// 	this.props.history.push('/')
		// }
	}
	handleSubmit = (e) => {
		e.preventDefault()
		this.props.form.validateFields((err, values) => {
			if (!err) {
				const { phone, smsCode } = values
				this.setState({
					loginLoading: true
				})
				axios.post(
					'/phone/doLogin', 
					{ phone, smsCode }, 
					{
						headers: {
							'Content-Type': 'application/json',
						}
					}
				).then(res => {
						this.setState({
							loginLoading: false
						})
						if (res.data.code == 2) {
							actions.user.login({ status: 'online' })
							const { history } = this.props
							Cookies.set('TRIPNOTE-KEY', 'Bearer ' + res.data.data.token, { path: '/', expires: 7 })
							history.push('/')
						}
					})
					.catch(err => {
						console.log('err', err)
						if(err){
							this.setState({
								loginLoading: false
							})
							this.props.form.setFields({
								smsCode: {
									value: smsCode,
									errors: [new Error(`登录失败, 请稍候再试(${err.response.status})`)]
								},
							})
						}
					})
			}
		})
	}

	getCode = () => {
		console.log(this.props.form.getFieldsValue())
		this.props.form.validateFields((err, values) => {
			if(!err.phone){
				this.setState({
					getCodeAble: false,
					time: 60
				})
				const { phone } = values

				axios.post('/sendLoginCode', { phoneNo: phone })
					.then(res => {
						console.log(res)
					})
					.catch(err => {
						
					})
			}
		})
	}

	checkPhone = (rule, value, callback) => {
		if(value && !(/^(([0\+]\d{2,3}-)?(0\d{2,3})-)?(\d{7,8})(-(\d{3,}))?$/.test(value)||/^(1(([35][0-9])|(47)|[8][01236789]))\d{8}$/.test(value))){
      callback('请输入正确的电话号码')
    }
    callback()
    return
  }

	render() {
		const { getFieldDecorator } = this.props.form
		const { loginLoading } = this.state
		return (
			<div className="loginpagewrap">
				<div className="loginContent">
					<div className="loginContentInner maxWidth1200">
						<div className="loginBox">
							<h2>登录</h2>
							<Form onSubmit={this.handleSubmit} className="loginForm">
								<FormItem>
									{getFieldDecorator('phone', {
										rules: [
											{ required: true, message: '请输入手机号'},
											{ validator: this.checkPhone }
										],
									})(
										<Input placeholder="请输入手机号" prefix={<Icon type="user" />} />
									)}
								</FormItem>
								<FormItem className="loginPwd">
									{getFieldDecorator('smsCode', {
										rules: [{ required: true, message: '请输入验证码' }],
									})(
										<Input className="inputCode" placeholder="请输入验证码" prefix={<Icon type="unlock" />} />
									)}
									<a href="javascript:;" className="getCode" onClick={this.getCode}>获取验证码</a>
									{/* <p className="loginErr" ref={(el) => this.errEl = el}><Icon type="minus-circle" /><span></span></p> */}
								</FormItem>
								<Button loading={loginLoading} type="primary" htmlType="submit" className="loginBtn">登录</Button>
							</Form>
						</div>
					</div>
				</div>
			</div>
		)
	}
}
let Login = Form.create()(LoginPage)

// const ConnLogin = connect(state=>{
//     return{
//         status:state.user.status
//     }
// })(Login)
export default Login
