import React, {Component} from 'react'
import {Tabs, Select, Button, Input, Icon, DatePicker} from 'antd'
import {FormattedMessage} from 'react-intl'
import {connect, actions, Link} from 'mirrorx'
import '../../components/layout/layout.less'

const Option = Select.Option

class TopDealHeader extends Component {
    constructor(props) {
        super(props)
        this.state = {
            keyword: '',
            showSearch: false
        }
        this.searchIpt = null
    }

    showSearchIpt = () => {
        this.setState({
            showSearch: true
        }, () => {
            this.searchIpt.input.focus()
        })

    }
    searhCluster = (value) => {
        this.setState({
            keyword: value
        })

    }
    searchBlur = () => {
        this.setState({
            showSearch: false
        })
    }

    render() {

        const {keyword, showSearch} = this.state
        return <div className="dealHeaderWrap">
            <div>
                <FormattedMessage id={'cluster.current'}/>：
                <Select
                    showSearch
                    style={{width: 200}}
                    placeholder="选择集群"
                    optionFilterProp="children"
                    onChange={this.handleChange}
                    filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                >
                    <Option value="cluster1">开发集群1</Option>
                    <Option value="cluster2">开发集群2</Option>
                    <Option value="cluster3">开发集群3</Option>
                </Select>
            </div>
            <div className="rightHead">
                <Icon className="icon" type="search" onClick={this.showSearchIpt}/>
                {showSearch && <Input
                    ref={input => this.searchIpt = input}
                    placeholder="请输入搜索内容"
                    onBlur={this.searchBlur}
                    onChange={this.searhCluster}
                />}

                <Icon className="icon" type="sync"/>
                <Icon className="icon" type="question-circle-o"/>
                <Link to="/createCluster">
                    <Button icon="plus">创建集群</Button>
                </Link>
            </div>
        </div>
    }

}

const ConnTopDealHeader = connect(state => {
    return {}
})(TopDealHeader)
export default ConnTopDealHeader