import React, {Component} from 'react'
import { connect, actions, Link } from "mirrorx";

import {Tabs, Select, Button, Input, Icon, DatePicker, Table, Tag} from 'antd'

import NullCluster from 'pages/nullCluster'

import ConsoleHeader from 'components/consoleHeader'

import './index.less'

const TabPane = Tabs.TabPane
const Option = Select.Option

class AlertsList extends Component {
    constructor(props) {
        super(props)
        this.state = {
            keyword: '',
            filterValues: {},
            headerFilterKeywords: ''
        }
    }

    componentWillMount(){
      if(this.props.currentCluster){
        actions.alerts.getAlertsList()
      }else{
        actions.app.getClusterList().then(rec => {
          if(rec && rec.length > 0){
            actions.alerts.getAlertsList()
          }
        })
      }
    }

    changeStatus = (value) => {
      return () => {
        actions.alerts.changeAlertStatus({
          id: value.definition_id,
          status: value.maintenance_state == 'OFF' ? false : true
        })
      }
    }

    formatStatus = (value) => {
      return (
        <div>
        {value.indexOf('OK') > -1 ? <Tag color="#1ACB40" >
              OK ({value.filter((item,index,arr) => {
                  return item == 'OK'
              }).length})
            </Tag> : ''}

            {value.indexOf('CRITICAL') > -1 ? <Tag color="#FF5858" >
            CRIT ({value.filter((item,index,arr) => {
                return item == 'CRITICAL'
            }).length})
            </Tag> : ''}

          </div>
      )
    }

    headerFilterKeywordsChange = (e) => {
      this.setState({
        headerFilterKeywords: e.target.value
      })
    }

    headerFilterKeywordsSubmit = (e) => {
      if(e.keyCode == 13 || e.witch == 13){
        alert(this.state.headerFilterKeywords)
      }
    }

    headerFilterStateChange = (value) => {
    }

    filterValueChange = (key, value) => {
      const val = typeof value == 'object' ? value.target.value : value
      const {filterValues} = this.state
      if(val !== 'all'){
        filterValues[key] = val
      }else{
        delete filterValues[key]
      }

      actions.alerts.filterAlertsList(filterValues)

      this.setState({
        filterValues
      })
    }

    formatTimeAgo = (time) => {
      if((new Date().getTime()  - time)/(1000*60*60*24) > 1){
        return parseInt((new Date().getTime()  - time)/(1000*60*60*24)) + ' days ago'
      }else if((new Date().getTime()  - time)/(1000*60*60) > 1){
        return parseInt((new Date().getTime()  - time)/(1000*60*60)) + ' hours ago'
      }else if((new Date().getTime()  - time)/(1000*60) > 1){
        return parseInt((new Date().getTime()  - time)/(1000*60)) + ' minutes ago'
      }else if((new Date().getTime()  - time)/(1000) > 1){
        return parseInt((new Date().getTime()  - time)/(1000)) + ' seconds ago'
      }
    }

    getColumns = (filterValues, serviceList) => (
      [{
        title: (
          <div className="tableHeaderFilter">
            <div className="item">告警定义名称</div>
            <div className="item">
              <Input placeholder="关键字搜索" 
                style={{width: '200px'}}
                value={filterValues['label']|| ''}
                onChange={this.filterValueChange.bind(this, 'label')}
              />
            </div>
          </div>
        ),
        dataIndex: "label",
        key: "label"
      },
      {
        title: (
          <div className="tableHeaderFilter">
            <div className="item">状态</div>
            <div className="item">
              <Select defaultValue="all" value={filterValues['states']|| 'all'} style={{ width: 120 }} onChange={this.filterValueChange.bind(this, 'states')}>
                <Option value="all">全部</Option>
                <Option value="OK">OK</Option>
                <Option value="WARNING">WARNING</Option>
                <Option value="CRITICAL">CRITICAL</Option>
                <Option value="UNKNOWN">UNKNOWN</Option>
                <Option value="NONE">NONE</Option>
              </Select>
            </div>
          </div>
        ),
        dataIndex: "states",
        key: "states",
        render: (value, row, index) => (
          <div>
            {this.formatStatus(value)}
          </div>
        )
      },
      {
        title: (
          <div className="tableHeaderFilter">
            <div className="item">服务</div>
            <div className="item">
              <Select defaultValue="all" value={filterValues['service_name']|| 'all'}  style={{ width: 120 }} onChange={this.filterValueChange.bind(this, 'service_name')}>
                <Option value="all">全部</Option>
                {
                  serviceList.map((item) => ((
                    <Option key={item} value={item}>{item}</Option>
                  )))
                }
              </Select>
            </div>
          </div>
        ),
        dataIndex: "service_name",
        key: "service_name"
      },
      {
        title: (
          <div className="tableHeaderFilter">
            <div className="item">状态改变时间</div>
            <div className="item">
              <Select defaultValue="all" value={filterValues['latest_timestamp']|| 'all'} style={{ width: 120 }} onChange={this.filterValueChange.bind(this, 'latest_timestamp')}>
                <Option value="all">全部</Option>
                <Option value="3600000">Past one hour</Option>
                <Option value="86400000">Past one day</Option>
                <Option value="172800000">Past 2 day</Option>
                <Option value="604800000">Past 7 day</Option>
                <Option value="1209600000">Past 14 day</Option>
                <Option value="2592000000">Past 30 day</Option>
              </Select>
            </div>
          </div>
        ),
        dataIndex: "latest_timestamp",
        key: "latest_timestamp",
        render: (value, row, index) => {
          return (
            <div>{this.formatTimeAgo(value)}</div>
          )
        }
      },
      {
        title: (
          <div className="tableHeaderFilter">
            <div className="item">开启状态</div>
            <div className="item">
              <Select defaultValue="all" value={filterValues['status']|| 'all'}  style={{ width: 120 }} onChange={this.filterValueChange.bind(this, 'status')}>
                <Option value="all">全部</Option>
                <Option value="yes">启用</Option>
                <Option value="no">禁用</Option>
              </Select>
            </div>
          </div>
        ),
        dataIndex: "status",
        key: "status",
        render: (value, row, index) => {
          if(!row.maintenance_state == "OFF"){
            return (
              <a href="javascript:;" style={{ color: "#1ACB40" }} onClick={this.changeStatus(row)}>
                <Icon type="poweroff" /> 启用
              </a>
            )
          }else{
            return (
              <a href="javascript:;" style={{ color: "#FF5858" }} onClick={this.changeStatus(row)}>
                <Icon type="poweroff" /> 停用
              </a>
            )
          }
        }
      }]
    )

    clusterChange = (val) => {
      actions.alerts.getAlertsList()
    }

    render() {
        const {filterValues} = this.state
        const { status, alertsList, clusterList } = this.props

        let servicesList = []

        alertsList.forEach((item) => {
          if(servicesList.indexOf(item.service_name) == -1){
            servicesList.push(item.service_name)
          }
        })

        if(!clusterList || clusterList.length == 0){
          return <NullCluster />
        }

        return <div className="alertsWrap">
            <ConsoleHeader selectChange={this.clusterChange}/>
            <div className="tabContent">
              <Table
                loading={status}
                bordered={true}
                dataSource={alertsList}
                columns={this.getColumns(filterValues, servicesList)}
                // pagination={false}
              />
            </div>
          </div>
    }
}
const ConnAlertsList = connect(state => {
  return {
    status: state.alerts.status,
    alertsList: state.alerts.alertsList,
    currentCluster: state.app.currentCluster,
    clusterList: state.app.clusterList
  }
})(AlertsList)

export default ConnAlertsList;