import mirror, {actions} from "mirrorx"
import {getCookie} from 'utils/cookie'
const loginStatus = getCookie('LEAP-CLOUD-USERNAME') || ''
mirror.model({
  name: 'user',
  initialState: {
    status: (loginStatus ? 'online' : 'offline') || "offline",
    // status: "offline",
    username: loginStatus || ""
  },
  reducers: {
    updateState(state, data) {
      return {
        ...state, ...data
      }
    }
  },
  effects: {
    async login(logindata, getState) {
      const {status} = getState().user
      const {updateState} = actions.user
      updateState(logindata)
    },
    async logout(data, getState){
      const {updateState} = actions.user
      updateState({status: 'offline', username: ''})
    }
  }
})