import React, {Component} from 'react'
import {Tabs, Select, Button, Input, Icon, DatePicker} from 'antd'

import ServiceAccount from './serviceAccount'
import Kerberos from './kerberos'
import License from './license'

import './index.less'

import { connect, actions } from 'mirrorx'
import NullCluster from 'pages/nullCluster'

import ConsoleHeader from 'components/consoleHeader'

const TabPane = Tabs.TabPane
const Option = Select.Option


class Security extends Component {
    constructor(props) {
        super(props)
        this.state = {
            activeKey: '1',
            keyword: '',
            searchInputWidth: 0
        }
    }

    componentWillMount(){
        if(!this.props.currentCluster){
            actions.app.getClusterList()
        }
    }

    toggleSearchInput = e => {
      const width = this.state.searchInputWidth || 0;
      this.setState({ searchInputWidth: 200 - width });
    }

    tabChange(val) {
        console.log(val)
        this.setState({
            activeKey: val
        })
    }

    render() {
        const {keyword, clusterList} = this.props

        if(!clusterList || clusterList.length == 0){
            return <NullCluster />
        }

        return <div className="securityWrap">
            <ConsoleHeader />

            <Tabs className="tabContent" activeKey={this.state.activeKey} onChange={this.tabChange.bind(this)} type="card">
              <TabPane tab="Service账号" key="1">
                <ServiceAccount />
              </TabPane>
              <TabPane tab="Kerberos" key="2">
                <Kerberos />
              </TabPane>
              <TabPane tab="许可证管理" key="3">
                  <License />
              </TabPane>
            </Tabs>
          </div>
    }
}

const ConnSecurity = connect(state => {
    return {
        clusterList: state.app.clusterList,
        currentCluster: state.app.currentCluster
    }
})(Security)

export default ConnSecurity 