import React, { Component } from 'react'
import { actions, Route, Link, Redirect, connect } from 'mirrorx'
import {Slider, Menu, Icon} from 'antd'
import ServiceCont from './serviceConf'
import './index.less'
export default class componentName extends Component {
  constructor(props){
    super(props)
    this.state = {}
  }
  componentWillMount(){
    // console.log(this.props)
    const curService = this.props.location.pathname.split('/configs/')[1]
    const data = MENULIST
    let selectedKey
    
    data.map(rec => {
      if(rec.displayName == curService){
        selectedKey = [String(rec.id)]
      }
    })

    this.setState({
      configMenu: data,
      selectedKeys  : curService ? selectedKey : [String(MENULIST[0].id)]
    })
  }
  onMenuSelect = (item) => {
    this.setState({
      selectedKeys: item.selectedKeys
    })
  }
  sliderChange = (a,b,c) => {
  }
  render() {
    const {configMenu, selectedKeys} = this.state
    const curPathname = this.props.location.pathname
    const host = curPathname.split('/configs')[0].split('/hosts/')[1]
    return (
      <div className="leapHostConfig clearfix">
        <div className="leapConfigLeftMenu fl">
          <Menu
            className="LeapConifgMenu"
            mode="inline"
            onSelect={this.onMenuSelect}
            selectedKeys={selectedKeys}
          >
          {
            configMenu && configMenu.map(rec => {
              return <Menu.Item key={rec.id}>
              <Link to={`/hosts/${host}/configs/${rec.displayName}`}>
                <span className="menuName">{rec.displayName}</span>
                <Icon type="right" className="fr"/>
              </Link>
            </Menu.Item>
            })            
          }
          </Menu>
        </div>
        <div className="leapConfigRightCont">
         <Route path={`/hosts/${host}/configs/:service`} component={ServiceCont}/>
         {
           configMenu && curPathname == `/hosts/host/configs` && 
           <Redirect exact 
                from={`/hosts/${host}/configs`} 
                to={`/hosts/${host}/configs/${configMenu[0].displayName}`} />
          }
        </div>
      </div>
    )
  }
}
// <LeapSlider onChange={this.sliderChange} min={0} max={100} marksType='double'/>
const LeapSlider = (props) => {
  let marks = {};
  const value = props.max - props.min
  let marksLen = props.marksLen || 2
  if(!props.singleMark){
    marksLen = 2 * marksLen
  }
  '0'.repeat(marksLen).split('').map((rec, idx) => {
    const val = props.min + idx * value / marksLen
    if(props.marksType == 'double' && idx % 2){
      marks[val] = ''
    }else{
      marks[val] = val
    }
  })
  marks[props.max] = props.max
  let className = ['leapStep']
  if(props.className) { 
    className = className.concat(props.className.split(' '))
    delete props.className
  }
  const defaultStep = value > 10 ? value / 100 : value / 10
  return <div>
    <Slider className={className.join(' ')} 
            step={defaultStep}
            marks={marks}
            tipFormatter={null}
            {...props} />
  </div>
}

const MENULIST = [
  {id: 1,  displayName: 'HDFS'},
  {id: 2,  displayName: 'YARN'},
  {id: 3,  displayName: 'MapReduce2'},
  {id: 4,  displayName: 'Hive'},
  {id: 5,  displayName: 'HBase'},
  {id: 6,  displayName: 'Sqoop'},
  {id: 7,  displayName: 'ZooKeeper'},
  {id: 8,  displayName: 'Flume'},
  {id: 9,  displayName: 'ELK'},
  {id: 10, displayName: 'SQL Editor'},
  {id: 11, displayName: 'Impala'},
]