import moment from 'moment'
import chartsName from './chartsName'

function echatOptions(data, isarea = false, type) {
  const newData = {}
  let allavg = {}
  if (Object.prototype.toString.call(data) == "[object Object]") {
    Object.keys(data).map(key => {
      const values = data[key]//.splice(0, 360)
      let newYVal = []
      let yvalue = []
      let max = values[0][0]
      let min = values[0][0]
      values.map((rec, idx) => {
        newYVal.push({
          name: +(rec[1] + '000'),
          value: [
            +(rec[1] + '000'),
            rec[0]
          ]
        })
        if(max < rec[0]) max = rec[0]
        if(min > rec[0]) min = rec[0]
        yvalue.push(rec[0])
      })
      let curAvg = 0
      yvalue.map(rec => {
        curAvg += rec
      })
      let avg = (curAvg / yvalue.length)
      allavg[key] = {
        avg: avg != 0 ? avg.toFixed(2) : avg,
        max,
        min
      }
      newData[key] = newYVal //yValue
    })
  }
  return {
    title: {
      show: false,
    },
    tooltip: {
      trigger: 'axis',
      show: false,
      formatter: function (params) {
        let showHtml = "";
        params.map(rec => {
          const name = chartsName[type][rec.seriesName]
          const mk = rec.marker
          let value = rec.value[1]
          if(type === 'memory'){
            // value = value != 0 ? (value / 1024 / 1024).toFixed(1) + ' GB' : value
            value = getUnit(value)
          }
          if(type === 'network'){
            // const  val = value / 1024 / 1024
            // value = val != 0 ? (val > 1 ? val.toFixed(1) + 'MB' : (value / 1024).toFixed(1) + 'KB') : value
            value = getUnit(value)
          }
          if(type === 'disk'){
            const  val = value / 1024
            value = val != 0 ? (val > 1 ? val.toFixed(1) + 'TB' : value.toFixed(1) + 'GB') : value
          }
          if(type === 'cpu'){
            value = value ? value.toFixed(4) + '%' : value
          }
          showHtml += mk + name + ': ' + value + ' <br/>'
        })
        return "<span style='text-align: center; margin-left: 15px;'>    " + moment(params[0].axisValue).format('YYYY/MM/DD HH:mm:ss') + "</span><br />" + showHtml;
      },
      axisPointer: {
        animation: false
      }
    },
    legend: {
      data: Object.keys(newData),
      formatter: function (name) {
        return `${chartsName[type][name]}           |       min:  ${allavg[name].min} ${type == 'cpu' ? '%' : ''}       avg:  ${allavg[name].avg} ${type == 'cpu' ? '%' : ''}       max:  ${allavg[name].max} ${type == 'cpu' ? '%' : ''}`
      },
      show: false,
      padding: [15, 60],
      backgroundColor: '#E6EAEC',
      textStyle: {
        fontWeight: 'bolder',
        color: '#666'
      }
    },
    grid: {
      top: 10,
      left: 20,
      right: 20,
      bottom: 0,
      containLabel: true
    },
    xAxis: {
      type: 'time',
      splitLine: {
          show: false
      },
      axisLabel: {
        show: false
      }
    },
    yAxis:{
      type: 'value',
      // splitNumber: 4,
      boundaryGap: [0, '100%'],
      zlevel: 10,
      splitLine: {
        show: true,
        interval: 25,
        lineStyle: {
          type: 'dotted'
        }
      },
      axisTick: {
        inside: true,
      },
      max: 'dataMax',
      // max: (value) => {
      //   return (value.max).toFixed(1);
      // },
      axisLabel: {
        inside: true,
        fontSize: 10,
        fontWeight: 'bold',
        formatter: (value, idx) => {
          if(type === 'memory'){
            // return value != 0 ? (value / 1024 / 1024).toFixed(1) + ' GB' : value
            return getUnit(value)
          }
          if(type === 'network'){
            // const val = value / 1024 / 1024
            // return val != 0 ? val >= 1 ? val.toFixed(1) + 'MB' : (value / 1024).toFixed(1) + 'KB' : value
            return getUnit(value)
          }
          if(type === 'disk'){
            const  val = value / 1024
            return val != 0 ? (val >= 1 ? val.toFixed(1) + 'TB' : value.toFixed(1) + 'GB') : value
          }
          return value
        }
      }
    },
    series: Object.keys(newData).reverse().map(rec => {
      const curr = {
        name: rec,
        type: 'line',
        symbol: 'none',
        data: newData[rec]
      }
      if(isarea){
        curr.areaStyle = { normal: {} }
      }
      return curr
    })
    // series: 
  }
}

function getUnit(val){
  let fVal = val / 1024
  if(fVal < 1){
    return `${val.toFixed(1)}B`
  }else if(fVal / 1024 < 1){
    return `${fVal.toFixed(1)}KB`
  }else if(fVal / 1024 / 1024 < 1){
    return `${(fVal / 1024).toFixed(1)}MB`
  }else if(fVal / 1024 / 1024 / 1024 < 1){
    return `${(fVal / 1024 / 1024).toFixed(1)}GB`
  }else if(fVal / 1024 / 1024 / 1024 / 1024 < 1){
    return `${(fVal / 1024 / 1024 / 1024).toFixed(1)}TB`
  }else{
    return `${(fVal / 1024 / 1024 / 1024 / 1024).toFixed(1)}PB`
  }
}

export {echatOptions}