import React, {
    Component
} from 'react'
import {
    Tabs,
    Row,
    Col,
    Popover,
    Select,
    Button,
    Icon,
    Spin,
    InputNumber,
    Input
} from 'antd'
import {
    connect,
    actions
} from 'mirrorx'

const Option = Select.Option;
const calPercent = (used, total) => {
    return (used / total * 100).toFixed(1) + '%'
}
const HostPopPanel = ({host, activeWidgetInfo}) => {
    return <div className="hostPopPanel">
        <h3>{host.publicHostName}</h3>
        <div><span>IP地址</span>:<span>{host.ip}</span></div>
        <div><span>Rack</span>:<span>{host.rack}</span></div>
        <div><span>{activeWidgetInfo.widget_name}</span>:<span>{activeWidgetInfo.widget_name}</span></div>
        <div><span>磁盘</span>:<span>{calPercent(host.diskTotal - host.diskFree, host.diskTotal)}</span></div>
        <div><span>CPU</span>:<span>{(host.cpuSystem + host.cpuUser) + '%'}</span></div>
        <div><span>内存</span>:<span>{calPercent(host.memTotal - host.memFree, host.memTotal)}</span></div>
        <div className="flexWrap"><span>组件</span>:
            {host.hostComponents.map((item, idx) => (<span key={idx}>{item}</span>))}
        </div>
    </div>
}

class HeatMapsPage extends Component {
    constructor(props) {
        super(props)
    }

    componentDidMount() {
        actions.heatMap.getActiveWidget(this.props.servicesName)
        actions.heatMap.getRackList()
        actions.heatMap.getUserDropDownWidgetLayouts(this.props.servicesName)
    }

    handleBlur = () => {

    }
    onFocus = () => {

    }
    changeDisplayWidget = (id) => {
        actions.heatMap.changeDisplayWidgetName(id)

    }
    changeMax = (id) => {
        actions.heatMap.updateIptMax(id)
    }
    getBackGroundColor = (data) => {
        if (this.props.loadRack && this.props.loadMetrics) {
            return actions.heatMap.hostTemperatureStyle(data)
        }
    }

    render() {
        const {
            rackList,
            loadRack,
            inputMaximum,
            activeWidgetInfo,
            slotDefinitions,
            dropDownLayoutInfo
        } = this.props
        return <div className="heatMapPage">
            {
                this.props.isLoaded ? <Row>
                    <Col span={8} xs={24} md={12} lg={8}>
                        <Select
                            showSearch
                            style={{width: 258}}
                            placeholder="选择指标"
                            optionFilterProp="children"
                            notFoundContent={dropDownLayoutInfo.loaded ? <Spin size="small"/> : null}
                            onChange={this.changeDisplayWidget}
                            value={activeWidgetInfo.widget_name}
                            onFocus={this.handleFocus}
                            onBlur={this.handleBlur}
                            filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                        >
                            {dropDownLayoutInfo.list.map(layout => <Option
                                key={layout.WidgetInfo.id}>{layout.WidgetInfo.widget_name}</Option>)}
                        </Select>
                        <div className="colorContent">
                            <table className="legend">
                                <tbody>
                                {
                                    slotDefinitions.map((slot, idx) => {
                                        return <tr key={idx}>
                                            <td>
                                                <div className="tile" style={{background: slot.cssStyle}}/>
                                            </td>
                                            <td>{slot.label}</td>
                                        </tr>
                                    })
                                }
                                </tbody>
                            </table>
                        </div>
                        <div className="maxContent">
                            <span>最大值:</span>
                            <InputNumber onChange={this.changeMax} value={inputMaximum}/>
                            <span>{activeWidgetInfo.displayUnit}</span>
                        </div>
                    </Col>
                    <Col span={16} xs={24} md={12} lg={16}>
                        <div className=" heatMapContent">
                            <h3 className="widgetName">{activeWidgetInfo.widget_name}</h3>
                            {
                                loadRack ? rackList.map((rack, idx) => {
                                        return <div className="rack" key={idx}>
                                            <div className="rackHeader ">
                                                <div className="statusName">
                                                    <h3 className="panel-title rackName">{rack.name}</h3>
                                                </div>
                                            </div>
                                            <div className="rackContent">
                                                {
                                                    rack.hosts.map((host, index) => {
                                                        return <Popover placement="bottom"
                                                                        content={<HostPopPanel host={host}
                                                                                               activeWidgetInfo={activeWidgetInfo}/>}
                                                                        trigger="hover" key={index}>
                                                            <Button className="hostBlock"
                                                                    style={{background: this.getBackGroundColor(host)}}/>
                                                        </Popover>
                                                    })
                                                }
                                            </div>
                                        </div>
                                    }
                                ) : <div className="flexVerticalCenter"><Spin/></div>
                            }
                        </div>

                    </Col>
                </Row> : <div className="flexVerticalCenter"><Spin/></div>
            }

        </div>
    }
}

const ConnHeatMapsPage = connect(state => {
    return {
        isLoaded: state.heatMap.isLoaded,
        loadRack: state.heatMap.loadRack,
        loadMetrics: state.heatMap.loadMetrics,
        rackList: state.heatMap.rackList,
        activeWidgetInfo: state.heatMap.activeWidgetInfo,
        inputMaximum: state.heatMap.inputMaximum,
        slotDefinitions: state.heatMap.slotDefinitions,
        dropDownLayoutInfo: state.heatMap.dropDownLayoutInfo,
    }
})(HeatMapsPage)

export default ConnHeatMapsPage