import React, { Component } from 'react'
import { Card, Input, Icon, Modal } from 'antd'
import {actions} from 'mirrorx'

export default class HostSuammary extends Component {
  constructor(props){
    super(props)
    this.state = {}
  }
  // 改变主机Rack
  rackChange = (e) => {
    let rackVal = e.target.value
    if(rackVal.indexOf('/') !== 0){
      rackVal = '/' + rackVal
    }
    this.setState({ rackVal })
  } 
  // 展示Rack修改框
  showEditRackModal = (rack, e) => {
    this.setState({
      isEditRack: true, 
      oldRack: rack, 
    })
  }
  operateOk = async() => {
    const {rackVal, oldRack} = this.state
      if(!rackVal){
        this.setState({ isEditRack: false })
        return
      }
      const postData = {
        context: '设置Rack',
        rack: rackVal,
        cluster_name: oldRack.cluster_name,
        hosts: oldRack.host_name,
      }
      this.setState({
        modalLoading: true
      })
      actions.host.setHostRack(postData)
      .then(async rec => {
        if(rec.code == 1){
          await actions.host.getHostSummary({host_name: oldRack.host_name})
          this.setState({
            oldRack: null,
            rackVal: null,
            isEditRack: false,
            modalLoading: false,
            modalHasErr: false
          })
        }
      })
      .catch(err => {
        this.setState({modalLoading: false, modalHasErr: true})
      })
  }
  cancelOperate = () => {
    this.setState({
      isEditRack: false
    })
  }
  render() {
    const {host_info} = this.props
    const { isEditRack, oldRack, rackVal, modalHasErr, modalLoading } = this.state
    return (
      <Card className="hostDetailCard hostDetail" title="概览">
          <ul>
            <li>
              <span>主机名:</span>
              <span>{host_info.host_name}</span>
            </li>
            <li>
              <span>IP地址:</span>
              <span>{host_info.ip}</span>
            </li>
            <li>
              <span>Rack:</span>
              <span>{host_info.rack_info}<Icon type="edit" onClick={this.showEditRackModal.bind(this, host_info)} style={{ cursor: 'pointer' }} /> </span>
            </li>
            <li>
              <span>操作系统:</span>
              <span>{host_info.os_type}({host_info.os_arch})</span>
            </li>
            <li>
              <span>内核(CPU):</span>
              <span>{host_info.ph_cpu_count}({host_info.cpu_count})</span>
            </li>
            <li>
              <span>磁盘:</span>
              <span>669.93GB/1775.48GB(37.73%使用)</span>
            </li>
            <li>
              <span>内存:</span>
              <span>{(host_info.total_mem / 1024 / 1024).toFixed(2)}GB</span>
            </li>
            <li>
              <span>平均负载:</span>
              <span>3.31</span>
            </li>
            <li>
              <span>心跳:</span>
              <span>{host_info.last_heartbeat_time}</span>
            </li>
            <li>
              <span>当前版本:</span>
              <span>3.3.4</span>
            </li>
          </ul>
        <Modal
          title='设置Rack'
          wrapClassName='setRackModal'
          visible={isEditRack}
          onOk={this.operateOk}
          onCancel={this.cancelOperate}
          confirmLoading={modalLoading}
          maskClosable={false}
          okText="确认"
          cancelText="取消"
        >
          <div>
            <label>Rack:</label> 
            <Input ref={(el) => this.rackIpt = el} value={rackVal || (oldRack && oldRack.rack_info)} onChange={this.rackChange}/>
            <p style={{color: modalHasErr ? 'red' : '#999'}}>{modalHasErr ? 'Rack更改失败' : '应以斜杠开始，可以包含字母、数字、点、连字符和斜杠'}</p>
          </div>
        </Modal>
      </Card>
    )
  }
}
HostSuammary.defaultProps = {
  host_info: {}
}